<template>
  <transition name="slide">
    <div class="hs-container-child" v-if="show">
      <c-header leftIcon1="return.png" :leftFn1="close" :title="$t('base.SelectContact')">
        <div slot="right" class="header-right" @click="_save">
          <p>{{$t('base.Confirm')}}</p>
        </div>
      </c-header>
      <hs-scroll
        class="table-view"
        :data="currentDefaultContacts"
        :options="tableViewOptions"
        @pulling-down="_getDatalist">
        <ul>
          <hs-section>
            <hs-cell-1 :showLogo="false">
              <div class="custom-cell-buttons">
                <hs-button width="100%" @click="_addContact">{{$t('base.New')}}</hs-button>
              </div>
            </hs-cell-1>
          </hs-section>
          <hs-section>
            <hs-cell-1 v-for="(item, index) in currentDefaultContacts" :key="index" :showLogo="false" @click.native="_toggleContacts(item)">
              <div class="custom-cell-contact">
                <div class="checkbox" style="background-image: url('static/image/base/check-box/check-box.png')" v-if="currentDatalist.map(v => v.ID).indexOf(item.ID) >= 0"></div>
                <div class="checkbox" style="background-image: url('static/image/base/check-box/check-box-default.png')" v-else></div>
                <div class="right">
                  <div class="name">
                    <p>{{item.Name}}</p>
                    <p class="mine" v-if="item.IsCurrentLoginUser">{{$t('base.Self')}}</p>
                    <p class="default" v-if="item.IsDefault">{{$t('base.Default')}}</p>
                    <p class="frequent" v-if="item.ContactUpID || item.UpID">{{$t('base.FrequentTraveler')}}</p>
                  </div>
                  <p>{{$t('base.CellphoneNumber')}}&nbsp;{{item.Mobile ? item.Mobile : $t('base.Unfilled')}}</p>
                  <p>{{$t('base.Email')}}&nbsp;{{item.Email ? item.Email : $t('base.Unfilled')}}</p>
                </div>
                <div class="edit" style="background-image: url('static/image/plane-ticket/book/edit.png')" @click.stop="_gotoDetails(item, index)"></div>
              </div>
            </hs-cell-1>
          </hs-section>
        </ul>
      </hs-scroll>
      <c-contacts :show.sync="showCContacts" v-model="contactDetails" :datalist="currentDatalist" :OrderBusinessType="OrderBusinessType" @delegate="_delegateWithContacts"></c-contacts>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
import CHeader from 'components/c-header/c-header'
import HsButton from 'base/hs-button/hs-button'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import CContacts from 'components/c-passenger/c-contacts'
import {kpiUserGetDefaultContacts} from 'api'
export default {
  props: {
    orderInit: {
      type: Object,
      default: function() {
        return {}
      }
    },
    datalist: {
      type: Array,
      default: function() {
        return []
      }
    },
    // 订单业务类型(国内机票:1 酒店:2 旅游:3 保险:5 国际机票:6 火车票:10)
    OrderBusinessType: {
      type: Number,
      default: 1
    }
  },
  data() {
    return {
      tableViewOptions: {
        pullDownRefresh: {
          threshold: 50
        }
      },
      show: false,
      showCContacts: false,
      contactDetails: {},
      currentDefaultContacts: [],
      currentDatalist: []
    }
  },
  methods: {
    open() {
      this.show = true
      this.currentDatalist = JSON.parse(JSON.stringify(this.datalist))
      this._getDatalist()
    },
    close() {
      this.show = false
    },
    _getDatalist() {
      this.currentDefaultContacts = []
      kpiUserGetDefaultContacts().then((res) => {
        console.log('获取常用联系人列表: ', res)
        let list = res.ResultData ? res.ResultData : []
        // 将本人挪至第一位
        let mine = list.filter(v => v.IsCurrentLoginUser === true)
        if (mine && mine[0]) {
          let index = list.map(v => v.ID).indexOf(mine[0].ID)
          list.splice(index, 1)
          list.unshift(mine[0])
        }
        // ========================
        // 拼接并去重
        this.currentDefaultContacts = this.currentDatalist.concat(list)
        let maplist = new Map()
        this.currentDefaultContacts = this.currentDefaultContacts.filter(a => !maplist.has(a.ID) && maplist.set(a.ID, 1))
        // ============================
        // 初始化
        this.currentDefaultContacts.map(item => {
          item.IsSendBookSms = this.noValue(item.IsSendBookSms) ? this.orderInit.IsSendBookSmsToPassenger : item.IsSendBookSms
          item.IsSendBookEmail = this.noValue(item.IsSendBookEmail) ? this.orderInit.IsSendBookEmailToPassenger : item.IsSendBookEmail
          item.IsSendIssuedSms = this.noValue(item.IsSendIssuedSms) ? this.orderInit.IsSendIssuedSmsToPassenger : item.IsSendIssuedSms
          item.IsSendIssuedEmail = this.noValue(item.IsSendIssuedEmail) ? this.orderInit.IsSendIssuedEmailToPassenger : item.IsSendIssuedEmail
          item.IsSendBookWechat = this.noValue(item.IsSendBookWechat) ? this.orderInit.IsSendBookWechatToPassenger : item.IsSendBookWechat
          item.IsSendIssuedWechat = this.noValue(item.IsSendIssuedWechat) ? this.orderInit.IsSendIssuedWechatToPassenger : item.IsSendIssuedWechat
          item.IsSendConfirmSms = this.noValue(item.IsSendConfirmSms) ? this.orderInit.IsSendConfirmSmsToGuest : item.IsSendConfirmSms
          item.IsSendConfirmEmail = this.noValue(item.IsSendConfirmEmail) ? this.orderInit.IsSendConfirmEmailToGuest : item.IsSendConfirmEmail
          item.IsSendRefundSms = this.noValue(item.IsSendRefundSms) ? true : item.IsSendRefundSms
          item.IsSendRefundEmail = this.noValue(item.IsSendRefundEmail) ? true : item.IsSendRefundEmail
          return item
        })
        // ============================
        console.log('处理后的联系人列表: ', this.currentDefaultContacts)
      })
    },
    _toggleContacts(item) {
      let index = this.currentDatalist.map(v => v.ID).indexOf(item.ID)
      if (index >= 0) {
        this.currentDatalist.splice(index, 1)
      } else {
        if (this.currentDatalist.map(v => v.Mobile).indexOf(item.Mobile) >= 0) {
          this.$toast('已存在相同手机号的联系人')
          return
        }
        this.currentDatalist.push(item)
      }
    },
    _save() {
      let maplist = new Map()
      if (this.currentDatalist.filter(a => !maplist.has(a.Mobile) && maplist.set(a.Mobile, 1)).length < this.currentDatalist.length) {
        this.$toast(`不得同时选择手机号相同的联系人`)
        return
      }
      this.$emit('delegate', JSON.parse(JSON.stringify(this.currentDatalist)))
      this.close()
    },
    // 添加联系人
    _addContact() {
      this.contactDetails = {
        index: -1,
        Name: '',
        Mobile: '',
        Email: '',
        IsSendBookSms: this.orderInit.IsSendBookSmsToContact,
        IsSendIssuedSms: this.orderInit.IsSendIssuedSmsToContact,
        IsSendBookEmail: this.orderInit.IsSendBookEmailToContact,
        IsSendIssuedEmail: this.orderInit.IsSendIssuedEmailToContact,
        IsSendBookWechat: this.orderInit.IsSendBookWechatToPassenger,
        IsSendIssuedWechat: this.orderInit.IsSendIssuedWechatToPassenger,
        IsSendConfirmSms: this.orderInit.IsSendConfirmSmsToContact,
        IsSendConfirmEmail: this.orderInit.IsSendConfirmEmailToContact,
        IsSendRefundSms: true,
        IsSendRefundEmail: true
      }
      this.showCContacts = true
    },
    // 联系人详情
    _gotoDetails(item, index) {
      this.contactDetails = Object.assign(JSON.parse(JSON.stringify(item)), {
        index: index,
        IsSendBookSms: this.noValue(item.IsSendBookSms) ? this.orderInit.IsSendBookSmsToPassenger : item.IsSendBookSms,
        IsSendBookEmail: this.noValue(item.IsSendBookEmail) ? this.orderInit.IsSendBookEmailToPassenger : item.IsSendBookEmail,
        IsSendIssuedSms: this.noValue(item.IsSendIssuedSms) ? this.orderInit.IsSendIssuedSmsToPassenger : item.IsSendIssuedSms,
        IsSendIssuedEmail: this.noValue(item.IsSendIssuedEmail) ? this.orderInit.IsSendIssuedEmailToPassenger : item.IsSendIssuedEmail,
        IsSendBookWechat: this.noValue(item.IsSendBookWechat) ? this.orderInit.IsSendBookWechatToPassenger : item.IsSendBookWechat,
        IsSendIssuedWechat: this.noValue(item.IsSendIssuedWechat) ? this.orderInit.IsSendIssuedWechatToPassenger : item.IsSendIssuedWechat,
        IsSendConfirmSms: this.noValue(item.IsSendConfirmSms) ? this.orderInit.IsSendConfirmSmsToGuest : item.IsSendConfirmSms,
        IsSendConfirmEmail: this.noValue(item.IsSendConfirmEmail) ? this.orderInit.IsSendConfirmEmailToGuest : item.IsSendConfirmEmail,
        IsSendRefundSms: this.noValue(item.IsSendRefundSms) ? true : item.IsSendRefundSms,
        IsSendRefundEmail: this.noValue(item.IsSendRefundEmail) ? true : item.IsSendRefundEmail
      })
      // this.titleForCPassengerDetails = `${this.maxPassengerWarning}详情`
      this.showCContacts = true
    },
    _delegateWithContacts(val) {
      if (val && val.index >= 0) {
        this.currentDefaultContacts.splice(val.index, 1, Object.assign(val, {openDelete: false}))
        let indexWithDatalist = this.currentDatalist.map(v => v.ID).indexOf(val.ID)
        if (indexWithDatalist >= 0) {
          console.log('成功了吗', indexWithDatalist)
          this.currentDatalist.splice(indexWithDatalist, 1, Object.assign(val, {openDelete: false}))
        }
      } else {
        this.currentDatalist.unshift(Object.assign(val, {openDelete: false}))
        this.currentDefaultContacts.unshift(Object.assign(val, {openDelete: false}))
      }
      this.$emit('delegate', JSON.parse(JSON.stringify(this.currentDatalist)))
    }
  },
  components: {
    CHeader,
    HsButton,
    HsScroll,
    HsSection,
    HsCell1,
    CContacts
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container-child
  position absolute
  width 100%
  height 100%
  left 0
  top 0
  bottom 0
  background-color $color_gray_background_0
  z-index 200
  .header-right
    @extend .flex-row
    @extend .flex-end
    width 60px
    height 100%
    >p
      font-size 0.9rem
  .table-view
    width 100%
    height calc(100% - 45px)
    overflow hidden
    .custom-cell-buttons
      @extend .flex-row
      @extend .flex-center
      width 100%
      padding 12px 0
      >.hs-button
        margin 0 5px
        &:first-child
          margin-left 0px
        &:last-child
          margin-right 0px
    .custom-cell-contact
      @extend .flex-row
      @extend .flex-start
      width 100%
      >.checkbox
        width 20px
        height 20px
        background-size 100% 100%
        margin-right 10px
      >.right
        width calc(100% - 64px)
        padding 15px 0
        margin-right 10px
        >.name
          @extend .flex-row
          @extend .flex-start
          width 100% !important
          font-size 0.9rem
          color $color_black_text_0
          margin-bottom 5px
          flex-wrap wrap
          >p
            font-weight bold
            margin-right 5px
            line-height 1.2em
          >.frequent
          >.mine
          >.default
            font-size 0.7rem
            margin-right 5px
            border 1px solid $color_theme
            background-color $color_red_background_0
            color $color_theme
            border-radius 3px
            padding 2px 5px
            font-weight normal
          >.frequent
            border 1px solid $color_green_layer !important
            color $color_green_layer !important
            background-color $color_green_background_0 !important
          >.mine
            border 1px solid $color_orange_layer !important
            color $color_orange_layer !important
            background-color $color_orange_background_0 !important
        >p
          font-size 0.8rem
          line-height 1.1em
          color $color_gray_text
          margin-bottom 5px
          &:nth-child(1)
            font-size 0.9rem
            color $color_black_text_0
            font-weight bold
          &:last-child
            margin-bottom 0
      >.edit
        width 24px
        height 24px
        background-size 100% 100%
</style>



