<template>
  <transition name="opacity">
    <div class="c-return-explain" v-if="currentValue" @click.stop="currentValue = false">
      <hs-scroll class="table-view" :data="[details]">
        <ul>
          <div class="highlight">
            <div>
              <p>{{$t('base.ReturnAndChangeInstructions')}}</p>
              <div class="logo" style="background-image: url('static/image/plane-ticket/book/warning_fill.png')"></div>
            </div>
            <p>{{$t('base.TheSpecificRefundAndChangeRulesShallBeSubjectToTheDateOfTheTicketIssue')}}</p>
          </div>
          <template v-if="noValue(details.vhtml)">
            <template v-if="details.IsNormal">
              <table class="c-return-explain-table">
                <tbody>
                  <template v-if="details.CabinRule">
                    <tr>
                      <td>{{$t('base.RefundPolicy')}}</td>
                      <td>{{details.CabinRule.RefundRule}}</td>
                    </tr>
                    <tr>
                      <td>{{$t('base.ChangePolicy')}}</td>
                      <td>{{details.CabinRule.ChangeRule}}</td>
                    </tr>
                    <tr>
                      <td>{{$t('base.SigningPolicy')}}</td>
                      <td>{{details.CabinRule.ReissueRule}}</td>
                    </tr>
                    <tr>
                      <td>{{$t('base.BaggagePolicy')}}</td>
                      <td>{{details.CabinRule.Luggage}}</td>
                    </tr>
                  </template>
                  <tr>
                    <td>{{$t('base.OtherPolicy')}}</td>
                    <td>{{$t('base.ChargedAccordingToTheCorrespondingAirlineTicketRegulations')}}</td>
                  </tr>
                </tbody>
              </table>
            </template>
            <p class="UnNormalCabinRule" v-else>
              {{details.UnNormalCabinRule}}
            </p>
          </template>
          <div v-else v-html="details.vhtml" class="rich-text"></div>
        </ul>
      </hs-scroll>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
import HsScroll from 'base/hs-scroll/hs-scroll'
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    details: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  components: {
    HsScroll
  },
  computed: {
    currentValue: {
      get: function() {
        return this.value
      },
      set: function(val) {
        return this.$emit('input', val)
      }
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.c-return-explain
  position absolute
  top 0
  left 0
  width 100%
  bottom 0
  background-color $color-black_1
  color $color_white
  z-index 99
  padding 20px
  >.table-view
    width 100%
    height 100%
    overflow hidden
    .c-return-explain-table
      width 100%
      margin 15px 0
      tr
        border-top 1px solid $color_white
        >td
          &:nth-child(1)
            padding 20px 20px 20px 5px
            width 100px
            border-right 1px solid $color_white
            font-size 0.9rem
            vertical-align middle
          &:nth-child(2)
            padding 20px 5px 20px 20px
            font-size 0.8rem
            line-height 1.5em
            text-align justify
        &:last-child
          border-bottom 1px solid $color_white
    .highlight
      @extend .flex-column
      @extend .flex-center
      @extend .flex-top
      color $color_white
      margin-top 30px
      font-size 0.8rem
      line-height 1.2em
      >div
        @extend .flex-row
        @extend .flex-start
        margin-bottom 5px
        >.logo
          width 20px
          height 20px
          background-size 100% 100%
          margin-left 4px
    .UnNormalCabinRule
    .rich-text
      color $color_white
      font-size 0.9rem
      line-height 1.4em
      margin 15px 0
      width 100%
</style>



