<template>
  <hs-section v-if="detail"
    :title="title">
    <hs-cell-1 :showLogo="false">
      <div class="cell-segment">
        <!-- <div class="header">
          <div class="mark" :type="language" v-if="mark">{{mark}}</div>
          <p>{{$t(beTime(detail.DepartDate), 'date', ['MM月dd日', 'MM dd'])}}&nbsp;{{getWeeks(beTime(detail.DepartDate))}}</p>
        </div> -->
        <div class="details">
          <div class="cell-segment-left">
            <div class="left-time">
              <p>{{$t(beTime(detail.DepartDate), 'date', ['MM月dd日', 'MM dd'])}}&nbsp;{{getWeeks(beTime(detail.DepartDate))}}</p>
              <p>{{detail.DepartureTime}}</p>
              <p>{{detail.DepartStationName}}</p>
            </div>
          </div>
          <div class="cell-segment-middle">
            <p>{{detail.TrainCode}}</p>
            <div class="cell-segment-time" @click="_ClickTimeTab()">
              <div class="layer"></div>
            </div>
            <p>{{detail.RunTime}}</p>
          </div>
          <div class="cell-segment-right">
            <div class="left-time">
              <p>{{$t(beTime(detail.ArrivalDate), 'date', ['MM月dd日', 'MM dd'])}}&nbsp;{{getWeeks(beTime(detail.ArrivalDate))}}</p>
              <p>{{detail.ArrivalTime}}<span class="ArrivalDays" v-if="detail.Days > 0">+{{detail.Days}}</span></p>
              <p>{{detail.ArrivalStationName}}</p>
            </div>
          </div>
        </div>
      </div>
    </hs-cell-1>
    <slot>
      <hs-cell-1 :showLogo="false">
        <div class="cell-segment-one">
          <p>{{detail.SeatTypeName}}</p>
          <a>{{$t('base.BookingInstructionsAndInstructionsForReturning')}}</a>
        </div>
      </hs-cell-1>
    </slot>
  </hs-section>
</template>
<script type='text/ecmascript-6'>
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsScroll from 'base/hs-scroll/hs-scroll'
export default {
  props: {
    detail: {
      type: Object,
      default: () => {}
    },
    mark: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    }
  },
  methods: {
    _ClickTimeTab(index) {
      this.$emit('delegate', this.detail)
    }
  },
  components: {
    HsCell1,
    HsSection,
    HsScroll
  }
}
</script>
<style lang='stylus' scoped='' rel='stylesheet/stylus'>
@import '~common/stylus/base'
@import '~common/stylus/variable'
.cell-segment
  width 100%
  padding 15px 0
  >.header
    @extend .flex-row
    @extend .flex-start
    width 100%
    margin-bottom 10px
    >.mark
      @extend .flex-center
      font-size 0.7rem
      border-radius 2px
      border 1px solid $color_theme
      color $color_theme
      padding 3px 0
      width 40px
      margin-right 10px
    >.mark[type="EN"]
      @extend .no-wrap
      width auto
      padding 3px
    >p
      width calc(100% - 50px)
  >.details
    @extend .flex-row
    @extend .flex-between
    width 100%
    .cell-segment-left
      @extend .flex-column
      @extend .flex-center
      line-height 1.2em
      width 33.33%
      .left-station
        @extend .flex-row
        .station-one
          @extend .flex-center
          background-color  #ee7e7e
          color $color_white
          font-size 0.8rem
          border-radius 7px
          width 20px
          height 20px
        .station-two
          font-size 0.8rem
          color $color_black_text_1
      .left-time
        @extend .flex-column
        @extend .flex-center
        >p
          &:nth-child(2)
            font-size 1.2rem
            font-weight bold
            margin 3px 0
          &:nth-child(1)
          &:nth-child(3)
            font-size 0.8rem
    .cell-segment-middle
      @extend .flex-column
      @extend .flex-center
      width 33.33%
      line-height 1.2em
      >p
        &:nth-child(1)
          font-size 0.8rem
          font-weight bold
        &:nth-child(3)
          font-size 0.6rem
          color $color_gray_text
      >.cell-segment-time
        @extend .flex-center
        @extend .flex-row
        >.layer
          @extend .flex-center
          height 1px
          width 100px
          background-color $color_gray_text
        >.time
          @extend .flex-center
          font-size 0.6rem
          color $color_gray_text
          padding 0 8px
          height 20px
          border-radius 10px
          border 1px solid $color_gray_text
          background-color $color_white
    .cell-segment-right
      @extend .flex-column
      @extend .flex-center
      line-height 1.2em
      width 33.33%
      .left-station
        @extend .flex-row
        >p
          // &:nth-child(1)
          //   @extend .flex-center
          //   background-color  #72d3df
          //   color $color_white
          //   font-size 0.8rem
          //   border-radius 7px
          //   width 20px
          //   height 20px
          &:nth-child(1)
            font-size 0.8rem
            color $color_black_text_1
      .left-time
        @extend .flex-column
        @extend .flex-center
        >p
          &:nth-child(2)
            font-size 1.2rem
            font-weight bold
            position relative
            margin 3px 0
          &:nth-child(1)
          &:nth-child(3)
            font-size 0.8rem
        .ArrivalDays
          color $color_theme
          font-size 0.8rem
          position absolute
          top -4px
          right -16px
  .cell-segment-one
    @extend .flex-row
    @extend .flex-between
    width 100%
    >p
      &:nth-child(1)
        font-size 0.8rem
        color $black_6
    >a
      &:nth-child(2)
        font-size 0.8rem
        color $color_black_text_1
</style>



