<template>
  <transition name="slide">
    <div class="hs-container-child" v-if="currentValue">
      <c-header :title="$t('base.ApprovalInformation')" leftIcon1="return.png" :leftFn1="() => currentValue = false"></c-header>
      <hs-scroll class="table-view">
        <ul>
          <hs-section v-for="(item, index) in  details.VettingProcessList" :key="index">
            <hs-cell-1 :showLogo="false">
              <div class="custom-cell">
                <div class="header">
                  <p>{{passengerWarning}}:</p>
                  <p v-for="(itemChild, indexChild) in  item.Passengers" :key="indexChild">{{itemChild.Name}}</p>
                </div>
                <div class="details">
                  <div class="item" v-for="(itemChild, indexChild) in  item.ProcessNodes" :key="indexChild">
                    <p>{{$t('base.LevelXAapproval', 'function', itemChild.VettingLevel)}}</p>
                    <p>{{itemChild.ProcessPersonName}}</p>
                  </div>
                </div>
              </div>
            </hs-cell-1>
          </hs-section>
        </ul>
      </hs-scroll>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
// 机票订单详情审批信息页面
import Vue from 'vue'
import CHeader from 'components/c-header/c-header'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
export default {
  components: {
    CHeader,
    HsScroll,
    HsSection,
    HsCell1
  },
  props: {
    value: {
      type: Boolean,
      default: false
    },
    details: {
      type: Object,
      default: function() {
        return {}
      }
    },
    passengerWarning: {
      type: String,
      default: Vue.prototype.$t('base.FrequentTraveler')
    }
  },
  computed: {
    currentValue: {
      get: function() {
        return this.value
      },
      set: function(val) {
        return this.$emit('input', val)
      }
    }
  },
  data() {
    return {
      // demoIndex: 0
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container-child
  position absolute
  width 100%
  height 100%
  left 0
  top 0
  bottom 0
  background-color $color_white
  z-index 200
  >.table-view
    width 100%
    height calc(100% - 45px)
    overflow hidden
    .custom-cell
      width 100%
      padding 15px 0
      >.header
        @extend .flex-row
        @extend .flex-start
        @extend .flex-top
        flex-wrap wrap
        margin-bottom 8px
        >p
          font-size 1rem
          line-height 1.5em
          margin-right 15px
          font-weight bold
      >.details
        width 100%
        >.item
          width 100%
          padding 10px 0
          border-bottom 1px dashed $color_gray_layer
          >p
            @extend .flex-row
            @extend .flex-start
            width 100%
            line-height 1.5em
</style>



