<template>
  <div class="c-tabbar">
    <div class="item" @click.stop="$router.push('/home')">
      <div class="logo" :style="{'background-image': `url('static/image/tabbar/home${$route.path === '/home' ? '_red' : ''}@2x.png')`}"></div>
      <p :class="{light: $route.path === '/home'}">{{$t('base.Home')}}</p>
    </div>
    <div class="item" @click.stop="$router.push('/management')">
      <div class="logo" :style="{'background-image': `url('static/image/tabbar/manage${$route.path === '/management' ? '_red' : ''}@2x.png')`}"></div>
      <p :class="{light: $route.path === '/management'}">{{$t('base.Manage')}}</p>
    </div>
    <div class="item" @click.stop="$router.push('/ourService')">
      <div class="logo" :style="{'background-image': `url('static/image/tabbar/customerService${$route.path === '/ourService' ? '_red' : ''}@2x.png')`}"></div>
      <p :class="{light: $route.path === '/ourService'}">{{$t('base.Service')}}</p>
    </div>
    <div class="item" @click.stop="$router.push('/personal/center')">
      <div class="logo" :style="{'background-image': `url('static/image/tabbar/user${$route.path === '/personal/center' ? '_red' : ''}@2x.png')`}"></div>
      <p :class="{light: $route.path === '/personal/center'}">{{$t('base.Account')}}</p>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  created() {
  },
  methods: {
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.c-tabbar
  @extend .flex-row
  @extend .flex-start
  width 100%
  height 50px
  background-color $color_white
  border-top 1px solid $color_gray_layer
  >.item
    @extend .flex-column
    @extend .flex-center
    flex 1
    >.logo
      width 23px
      height 23px
      background-size 100% 100%
    >p
      font-size 0.8rem
      margin-top 3px
      color $color_black_text_1
    >.light
      color $color_theme
</style>



