<template>
  <div class="hs-tab">
    <div
      class="item"
      :type="language"
      :class="{'item-active': currentIndex === index}"
      :style="{'width': `${100 / options.length}%`}"
      v-for="(item, index) in options" :key="index"
      @click="_select(index)" v-html="item"></div>
    <div class="slider-bar" :style="{width: `${100 / options.length}%`, transform: `translateX(${100 * currentIndex}%)`}"></div>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  props: {
    options: {
      type: Array,
      default: function() {
        return []
      },
      required: true
    },
    selectedIndex: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      currentIndex: this.selectedIndex
    }
  },
  watch: {
    selectedIndex(val) {
      this.currentIndex = val
    }
  },
  methods: {
    _select(index) {
      this.currentIndex = index
      this.$emit('select', index)
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-tab
  @extend .flex-row
  @extend .flex-between
  width 100%
  height 40px
  position relative
  background-color $color_white
  overflow auto
  >.item
    @extend .flex-center
    @extend .no-wrap
    height 100%
    font-size 1rem
    color $color_black_text_0
    padding 0 5px
    text-align center
  >.item[type="EN"]
    font-size 0.8rem
  >.item-active
    color $color_theme
  >.slider-bar
    position absolute
    left 0
    bottom 0
    height 2px
    background-color $color_theme
    transition all 300ms
</style>



