<template>
  <transition name="opacity">
    <div class="c-travel-standard" v-if="currentValue" @click.stop="currentValue = false">
      <hs-scroll class="table-view" :data="[details]">
        <ul>
          <div class="details" v-if="details">
            <div class="header">{{$t('base.TravelPolicy')}}</div>
            <div class="item">
              <p>{{travelPolicy}}</p>
            </div>
            <div class="item">
              <h2>{{$t('base.RankName')}}</h2>
              <p>{{details.Name}}</p>
            </div>
            <!-- 机票 -->
            <template v-if="type === 1">
              <div class="item">
                <h2>{{$t('base.BookingRange')}}</h2>
                <p>
                  {{details.IsFirstClass ? `${$t('base.FirstClass')}、` : ''}}
                  {{details.IsBussinessClass ? `${$t('base.BusinessClass')}、` : ''}}
                  {{details.IsSuperEconomyClass ? `${$t('base.SuperEconomyClass')}、` : ''}}
                  {{details.IsEconomyClass && (details.EconomyDiscountLimit === 100 || details.EconomyDiscountLimit === 0) ? $t('base.FullPrice') : ''}}
                  {{$t('base.EconomyClass')}}
                  {{details.IsEconomyClass && (details.EconomyDiscountLimit !== 100 && details.EconomyDiscountLimit !== 0) ? (details.EconomyDiscountLimit / 10 + $t('base.OffAndBelow')) : ''}}
                </p>
              </div>
            </template>
            <!-- 酒店 -->
            <template v-else-if="type === 2">
              <div class="item">
                <h2>{{$t('base.BookingLevel')}}</h2>
                <p v-for="(item, index) in details.UPBookingConditons" :key="index">
                  {{item.Name}}:&nbsp;{{$t('base.PriceCeiling')}}:{{item.PriceRange}}{{$t('base.RMBNight')}}
                </p>
              </div>
            </template>
            <!-- 火车票 -->
            <template v-else-if="type === 3">
              <div class="item">
                <h2>{{$t('base.BookingLevel')}}</h2>
                <p v-for="(item, index) in details.Ranks" :key="index">
                  {{item.Name}}:&nbsp;{{item.Seats.join('、')}}
                </p>
              </div>
            </template>
            <div class="item" v-if="details.SearchLimits">
              <h2>{{$t('base.QueryRange')}}</h2>
              <p>{{details.SearchLimits.join('、')}}</p>
            </div>
          </div>
        </ul>
      </hs-scroll>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
import HsScroll from 'base/hs-scroll/hs-scroll'
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    details: {
      type: Object,
      default: function() {
        return {}
      }
    },
    travelPolicy: {
      type: String,
      default: ''
    },
    type: {
      type: Number,
      default: 1
    }
  },
  components: {
    HsScroll
  },
  computed: {
    currentValue: {
      get: function() {
        return this.value
      },
      set: function(val) {
        return this.$emit('input', val)
      }
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.c-travel-standard
  position absolute
  top 0
  left 0
  width 100%
  height 100%
  background-color $color_white_mask
  color $color_black_text_0
  z-index 99
  padding 50px 20px
  >.table-view
    width 100%
    height 100%
    overflow hidden
    .details
      width 100%
      >.header
        @extend .flex-row
        @extend .flex-start
        width 100%
        padding 10px 0
        margin-bottom 10px
        font-size 1.1rem
        font-weight bold
        border-bottom 1px solid $color_black_text_0
      // >p
      //   margin-bottom 20px
      //   line-height 1.4em
      //   font-size 0.9rem
      //   &:last-child
      //     margin-bottom 10px
      >.item
        width 100%
        margin-bottom 20px
        >h2
          font-size 1.1rem
          font-weight bold
          margin-bottom 5px
        >p
          font-size 0.9rem
          line-height 1.2em
          margin-bottom 5px
        &:last-child
          margin-bottom 10px
</style>



