<template>
  <transition name="fade">
    <div class="hs-dialog" v-show="currentValue" :style="{height: height, backgroundColor: backgroundColor}" @click.stop="showDetails = false">
      <transition name="slide">
        <div class="details" v-show="showDetails">
          <slot></slot>
        </div>
      </transition>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    height: {
      type: String,
      default: '100%'
    },
    backgroundColor: {
      type: String,
      default: 'rgba(0, 0, 0, 0.7)'
    }
  },
  computed: {
    currentValue: {
      get: function() {
        this._setShowDetails(this.value)
        return this.value
      },
      set: function(val) {
        return this.$emit('input', val)
      }
    }
  },
  watch: {
    showDetails(val, oldVal) {
      if (!val && oldVal) {
        setTimeout(() => {
          this.currentValue = false
        }, 400)
      }
    }
  },
  data() {
    return {
      showDetails: false
    }
  },
  methods: {
    /** 关闭 */
    close() {
      this.showDetails = false
    },
    _setShowDetails(val) {
      setTimeout(() => {
        this.showDetails = val
      }, 100)
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-dialog
  position absolute
  width 100%
  top 0
  left 0
  // background-color $color-black_1
  z-index 99
  overflow hidden
  >.details
    width 100%
    height 100%
    position relative
.fade-enter-active
.fade-leave-active
  transition: all 300ms ease-in-out
.fade-enter
.fade-leave-active
  opacity 0
.slide-enter-active
.slide-leave-active
  transition: all 400ms ease-in-out
.slide-enter
.slide-leave-active
  transform translateY(100%)
</style>



