<template>
  <div class="hs-null">
    <template v-if="noData">
      <div class="logo" style="background-image: url('static/image/base/null/null-image.png')"></div>
      <p>{{title}}</p>
    </template>
  </div>
</template>
<script type="text/ecmascript-6">
import Vue from 'vue'
export default {
  props: {
    title: {
      type: String,
      default: Vue.prototype.$t('base.NoData')
    }
  },
  computed: {
    noData() {
      return !Vue.prototype.$loading.isLoading()
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-null
  @extend .flex-column
  @extend .flex-start
  width 100%
  height 100%
  font-size 0.8rem
  >.logo
    width 82px
    height 74px
    background-size 100% 100%
    margin-top 150px
  >p
    margin-top 20px
</style>



