<template>
  <transition name="hs-fade">
    <div class="hs-nation-picker" v-if="currentValue" @click.stop>
      <c-header :title="$t('base.Nation')">
        <p class="c-header-left" slot="left" @click="currentValue = false">{{$t('base.Cancel')}}</p>
      </c-header>
      <div class="search-bar">
        <div class="bar">
          <input maxlength='50' v-model.trim="searchTxt" :placeholder="$t('base.PleaseEnterAKeywordToSearch')"/>
          <div class="logo" style="background-image: url('static/image/plane-ticket/book/search.png')"></div>
        </div>
      </div>
      <hs-scroll
        :data="[details]" class="tableView">
        <ul>
          <hs-section :title="$t('base.Result')" v-if="!noValue(searchTxt)">
            <hs-cell-1 :value="$t('base.NoData')" :showLogo="false" v-if="noValue(JSON.stringify(vaguelist))"></hs-cell-1>
            <hs-cell-1 :showLogo="false" v-for="(item, index) in vaguelist" :key="index">
              <ul class="search-list">
                <li class="item-city" @click="_select(item)">
                  <p>{{item[`NationName_${language}`]}}</p>
                  <p>{{item.NationCode}}</p>
                </li>
              </ul>
            </hs-cell-1>
          </hs-section>

          <!-- 热门 -->
          <hs-section v-if="details.hot"
            :title="$t('base.PopularDestinations')">
            <!-- 中文样式 -->
            <hs-cell-1 :showLogo="false" v-if="language === 'CN'">
              <div class="city-blocks">
                <p class="block" v-for="(item, index) in details.hot.NationList" :key="index" @click="_select(item)">
                  {{item[`NationName_${language}`]}}
                </p>
              </div>
            </hs-cell-1>
            <!-------------------------------------------------------------------------------------------------------------------------------------------------------------->
            <!-- 英文样式 -->
            <template v-else>
              <hs-cell-1 v-for="(item, index) in details.hot.NationList" :key="index" :value="item[`NationName_${language}`]" :showLogo="false" @click.native="_select(item)"></hs-cell-1>
            </template>
            <!-------------------------------------------------------------------------------------------------------------------------------------------------------------->
          </hs-section>

          <!-- A - Z -->
          <div class="letterPicker" v-if="details.list">
            <div class="letter-block" v-for="(item, index) in details.list" :key="index">
              <div class="header" @click="details.list[index].open = !details.list[index].open">
                <p>{{item.Label}}</p>
                <div class="logo" :class="{'logo-open': item.open}"></div>
              </div>
              <div class="details" v-if="item.open">
                <div class="city" v-for="(itemChild, indexChild) in item.NationList" :key="indexChild" @click="_select(itemChild)">
                  <p>{{itemChild[`NationName_${language}`]}}</p>
                  <p>{{itemChild.NationCode}}</p>
                </div>
              </div>
            </div>
          </div>
        </ul>
      </hs-scroll>
    </div>
  </transition>
</template>

<script type='text/ecmascript-6'>
import CHeader from 'components/c-header/c-header'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    }
  },
  created() {
    this._getDetails()
  },
  data() {
    return {
      details: {},
      searchTxt: '',
      vaguelist: []
    };
  },
  computed: {
    currentValue: {
      get: function(val) {
        return this.value
      },
      set: function(val) {
        return this.$emit('input', val)
      }
    }
  },
  watch: {
    searchTxt(val) {
      this.timer && clearTimeout(this.timer) // eslint-disable-line
      this.timer = setTimeout(() => {
        this._vague(val)
      }, 100)
    }
  },
  methods: {
    close() {
      this.currentValue = false
    },

    _getDetails() {
      if (window.localStorage.NATION) {
        this.details = JSON.parse(window.localStorage.NATION)
      }
    },

    /** 模糊搜索 */
    _vague(val) {
      let res = []
      let valToUpperCase = val.toUpperCase() // 转大写
      this.details.list.forEach(element => {
        element.NationList.forEach(elementChild => {
          if (
            elementChild.NationName_CN.includes(valToUpperCase) ||
            elementChild.NationName_EN.includes(valToUpperCase) ||
            elementChild.NationCode.includes(valToUpperCase) ||
            elementChild.NationSpell.includes(valToUpperCase)) {
            res.push(elementChild)
          }
        })
      })
      this.vaguelist = res
    },

    /** 选择 */
    _select(item) {
      this.$emit('select', item)
      this.close()
    }
  },
  components: {
    CHeader,
    HsScroll,
    HsSection,
    HsCell1
  }
};
</script>

<style lang='stylus' scoped='' type='sylesheet/stylus'>
@import '~common/stylus/variable'
@import '~common/stylus/base'
.hs-nation-picker
  width 100%
  height 100%
  position absolute
  top 0
  left 0
  background-color $color_white
  >.header
    @extend .flex-row
    @extend .flex-between
    width 100%
    height 50px
    padding 0 10px
    background-color $color_theme
    color $color_white
    >p
      @extend .flex-center
      height 100%
      color $color_white
      &:nth-child(1)
        font-size 1rem
      &:nth-child(2)
        font-size 1.1rem
        font-weight bolder
      &:nth-child(3)
        width 2rem
  >.search-bar
    @extend .flex-center
    width 100%
    padding 5px 15px
    background-color $color_white
    >.bar
      @extend .flex-row
      @extend .flex-between
      width 100%
      border-radius 4px
      border 1px solid $color_gray_layer
      padding 10px
      background-color $color_white
      font-size 0.9rem
      >input
        width calc(100% - 30px)
        height 100%
      >.logo
        width 20px
        height 20px
        background-size 100% 100%

  >.tableView
    width 100%
    height calc(100% - 52px - 45px)
    overflow hidden
    .search-list
      width 100%
      background-color $color_white
      .item-city
        @extend .flex-row
        @extend .flex-start
        width 100%
        min-height 50px
        border-bottom 1px solid $color_gray_layer
        padding 10px 0
        &:last-child
          border-bottom-width 0px
        >p
          font-size 1rem
          &:nth-child(1)
            color $color_black_text_0
            font-weight bold
            margin-right 5px
          &:nth-child(2)
            color $color_gray_text
      .item-city[type="EN"]
        >p
          line-height 1.4em
          font-size 0.7rem !important
      .item-airport
        @extend .flex-row
        @extend .flex-between
        width calc(100% - 35px)
        min-height 50px
        margin-left 35px
        border-bottom 1px solid $color_gray_layer
        // &:last-child
        //   border-bottom-width 0px
        >.item-airport-details
          @extend .flex-row
          @extend .flex-start
          width calc(100% - 25px)
          padding 10px 0
          >.logo
            width 20px
            height 20px
            background-size 100% 100%
            transform scaleX(-1)
            filter fliph
            margin-right 5px
          >p
            font-size 1rem
          >.details
            >p
              line-height 1.4em
              &:nth-child(1)
                @extend .no-wrap
                font-size 0.7rem
                color $color_theme
                padding 2px
                border 1px solid $color_theme
                border-radius 2px
                margin-right 5px
              &:nth-child(2)
                color $color_black_text_0
                margin-right 5px
              &:nth-child(3)
                color $color_gray_text
        >.item-airport-details[type="CN"]
          >.details
            @extend .flex-row
            @extend .flex-start
        >.item-airport-details[type="EN"]
          >.details
            @extend .flex-column
            @extend .flex-top
            font-size 0.7rem
            >p
              &:nth-child(1)
                border 0
                padding 0
                font-weight bold
        >.logo
          width 20px
          height 20px
          background-size 100% 100%
      // .list-item
      //   @extend .flex-row
      //   @extend .flex-start
      //   width 100%
      //   height 50px
      //   padding 0 10px
      //   border-top 1px solid $color_gray_layer
      //   &:first-child
      //     border-top-width 0px
    .city-blocks
      @extend .flex
      @extend .flex-row
      @extend .flex-start
      @extend .flex-top
      flex-wrap wrap
      width 100%
      padding 5px 0
      >.block
        @extend .flex-center
        width calc(33.3333% - 10px)
        margin 5px
        padding 6px 0
        border 1px solid $color_gray_layer
        border-radius 3px
        font-size 1rem
    .letterPicker
      width 100%
      .letter-block
        width 100%
        >.header
          @extend .flex-row
          @extend .flex-between
          width 100%
          height 44px
          background-color $color_gray_background_0
          padding 0 10px
          >p
            font-size 0.9rem
          >.logo
            width 20px
            height 20px
            background-size 100% 100%
            background-image url('../../../static/image/base/cell/right_arrow_red.png')
            transition all 0.3s
          >.logo-open
            transform rotate(90deg)
        >.details
          width 100%
          .city
            @extend .flex-row
            @extend .flex-start
            width 100%
            height 44px
            padding 0 10px
            border-top 1px solid $color_gray_layer
            >p
              margin-right 5px
              &:nth-child(2)
              &:nth-child(3)
                color $color_gray_text
              &:last-child
                margin-right 0
.address-picker-enter-active
.address-picker-leave-active
  transition: all 300ms
  -webkit-transition: all 300ms
.address-picker-enter
.address-picker-leave-to
  transform translateY(100%)
</style>



