<template>
  <transition name="slide">
    <div class="hs-container-child" v-if="show">
      <c-header leftIcon1="return.png" :leftFn1="close" :title="$t('base.ChooseDepartment')"></c-header>
      <div class="search-bar">
        <div class="bar">
          <input maxlength='50' v-model.trim="KeyWord" :placeholder="$t('base.PleaseEnterAKeywordToSearch')"/>
          <div class="logo" style="background-image: url('static/image/plane-ticket/book/search.png')" @click="_getDatalist"></div>
        </div>
      </div>
      <hs-scroll
        class="table-view"
        ref="tableView"
        :data="datalist"
        :options="tableViewOptions"
        @pulling-down="_getDatalist"
        @pulling-up="_getDatalistMore">
        <ul>
          <hs-section>
            <hs-cell-1 :showLogo="false" :value="item.Name" v-for="(item, index) in datalist" :key="index" @click.native="_select(item)"></hs-cell-1>
          </hs-section>
        </ul>
      </hs-scroll>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
import CHeader from 'components/c-header/c-header'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import {kpiUserGetDepartments} from 'api'
export default {
  data() {
    return {
      show: false,
      tableViewOptions: {
        pullDownRefresh: {
          threshold: 50
        }
      },
      currentPageIndex: 1,
      currentPageSize: 30,
      datalist: [],
      KeyWord: ''
    }
  },
  watch: {
    show(val) {
      if (val) {
        this._getDatalist()
      }
    },
    KeyWord(val) {
      this.timer && clearTimeout(this.timer) // eslint-disable-line
      this.timer = setTimeout(() => {
        this._getDatalist()
      }, 1000)
    }
  },
  methods: {
    open() {
      this.show = true
    },
    close() {
      this.show = false
    },
    _getDatalist() {
      this.currentPageIndex = 1
      this.tableViewOptions.pullUpLoad = false
      let params = {
        PageIndex: this.currentPageIndex,
        PageSize: this.currentPageSize,
        KeyWord: this.KeyWord
      }
      kpiUserGetDepartments(params).then((res) => {
        console.log('部门: ', res)
        this.datalist = res.ResultData
        if (res.ResultData.length >= this.currentPageSize) {
          this.tableViewOptions.pullUpLoad = {threshold: 50}
        }
        this.$refs.tableView && this.$refs.tableView.forceUpdate()
      })
    },
    _getDatalistMore() {
      this.currentPageIndex++
      let params = {
        PageIndex: this.currentPageIndex,
        PageSize: this.currentPageSize
      }
      kpiUserGetDepartments(params).then((res) => {
        console.log('部门: ', res)
        this.datalist = this.datalist.concat(res.ResultData)
        if (res.ResultData.length < this.currentPageSize) {
          this.tableViewOptions.pullUpLoad = false
        } else {
          this.tableViewOptions.pullUpLoad = {threshold: 50}
        }
        this.$refs.tableView && this.$refs.tableView.forceUpdate()
      })
    },
    _select(item) {
      this.$emit('select', item)
      this.close()
      // this.passenger.Department = item
      // this.$router.replace({name: 'personalPassengersDetails', query: {TravelType: this.travelType}, params: {passenger: this.passenger}})
    }
  },
  // beforeRouteEnter(to, from, next) {
  //   next(vm => {
  //     if (vm.noValue(JSON.stringify(from.params))) {
  //       vm.$router.replace('/personal/center')
  //     }
  //   })
  // },
  // beforeRouteLeave (to, from, next) {
  //   console.log(to)
  //   if (to.path === '/personal/center') {
  //     next()
  //   } else if (to.path === '/personal/passengersDetails' && !this.noValue(JSON.stringify(to.params))) {
  //     next()
  //   } else {
  //     this.$router.replace({name: 'personalPassengersDetails', query: {TravelType: this.travelType}, params: {passenger: this.passenger}})
  //     next(false)
  //   }
  // },
  components: {
    CHeader,
    HsScroll,
    HsSection,
    HsCell1
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container-child
  @extend .flex-column
  @extend .flex-start
  position absolute
  width 100%
  height 100%
  left 0
  top 0
  bottom 0
  background-color $color_white
  z-index 200
  >.search-bar
    @extend .flex-center
    width 100%
    padding 5px 15px
    background-color $color_white
    >.bar
      @extend .flex-row
      @extend .flex-between
      width 100%
      border-radius 4px
      border 1px solid $color_gray_layer
      padding 10px
      background-color $color_white
      font-size 0.9rem
      >input
        width calc(100% - 30px)
        height 100%
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
  .table-view
    width 100%
    height calc(100% - 45px - 52px)
    overflow hidden
    background-color $color_white
</style>



