<template>
  <div class="hs-cell-small-title" :type="fixedLanguage ? fixedLanguage : language">
    <div class="details">
      <div class="left" :style="{width: showLogo ? 'calc(100% - 30px)' : '100%'}">
        <div class="header">
          <p>{{title}}</p>
          <div class="logo" style="background-image: url('static/image/plane-ticket/details/zhushi.png')" v-if="warning" @click.stop="_clickHeader"></div>
        </div>
        <slot>
          <p class="normal" v-if="type === 'normal'">{{currentValue}}</p>
          <p class="bold" v-else-if="type === 'bold'">{{currentValue}}</p>
          <input class="input" v-model="currentValue" maxlength='50' :placeholder="placeholder" v-else-if="type === 'input'"/>
          <input class="input disabled" v-model="currentValue" maxlength='50' :placeholder="placeholder" v-else-if="type === 'disabled-input'"/>
        </slot>
      </div>
      <div v-if="showLogo" class="logo" :style="{'background-image': `url('static/image/base/cell/${logoName}')`}"></div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  props: {
    title: {
      type: String,
      default: ''
    },
    value: {
      type: String,
      default: ''
    },
    type: {
      type: String,
      default: 'normal'
    },
    placeholder: {
      type: String,
      default: ''
    },
    logoName: {
      type: String,
      default: 'right_arrow_gray.png'
    },
    showLogo: {
      type: Boolean,
      default: true
    },
    warning: {
      type: Boolean,
      default: false
    },
    fixedLanguage: {
      type: String,
      default: ''
    }
  },
  computed: {
    currentValue: {
      get: function() {
        return this.value
      },
      set: function(val) {
        return this.$emit('input', val)
      }
    }
  },
  methods: {
    _clickHeader() {
      this.$emit('header')
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-cell-small-title
  width 100%
  padding 0 15px
  min-height 44px
  >.details
    @extend .flex-row
    @extend .flex-between
    width 100%
    padding 10px 0
    border-bottom 1px solid $color_gray_layer
    >.left
      >.header
        @extend .flex-row
        @extend .flex-start
        width 100%
        font-size 0.8rem
        color $color_black_text_1
        margin-bottom 8px
        >.logo
          width 14px
          height 14px
          background-size 100% 100%
          margin-left 3px
      >.normal
      >.bold
      >.input
        width 100%
        font-size 1rem
        line-height 1.2em
        padding 0
      >.bold
        font-weight bold
    >.logo
      width 20px
      height 20px
      background-size 100% 100%
  &:last-child
    >.details
      border-bottom 0px
.hs-cell-small-title[type="CN"]
  >.details
    padding 5px 0
    >.left
      @extend .flex-row
      @extend .flex-start
      >.header
        width 100px
        font-size 1rem
        margin-bottom 0px
        >.logo
          width 20px
          height 20px
      >.normal
      >.bold
      >.input
        @extend .flex-row
        @extend .flex-start
        width calc(100% - 100px)
        min-height 33px
</style>



