
<template>
  <transition name="slide">
    <div class="hs-container-child" v-if="show">
      <c-header class="header" :title="powerDetails.title" leftIcon1="return.png" :leftFn1="close">
        <p slot="right" class="right-btn" @click="_confirm">{{$t('base.Confirm')}}</p>
      </c-header>

      <div class="search-bar">
        <div class="bar">
          <input maxlength='50' v-model.trim="KeyWord" :placeholder="$t('base.PleaseEnterAKeywordToSearch')"/>
          <div class="logo" style="background-image: url('static/image/plane-ticket/book/search.png')" @click="_getDatalist"></div>
        </div>
      </div>

      <hs-scroll
        class="table-view"
        ref="tableView"
        :data="datalist"
        :options="tableViewOptions"
        @pulling-down="_getDatalist"
        @pulling-up="_getDatalistMore">
        <ul>

          <hs-section v-if="BookInit.IsAllowAddTraveler">
            <hs-cell-1 :showLogo="false">
              <div class="custom-cell">
                <hs-button width="100%" @click="_addPassenger">{{$t('base.New')}}</hs-button>
              </div>
            </hs-cell-1>
          </hs-section>

          <div style="width: 100%; height: 1px;"></div>
          <hs-null v-if="!datalist || datalist.length === 0"></hs-null>
          <div v-for="(item, index) in datalist" :key="index" @click="_select(item)"
            class="custom-cell-passenger">
            <div class="checkbox" v-show="!item.Incomplete && currentSelectedPassengers && currentSelectedPassengers.map(v => v.ID).indexOf(item.ID) >= 0" style="background-image: url('static/image/base/check-box/checkbox-right.png')"></div>
            <div class="checkbox" v-show="!item.Incomplete && currentSelectedPassengers && currentSelectedPassengers.map(v => v.ID).indexOf(item.ID) < 0" style="background-image: url('static/image/base/check-box/checkbox-right-default.png')"></div>
            <div class="details">
              <div class="header">
                <p class="name">
                  {{item.ChName}}
                  <template v-if="item.LastName && item.FirstName">{{item.LastName}}&nbsp;{{item.FirstName}}</template>
                </p>
                <div class="staff" v-if="item.UpType === 1">员工</div>
                <div class="no-staff" v-if="item.UpType === 2">非员工</div>
              </div>
              <p v-if="item.Incomplete" class="mark">基本信息不全</p>
              <div class="item" v-if="item.Credentials && item.Credentials.filter(v => v.CredentialType === item.DefaultCredentialType)[0] && item.Credentials.filter(v => v.CredentialType === item.DefaultCredentialType)[0].CredentialNo">
                <p>
                  {{item.Credentials.filter(v => v.CredentialType === item.DefaultCredentialType)[0].CredentialName}}
                </p>
                <p>
                  {{item.Credentials.filter(v => v.CredentialType === item.DefaultCredentialType)[0].CredentialNo}}
                </p>
              </div>
              <div class="item" v-if="item.Mobile">
                <p>{{$t('base.CellphoneNumber')}}</p>
                <p>{{item.Mobile}}</p>
              </div>
            </div>
            <div class="footer">
              <p @click.stop="_gotoDetails(index, item)">{{$t('base.Edit')}}</p>
            </div>
          </div>
          <div style="width: 100%; height: 10px;"></div>

        </ul>
      </hs-scroll>

      <!-- 常旅客详情 -->
      <passenger-details
        ref="PassengerDetails"
        :details="passengerDetails"
        :BookInit="BookInit"
        :OrderBusinessType="OrderBusinessType"
        @delegate="_delegateWithPassengerDetails"
      ></passenger-details>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
import CHeader from 'components/c-header/c-header'
import HsButton from 'base/hs-button/hs-button'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsNull from 'base/hs-null/hs-null'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import PassengerDetails from 'pages/personal/passengers/details.vue'
import {kpiFrequentTravelerList, kpiFrequentTravelerPut, kpiFrequentTravelerAdd} from 'api'
export default {
  props: {
    XOrder: {
      type: Object,
      default: function() {
        return {}
      }
    },
    BookInit: {
      type: Object,
      default: function() {
        return {}
      }
    },
    OrderBusinessType: {
      type: Number,
      default: 1
    },
    MaxLength: {
      type: Number,
      default: 9999
    },
    GuestToRoomNo: {
      default: undefined
    }
  },
  data() {
    return {
      tableViewOptions: {
        pullDownRefresh: {
          threshold: 50
        }
      },
      details: {},
      datalist: [],
      currentPageIndex: 1,
      currentPageSize: 30,
      KeyWord: '',
      show: false,
      /** 当前选中的乘客 */
      currentSelectedPassengers: [],
      /** 常旅客详情 */
      passengerDetails: {},
      /** 各板块权限 */
      powerDetails: {
        title: '', // 标题
        maxLength: 9999, // 最多可选人数
        requiredCredential: false, // 证件号必填
        requiredEffectiveDate: false, // 证件有效期必填
        requiredNation: false, // 国籍必填
        requiredMobile: true // 手机号必填
      }
    }
  },
  watch: {
    show(val) {
      if (val) {
        // 初始化已选乘客
        let Passengers = []
        if (this.OrderBusinessType === 1 || this.OrderBusinessType === 6 || this.OrderBusinessType === 10) {
          Passengers = JSON.parse(JSON.stringify(this.XOrder.Passengers))
        } else {
          Passengers = JSON.parse(JSON.stringify(this.XOrder.Guests))
        }
        this.currentSelectedPassengers = Passengers.map(v => {
          return this._checkList(v)
        })

        this._initPowerDetails()

        this._getDatalist()
      }
    }
  },
  methods: {
    open() {
      this.show = true
    },
    close() {
      this.show = false
    },

    /** 根据板块初始化页面权限 */
    _initPowerDetails() {
      // 常旅客
      if (this.OrderBusinessType === 0) {
        // 常旅客不用此列表
        this.powerDetails.requiredMobile = false
      // 国内机票
      } else if (this.OrderBusinessType === 1) {
        this.powerDetails.title = this.$t('base.Passenger')
        this.powerDetails.maxLength = 9
        this.powerDetails.requiredCredential = true
      // 国际机票
      } else if (this.OrderBusinessType === 6) {
        this.powerDetails.title = this.$t('base.Passenger')
        this.powerDetails.requiredCredential = true
        this.powerDetails.requiredEffectiveDate = true
        this.powerDetails.requiredNation = true
      // 国内酒店
      } else if (this.OrderBusinessType === 2) {
        this.powerDetails.title = this.$t('base.Roomer')
        this.powerDetails.maxLength = this.MaxLength
      // 国际酒店
      } else if (this.OrderBusinessType === 11) {
        this.powerDetails.title = this.$t('base.Roomer')
        this.powerDetails.maxLength = this.MaxLength
      // 火车票
      } else if (this.OrderBusinessType === 10) {
        this.powerDetails.title = this.$t('base.Passenger')
        this.powerDetails.maxLength = 5
        this.powerDetails.requiredCredential = true
      }
    },

    /** 获取常旅客列表 */
    _getDatalist() {
      this.details = {}
      this.datalist = []
      this.currentPageIndex = 1
      this.tableViewOptions.pullUpLoad = false
      let params = {
        KeyWord: this.KeyWord,
        OrderBusinessType: this.OrderBusinessType,
        PageIndex: this.currentPageIndex,
        PageSize: this.currentPageSize,
        TravelType: this.XOrder.TravelType
      }
      kpiFrequentTravelerList(params).then((res) => {
        console.log('获取常旅客列表: ', res)
        this.details = res.ResultData
        let list = res.ResultData.FrequentTravelers.map(v => {
          return this._checkList(v)
        })
        list = list.filter(element => this.currentSelectedPassengers.map(v => v.ID).indexOf(element.ID) < 0)
        this.datalist = this.currentSelectedPassengers.concat(list)
        if (res.ResultData.FrequentTravelers.length >= this.currentPageSize) {
          this.tableViewOptions.pullUpLoad = {threshold: 50}
        }
        this.$refs.tableView && this.$refs.tableView.forceUpdate()
      })
    },

    /** 获取更多常旅客列表 */
    _getDatalistMore() {
      this.currentPageIndex++
      let params = {
        KeyWord: this.KeyWord,
        OrderBusinessType: this.OrderBusinessType,
        PageIndex: this.currentPageIndex,
        PageSize: this.currentPageSize,
        TravelType: this.XOrder.TravelType
      }
      kpiFrequentTravelerList(params).then((res) => {
        console.log('常旅客列表： ', res)
        let list = res.ResultData.FrequentTravelers.map(v => {
          return this._checkList(v)
        })
        list = list.filter(element => this.currentSelectedPassengers.map(v => v.ID).indexOf(element.ID) < 0)
        this.datalist = this.datalist.concat(list)
        if (res.ResultData.FrequentTravelers.length < this.currentPageSize) {
          this.tableViewOptions.pullUpLoad = false
        } else {
          this.tableViewOptions.pullUpLoad = {threshold: 50}
        }
        this.$refs.tableView && this.$refs.tableView.forceUpdate()
      })
    },

    /** 检查和补充数据完整性 */
    _checkList(v) {
      let res = JSON.parse(JSON.stringify(v))
      // 1. 赋值通知初始化参数
      res.IsSendBookSms = res.IsSendBookSms === null ? this.BookInit.IsSendBookSmsToPassenger : res.IsSendBookSms
      res.IsSendBookEmail = res.IsSendBookEmail === null ? this.BookInit.IsSendBookEmailToPassenger : res.IsSendBookEmail
      res.IsSendIssuedSms = res.IsSendIssuedSms === null ? this.BookInit.IsSendIssuedSmsToPassenger : res.IsSendIssuedSms
      res.IsSendIssuedEmail = res.IsSendIssuedEmail === null ? this.BookInit.IsSendIssuedEmailToPassenger : res.IsSendIssuedEmail
      res.IsSendBookWechat = res.IsSendBookWechat === null ? this.BookInit.IsSendBookWechatToPassenger : res.IsSendBookWechat
      res.IsSendIssuedWechat = res.IsSendIssuedWechat === null ? this.BookInit.IsSendIssuedWechatToPassenger : res.IsSendIssuedWechat
      res.IsSendConfirmSms = res.IsSendConfirmSms === null ? this.BookInit.IsSendConfirmSmsToGuest : res.IsSendConfirmSms
      res.IsSendConfirmEmail = res.IsSendConfirmEmail === null ? this.BookInit.IsSendConfirmEmailToGuest : res.IsSendConfirmEmail

      // 冗余
      res.UpID = res.UpID ? res.UpID : res.ID

      res.Incomplete = false

      let credential = null
      if (this.powerDetails.requiredCredential) {
        credential = res.Credentials.filter(a => a.CredentialType === res.DefaultCredentialType)[0]
      }
      // 1. 中英文姓名必填一个
      if (
        this.OrderBusinessType === 0 &&
        this.noValue(res.ChName) &&
        (this.noValue(res.FirstName) || this.noValue(res.LastName))
      ) {
        res.Incomplete = true
      } else if (this.OrderBusinessType !== 0 && res.DefaultNameType === 0 && this.noValue(res.ChName)) {
        res.Incomplete = true
      } else if (this.OrderBusinessType !== 0 && res.DefaultNameType === 1 && (this.noValue(res.FirstName) || this.noValue(res.LastName))) {
        res.Incomplete = true

      // 2. 英文姓英文名需同时填写
      } else if (res.LastName && this.noValue(res.FirstName)) {
        res.Incomplete = true
      } else if (res.FirstName && this.noValue(res.LastName)) {
        res.Incomplete = true

      // 3. 保存的证件号必填
      } else if (this.powerDetails.requiredCredential && this.noValue(credential.CredentialNo)) {
        res.Incomplete = true

      // 4. 校验保存的证件号格式
      } else if (this.powerDetails.requiredCredential && !this.noValue(credential.CredentialNo) && credential.CredentialType === 1 && !this.idCard(credential.CredentialNo).valid) {
        res.Incomplete = true
      // eslint-disable-next-line
      } else if (this.powerDetails.requiredCredential && !this.noValue(credential.CredentialNo) && credential.CredentialNo.match(/[^a-zA-Z-0-9-\/\ ]/g) !== null) {
        res.Incomplete = true

      // 5. 国际机票, 除身份证, 其余证件有效期必填
      } else if (this.powerDetails.requiredEffectiveDate && credential.CredentialType !== 1 && this.noValue(credential.EffectiveDate)) {
        res.Incomplete = true

      // 6. 国际机票国籍必填
      } else if (this.powerDetails.requiredNation && (!res.Nation || !res.Nation.Name)) {
        res.Incomplete = true

      // 7. 生日必填
      } else if (this.noValue(res.Birthday)) {
        res.Incomplete = true

      // 8. 业务板块手机号必填
      } else if (this.powerDetails.requiredMobile && this.noValue(res.Mobile)) {
        res.Incomplete = true

      // 9. 校验手机号格式
      } else if (!this.noValue(res.Mobile) && !this.isPhoneNumber(res.Mobile)) {
        res.Incomplete = true

      // 10. 邮箱必填
      } else if (this.noValue(res.Email) && (res.IsSendBookEmail || res.IsSendIssuedEmail || res.IsSendConfirmEmail)) {
        res.Incomplete = true

      // 11. 校验邮箱格式
      } else if (!this.noValue(res.Email) && !this.isEmail(res.Email)) {
        res.Incomplete = true

      // 12. 成本中心
      } else if (this.BookInit && this.BookInit.IsRequiredCostCenter && (!res.CostCenter || !res.CostCenter.Name)) {
        res.Incomplete = true

      // 13. 检查部门
      } else if (this.BookInit && this.BookInit.IsRequiedDepartment && res.UpType === 1 && (!res.Department || !res.Department.Name)) {
        res.Incomplete = true

      // 14. 检查BU
      } else if (this.BookInit && this.BookInit.IsRequiredBusinessUnit && res.UpType === 1 && (!res.BusinessUnit || !res.BusinessUnit.Name)) {
        res.Incomplete = true
      }

      // console.log('res: ', res)
      return res
    },

    /** 添加常旅客 */
    _addPassenger() {
      this.passengerDetails = {}
      this.$set(this.passengerDetails, 'ChName', '')
      this.$set(this.passengerDetails, 'FirstName', '')
      this.$set(this.passengerDetails, 'LastName', '')
      this.$set(this.passengerDetails, 'Gender', true)
      this.$set(this.passengerDetails, 'Birthday', this.beTime(new Date()))
      this.$set(this.passengerDetails, 'Email', '')
      this.$set(this.passengerDetails, 'Mobile', '')
      this.$set(this.passengerDetails, 'Department', null)
      this.$set(this.passengerDetails, 'CostCenter', null)
      this.$set(this.passengerDetails, 'BusinessUnit', null)
      this.$set(this.passengerDetails, 'Credentials', this.details.SupportCredentialTypes.map(v => {
        return {
          CredentialName: v.CredentialName,
          CredentialType: v.CredentialType,
          CredentialNo: '',
          EffectiveDate: ''
        }
      }))
      this.$set(this.passengerDetails, 'UpType', Number(this.XOrder.TravelType) === 0 ? 1 : 2)
      this.$set(this.passengerDetails, 'Nation', null)
      this.$set(this.passengerDetails, 'TravelType', Number(this.XOrder.TravelType))
      this.$set(this.passengerDetails, 'TravelCardList', [])
      this.$set(this.passengerDetails, 'DefaultCredentialType', 1)
      this.$set(this.passengerDetails, 'DefaultNameType', 0)
      if (this.$refs.PassengerDetails) {
        this.$refs.PassengerDetails.open()
      }
    },

    /** 修改常旅客去详情页面 */
    _gotoDetails(index, item) {
      this.passengerDetails = item
      this.$set(this.passengerDetails, 'index', index)
      if (this.$refs.PassengerDetails) {
        this.$refs.PassengerDetails.open()
      }
    },

    /** 常旅客详情回调 */
    _delegateWithPassengerDetails(item) {
      let params = item
      params.OrderBusinessType = this.OrderBusinessType
      // 编辑常旅客详情
      if (item.index >= 0) {
        kpiFrequentTravelerPut(params).then((res) => {
          console.log('修改: ', res)
          if (res.ResultData === true) {
            this.$toast(this.$t('TM.SaveSucceed'))
            // 替换列表
            this.datalist.splice(item.index, 1, this._checkList(item))
            // 替换已选乘客信息
            let indexPassengers = this.currentSelectedPassengers.map(v => v.ID).indexOf(item.ID)
            if (indexPassengers >= 0) {
              this.currentSelectedPassengers.splice(indexPassengers, 1, item)
            } else if (this.OrderBusinessType === 11 && this.currentSelectedPassengers.filter(v => v.GuestToRoomNo === this.GuestToRoomNo).length < this.powerDetails.maxLength) {
              this.currentSelectedPassengers.push(Object.assign(item, {GuestToRoomNo: this.GuestToRoomNo}))
            } else if (this.OrderBusinessType !== 11 && this.currentSelectedPassengers.length < this.powerDetails.maxLength) {
              this.currentSelectedPassengers.push(item)
            }
            console.log('this.currentSelectedPassengers.filter(v => v.GuestToRoomNo).length: ', this.currentSelectedPassengers.filter(v => v.GuestToRoomNo).length)
            console.log('this.powerDetails.maxLength: ', this.powerDetails.maxLength)
          } else {
            this.$toast(this.$t('TM.SaveFailed'))
          }
        })
      } else {
        kpiFrequentTravelerAdd(params).then((res) => {
          console.log('常旅客添加: ', res)
          item.ID = res.ResultData.ID
          item.UpID = res.ResultData.ID
          item.TravelerID = res.ResultData.TravelerID
          // 插入
          this.datalist.splice(0, 0, this._checkList(item))
          this.currentSelectedPassengers.splice(0, 0, this._checkList(item))
          this.$toast(this.$t('TM.AddSucceed'))
          // this._getDatalist()
        })
      }
    },

    /** 选择 */
    _select(item) {
      if (item.Incomplete) {
        return
      }
      let index = this.currentSelectedPassengers.map(v => v.ID).indexOf(item.ID)
      if (index >= 0) {
        this.currentSelectedPassengers.splice(index, 1)
      } else if (this.OrderBusinessType === 11 && this.currentSelectedPassengers.filter(v => v.GuestToRoomNo === this.GuestToRoomNo).length < this.powerDetails.maxLength) {
        this.currentSelectedPassengers.push(Object.assign(item, {GuestToRoomNo: this.GuestToRoomNo}))
      } else if (this.OrderBusinessType !== 11 && this.currentSelectedPassengers.length < this.powerDetails.maxLength) {
        this.currentSelectedPassengers.push(item)
      } else {
        this.$toast(this.$t('TM.CanOnlySelectUpToXpassengers', 'function', {number: this.powerDetails.maxLength, name: this.powerDetails.title}))
      }
    },

    _confirm() {
      this.currentSelectedPassengers = this.currentSelectedPassengers.map(v => {
        v.Name = v.DefaultNameType === 0 ? v.ChName : `${v.FirstName} ${v.LastName}`
        v.Credential = v.Credentials.filter(a => a.CredentialType === v.DefaultCredentialType)[0]
        return v
      })
      this.$emit('delegateWithConfirm', this.currentSelectedPassengers)
      this.close()
    }
  },
  components: {
    CHeader,
    HsButton,
    HsCell1,
    HsNull,
    HsScroll,
    HsSection,
    PassengerDetails
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container-child
  position absolute
  width 100%
  height 100%
  left 0
  top 0
  bottom 0
  background-color $color_white
  z-index 200
  >.header
    >.header-left
      @extend .flex-row
      @extend .flex-start
      width 60px
      height 100%
      >p
        font-size 0.9rem
    .right-btn
      font-size 0.9rem
  >.search-bar
    @extend .flex-center
    width 100%
    padding 5px 15px
    background-color $color_white
    >.bar
      @extend .flex-row
      @extend .flex-between
      width 100%
      border-radius 4px
      border 1px solid $color_gray_layer
      padding 10px
      background-color $color_white
      font-size 0.9rem
      >input
        width calc(100% - 30px)
        height 100%
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
  >.table-view
    width 100%
    height calc(100% - 45px - 52px)
    overflow hidden
    background-color $color_gray_background_0

    .custom-cell-passenger
      width calc(100% - 20px)
      padding 15px
      margin-left 10px
      margin-top 10px
      background-color $color_white
      border-radius 3px
      box-shadow 0 0 10px $color_gray_boxshadow
      position relative
      overflow hidden
      >.checkbox
        width 40px
        height 40px
        background-size 100% 100%
        position absolute
        top 0
        right 0
      >.details
        width 100%
        >.header
          @extend .flex-row
          @extend .flex-start
          width 100%
          padding 5px 0
          >.name
            font-size 1.1rem
            font-weight bold
          >.staff
          >.no-staff
            padding 2px 5px
            border-radius 3px
            border 1px solid $color_green_layer
            color $color_green_layer
            font-size 0.7rem
            background-color $color_green_background_0
            margin-left 10px
          >.no-staff
            border 1px solid $color_theme
            color $color_theme
            background-color $color_red_background_0
        >.mark
          font-size 0.8rem
          color $color_theme
          margin-top 5px
        >.item
          @extend .flex-row
          @extend .flex-start
          width 100%
          margin-top 10px
          >p
            font-size 0.9rem
            line-height 1.2em
            color $color_gray_text
            &:nth-child(1)
              width 80px
              margin-right 10px
            &:nth-child(2)
              width calc(100% - 90px)
      >.footer
        @extend .flex-row
        @extend .flex-end
        width 100%
        >p
          @extend .flex-center
          background-color $color_gray_background_0
          height 30px
          border-radius 15px
          padding 0 15px
          font-size 0.9rem
          color $color_black_text_1

    .custom-cell
      @extend .flex-row
      @extend .flex-start
      width 100%
      padding 12px 2px
</style>



