<template>
  <transition name="fade">
    <div class="hs-container-child" v-if="show">
      <div class="header">
        <p @click="_gotoOrderList">{{$t('base.CheckOrder')}}</p>
        <p @click="_gotoHome">{{$t('base.Finish')}}</p>
      </div>
      <hs-scroll class="table-view">
        <ul>

          <div class="details-logo">
            <div class="logo" style="background-image: url('static/image/personal-center/about/logo.png')"></div>
            <div class="success" style="background-image: url('static/image/components/order-success/success.png')"></div>
            <p>{{$t('base.BookingSucceed')}}<template v-if="!noValue(JSON.stringify(XOrder.VettingProcessList))">({{$t('base.Pending')}})</template></p>
          </div>

          <div class="infor">

            <template v-if="OrderBusinessType === 1">
              <div class="item">
                <p class="title">{{$t('base.OrderNumber')}}</p>
                <p class="value">{{XOrder.TravelID}}</p>
              </div>
              <div class="item">
                <p class="title">{{$t('base.Segment')}}</p>
                <div class="segments">
                  <div class="segment">
                    <p>
                      {{XOrder.DepartObject[`AirPortName_${language}`]}}&nbsp;-&nbsp;{{XOrder.ArrivalObject[`AirPortName_${language}`]}}
                      {{$t(XOrder.GoSelectedFlight.DepartDate, 'date', ['MM月dd日', 'MM dd'])}}
                    </p>
                  </div>
                  <div class="segment" v-if="XOrder.BackSelectedFlight && XOrder.BackSelectedFlight.DepartDate">
                    <p>
                      {{XOrder.ArrivalObject[`AirPortName_${language}`]}}&nbsp;-&nbsp;{{XOrder.DepartObject[`AirPortName_${language}`]}}
                      {{$t(XOrder.BackSelectedFlight.DepartDate, 'date', ['MM月dd日', 'MM dd'])}}
                    </p>
                  </div>
                </div>
              </div>
              <div class="item">
                <p class="title">{{$t('base.Passenger')}}</p>
                <p class="value">{{XOrder.Passengers.map(v => v.Name).join('、')}}</p>
              </div>
            </template>

            <template v-if="OrderBusinessType === 6">
              <div class="item">
                <p class="title">{{$t('base.OrderNumber')}}</p>
                <div class="segments">
                  <div class="segment" v-for="(item, index) in XOrder.OrderIDs" :key="index">
                    <p>{{item}}</p>
                  </div>
                </div>
              </div>
              <div class="item">
                <p class="title">{{$t('base.Segment')}}</p>
                <div class="segments">
                  <div class="segment" v-for="(item, index) in XOrder.SearchParams.Segments" :key="index">
                    <p>
                      {{item.DepartObject[`AirPortName_${language}`]}}&nbsp;-&nbsp;{{item.ArrivalObject[`AirPortName_${language}`]}}
                      {{$t(item.DepartureTime, 'date', ['MM月dd日', 'MM dd'])}}
                    </p>
                  </div>
                </div>
              </div>
              <div class="item">
                <p class="title">{{$t('base.Passenger')}}</p>
                <p class="value">{{XOrder.Passengers.map(v => v.Name).join('、')}}</p>
              </div>
            </template>

            <template v-if="OrderBusinessType === 2">
              <!-- <div class="item">
                <p class="title">{{$t('base.OrderNumber')}}</p>
                <p class="value">{{XOrder.OrderID}}</p>
              </div> -->
              <div class="item">
                <p class="title">{{$t('base.Hotel')}}</p>
                <p class="value">{{XOrder.Hotel.HotelName}}</p>
              </div>
              <div class="item">
                <p class="title">{{$t('base.Hoomer')}}</p>
                <p class="value">{{XOrder.Guests.map(v => v.Name).join('、')}}</p>
              </div>
            </template>

            <template v-if="OrderBusinessType === 11">
              <!-- <div class="item">
                <p class="title">{{$t('base.OrderNumber')}}</p>
                <p class="value">{{XOrder.OrderID}}</p>
              </div> -->
              <div class="item">
                <p class="title">{{$t('base.Hotel')}}</p>
                <p class="value" v-if="language === 'CN'">{{XOrder.Hotel.NameChn}}</p>
                <p class="value" v-if="language === 'EN'">{{XOrder.Hotel.NameEng}}</p>
              </div>
              <div class="item">
                <p class="title">{{$t('base.Hoomer')}}</p>
                <p class="value">{{XOrder.Guests.map(v => v.Name).join('、')}}</p>
              </div>
            </template>

            <template v-if="OrderBusinessType === 10">
              <!-- <div class="item">
                <p class="title">{{$t('base.OrderNumber')}}</p>
                <p class="value">{{XOrder.Ids}}</p>
              </div> -->
              <div class="item">
                <p class="title">{{$t('base.Segment')}}</p>
                <div class="segments">
                  <div class="segment">
                    <p>
                      {{XOrder.DepartObject[`CityName_${language}`]}}&nbsp;-&nbsp;{{XOrder.ArrivalObject[`CityName_${language}`]}}
                      {{$t(beTime(XOrder.GoSelectedRoute.DepartureDateTime), 'date', ['MM月dd日', 'MM dd'])}}
                    </p>
                  </div>
                  <div class="segment" v-if="XOrder.BackSelectedRoute && XOrder.BackSelectedRoute.DepartureDateTime">
                    <p>
                      {{XOrder.ArrivalObject[`CityName_${language}`]}}&nbsp;-&nbsp;{{XOrder.DepartObject[`CityName_${language}`]}}
                      {{$t(beTime(XOrder.BackSelectedRoute.DepartureDateTime), 'date', ['MM月dd日', 'MM dd'])}}
                    </p>
                  </div>
                </div>
              </div>
              <div class="item">
                <p class="title">{{$t('base.Passenger')}}</p>
                <p class="value">{{XOrder.Passengers.map(v => v.Name).join('、')}}</p>
              </div>
            </template>
          </div>
          <div class="gift">
            <hs-swiper
              style="width: 100%;margin-top: 20px;"
              maxType="Width"
              :images="banners.map(v => v.PicUrl)"></hs-swiper>
            <!-- <img style="width: 100%;margin-top: 20px;" src="static/image/components/order-success/banner.jpg"> -->
          </div>
          <!-- <div class="gift">
            <div class="header">
              <p>线上下单有奖励</p>
              <p>领取奖励</p>
            </div>
            <div class="details">
              <div class="item">
                <p>现金红包</p>
                <p>1个</p>
                <div class="btn">选我</div>
              </div>
            </div>
          </div> -->
        </ul>
      </hs-scroll>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSwiper from 'base/hs-swiper/hs-swiper'
import {kpiAdList} from 'api'
export default {
  props: {
    XOrder: {
      type: Object,
      default: function() {
        return {}
      }
    },
    // 订单业务类型(国内机票:1 酒店:2 旅游:3 保险:5 国际机票:6 火车票:10 退票:N+100)
    OrderBusinessType: {
      type: Number,
      default: 1
    }
  },
  data() {
    return {
      show: false,
      banners: []
    }
  },
  watch: {
    show(val) {
      if (val) {
        this._getBannners()
      }
    }
  },
  methods: {
    open() {
      this.show = true
    },
    close() {
      this.show = false
    },

    /**
     * 获取轮播图
     */
    _getBannners() {
      let params = {
        adType: 6
      }
      kpiAdList(params).then((res) => {
        console.log('首页广告: ', res)
        this.banners = res.ResultData
      })
    },

    /** 查看详情 */
    _gotoOrderList() {
      if (this.OrderBusinessType === 1) {
        this.$router.replace({name: 'planeTicketOrderList', params: {TravelType: Number(this.XOrder.TravelType)}})
      } else if (this.OrderBusinessType === 6) {
        this.$router.replace({name: 'intlPlaneTicketOrderList', params: {TravelType: Number(this.XOrder.TravelType)}})
      } else if (this.OrderBusinessType === 2) {
        this.$router.replace({name: 'hotelOrderList', params: {TravelType: Number(this.XOrder.TravelType)}})
      } else if (this.OrderBusinessType === 11) {
        this.$router.replace({name: 'interHotelOrderList', params: {TravelType: Number(this.XOrder.TravelType)}})
      } else if (this.OrderBusinessType === 10) {
        this.$router.push({name: 'TrainOrderInforList', params: {TravelType: this.XOrder.TravelType}})
      }
    },

    /** 完成返回首页 */
    _gotoHome() {
      this.$router.replace('/home')
    }
  },
  components: {
    HsScroll,
    HsSwiper
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container-child
  position absolute
  width 100%
  height 100%
  left 0
  top 0
  bottom 0
  background-color $color_white
  z-index 200
  >.header
    @extend .flex-row
    @extend .flex-between
    width 100%
    height 45px
    padding 0 15px
    color $color_theme

  >.table-view
    width 100%
    height calc(100% - 45px)

    .details-logo
      @extend .flex-column
      @extend .flex-start
      width 100%
      padding 10px 0
      >.logo
        width 80px
        height 40px
        background-size 100% 100%
        margin 20px 0
      >.success
        width 60px
        height 60px
        background-size 100% 100%
        margin-bottom 20px
      >p
        font-weight bold
        color $color_theme

    .infor
      width 100%
      margin-top 20px
      >.item
        @extend .flex-row
        @extend .flex-between
        @extend .flex-top
        width 100%
        padding 5px 15px
        >.title
          color $color_gray_text
          line-height 1.2em
        >.value
          line-height 1.2em
          font-weight bold
          width calc(100% - 100px)
          text-align right
        >.segments
          >.segment
            margin-bottom 10px
            >.airline
              @extend .flex-row
              @extend .flex-start
              >.logo
                width 15px
                height 15px
                background-size 100% 100%
                margin-right 5px
            p
              line-height 1.2em
              text-align right
              &:nth-child(1)
                font-weight bold
            &:last-child
              margin-bottom 0

    .gift
      width 100%
      padding 20px 15px
      >.header
        @extend .flex-column
        @extend .flex-start
        border-top 1px solid $color_gray_layer
        padding-top 20px
        >p
          margin-bottom 20px
          &:nth-child(1)
            font-size 0.8rem
            color $color_gray_text
          &:nth-child(2)
            color $color_theme
            font-weight bold
      >.details
        @extend .flex-row
        @extend .flex-center
        width 100%
        >.item
          @extend .flex-column
          @extend .flex-start
          flex 1
          background-color $color_gray_background_0
          border-radius 3px
          padding 20px 0
          >p
            margin-bottom 15px
            font-weight bold
          >.btn
            background-color $color_theme
            border-radius 2px
            font-size 0.8rem
            color $color_white
            padding 5px 10px
</style>



