<template>
  <transition name="slide">
    <div class="hs-container-child" v-if="show">
      <c-header :title="powerDetails.title" leftIcon1="return.png" :leftFn1="close">
        <div slot="right" class="c-header-right">
          <p @click="_localCheck">{{$t('base.Confirm')}}</p>
        </div>
      </c-header>

      <hs-scroll
        class="table-view"
        :data="[details]">
        <ul>
          <hs-section>
            <div class="nameSwitch" v-if="powerDetails.showDefaultNameType" @click="currentDetails.DefaultNameType = currentDetails.DefaultNameType === 0 ? 1 : 0">
              <div :style="{transform: `translateX(${currentDetails.DefaultNameType === 0 ? '0px' : '20px'})`}"><p>{{currentDetails.DefaultNameType === 0 ? $t('base.CN') : $t('base.EN')}}</p></div>
            </div>

            <hs-cell-small-title v-if="currentDetails.DefaultNameType === 0 || OrderBusinessType === 0"
              title="中文姓名"
              v-model="currentDetails.ChName"
              :type="details.ChName ? 'bold' : 'input'"
              :placeholder="`${$t('base.PleaseFill')}`"
              :showLogo="false"
              :warning="currentDetails.DefaultNameType === 0"
              fixedLanguage="CN"
              @header="_openNameExplain">
            </hs-cell-small-title>

            <template v-if="currentDetails.DefaultNameType === 1 || OrderBusinessType === 0">
              <hs-cell-small-title
                title="英文姓"
                v-model="currentDetails.FirstName"
                :type="details.FirstName ? 'bold' : 'input'"
                placeholder="Surname, 如LI"
                :showLogo="false"
                :warning="currentDetails.DefaultNameType === 1"
                fixedLanguage="CN"
                @header="_openNameExplain">
              </hs-cell-small-title>

              <hs-cell-small-title
                title="英文名"
                v-model="currentDetails.LastName"
                :type="details.LastName ? 'bold' : 'input'"
                placeholder="Given names, 如SHIMING"
                fixedLanguage="CN"
                :showLogo="false">
              </hs-cell-small-title>
            </template>

            <template v-if="powerDetails.showCredential">
              <hs-cell-small-title @click.native="credentialsPicker.show()"
                :title="$t('base.IDType')"
                v-model="credential.CredentialName"
                type="disabled-input"
                :placeholder="`(${$t('base.RequiredChoose')}) ${$t('base.PleaseChoose')}`"
                fixedLanguage="CN">
              </hs-cell-small-title>

              <hs-cell-small-title
                :title="$t('base.IDNumber')"
                v-model="credential.CredentialNo"
                type="input"
                :placeholder="
                  `(${powerDetails.requiredCredential ?
                  $t('base.RequiredFill') :
                  $t('base.NotNecessaryFill')}) ${$t('base.PleaseFill')}`
                "
                :showLogo="false"
                fixedLanguage="CN">
              </hs-cell-small-title>

              <hs-cell-small-title v-if="powerDetails.showEffectiveDate && credential.CredentialType !== 1" @click.native="effectiveDatePicker.show()"
                :title="$t('base.EffectiveDate')"
                v-model="credential.EffectiveDate"
                type="disabled-input"
                :placeholder="
                  `(${powerDetails.requiredEffectiveDate ?
                  $t('base.RequiredFill') :
                  $t('base.NotNecessaryFill')}) ${$t('base.PleaseFill')}`
                "
                fixedLanguage="CN">
              </hs-cell-small-title>
            </template>
          </hs-section>

          <hs-section>
            <hs-cell-small-title v-if="powerDetails.showNation" @click.native="showNation = true"
              :title="$t('base.Nation')"
              :value="currentDetails.Nation ? currentDetails.Nation.Name : ''"
              type="disabled-input"
              :placeholder="`(${powerDetails.requiredNation ? $t('base.RequiredChoose') : $t('base.NotNecessaryChoose')}) ${$t('base.PleaseChoose')}`"
              fixedLanguage="CN">
            </hs-cell-small-title>

            <hs-cell-small-title v-if="powerDetails.showBirthday" @click.native="birthdayPicker.show()"
              :title="$t('base.DateOfBirth')"
              :value="currentDetails.Birthday"
              type="disabled-input"
              :placeholder="`(${powerDetails.requiredBirthday ? $t('base.RequiredChoose') : $t('base.NotNecessaryChoose')}) ${$t('base.PleaseChoose')}`"
              fixedLanguage="CN">
            </hs-cell-small-title>

            <hs-cell-small-title v-if="powerDetails.showGender"
              :title="$t('base.Gender')"
              :showLogo="false"
              fixedLanguage="CN">
              <div class="custom-cell-gender">
                <hs-checkbox :value="currentDetails.Gender" :title="$t('base.Male')" style="margin-right: 30px" @click.native="currentDetails.Gender = true"></hs-checkbox>
                <hs-checkbox :value="!currentDetails.Gender" :title="$t('base.Female')" @click.native="currentDetails.Gender = false"></hs-checkbox>
              </div>
            </hs-cell-small-title>

            <hs-cell-small-title
              :title="$t('base.CellphoneNumber')"
              v-model="currentDetails.Mobile"
              type="input"
              :placeholder="`(${powerDetails.requiredMobile ? $t('base.RequiredFill') : $t('base.NotNecessaryFill')}) ${$t('base.PleaseFill')}`"
              :showLogo="false"
              fixedLanguage="CN">
            </hs-cell-small-title>

            <hs-cell-small-title
              :title="$t('base.Email')"
              v-model="currentDetails.Email"
              type="input"
              :placeholder="
                `(${(currentDetails.IsSendBookEmail || currentDetails.IsSendIssuedEmail || currentDetails.IsSendConfirmEmail) ?
                $t('base.RequiredFill') :
                $t('base.NotNecessaryFill')}) ${$t('base.PleaseFill')}`
              "
              :showLogo="false"
              fixedLanguage="CN">
            </hs-cell-small-title>
          </hs-section>

          <hs-section v-if="Number(currentDetails.TravelType) === 0">
            <hs-cell-small-title v-if="currentDetails.index >= 0"
              title="旅客类型"
              :value="currentDetails.UpType === 1 ? '员工' : '非员工'"
              type="disabled-input"
              :showLogo="false"
              fixedLanguage="CN">
            </hs-cell-small-title>
            <hs-cell-1 v-else
              :showLogo="false">
              <div class="custom-cell-staff">
                <p>设为员工</p>
                <hs-switch :value="currentDetails.UpType === 1" @input="_setUpType(arguments[0])"></hs-switch>
              </div>
            </hs-cell-1>
            <hs-cell-small-title v-if="powerDetails.IsDisplayCostCenter" @click.native="powerDetails.EnableEditCostCenter && $refs.CostCenters.open()"
              :title="$t('base.CostCenter')"
              :value="currentDetails.CostCenter ? currentDetails.CostCenter.Name : ''"
              :type="powerDetails.EnableEditCostCenter ? 'disabled-input' : 'bold'"
              :placeholder="
                `(${powerDetails.IsRequiredCostCenter?
                $t('base.RequiredChoose') :
                $t('base.NotNecessaryChoose')}) ${$t('base.PleaseChoose')}`
              "
              :showLogo="powerDetails.EnableEditCostCenter"
              fixedLanguage="CN">
            </hs-cell-small-title>
            <template v-if="currentDetails.UpType === 1">
              <hs-cell-small-title v-if="powerDetails.IsDisplayDepartment" @click.native="powerDetails.EnableEditDepartment && $refs.Departments.open()"
                :title="$t('base.Department')"
                :value="currentDetails.Department ? currentDetails.Department.Name : ''"
                :type="powerDetails.EnableEditDepartment ? 'disabled-input' : 'bold'"
                :placeholder="
                  `(${powerDetails.IsRequiedDepartment?
                  $t('base.RequiredChoose') :
                  $t('base.NotNecessaryChoose')}) ${$t('base.PleaseChoose')}`
                "
                :showLogo="powerDetails.EnableEditDepartment"
                fixedLanguage="CN">
              </hs-cell-small-title>
              <hs-cell-small-title v-if="powerDetails.IsDisplayBusinessUnit" @click.native="powerDetails.EnableEditBusinessUnit && $refs.BusinessUnit.open()"
                :title="powerDetails.BusinessUnitName"
                :value="currentDetails.BusinessUnit ? currentDetails.BusinessUnit.Name : ''"
                :type="powerDetails.EnableEditBusinessUnit ? 'disabled-input' : 'bold'"
                :placeholder="
                  `(${powerDetails.IsRequiredBusinessUnit?
                  $t('base.RequiredChoose') :
                  $t('base.NotNecessaryChoose')}) ${$t('base.PleaseChoose')}`
                "
                :showLogo="powerDetails.EnableEditBusinessUnit"
                fixedLanguage="CN">
              </hs-cell-small-title>
            </template>
          </hs-section>

          <hs-section v-if="powerDetails.showCard">
            <hs-cell-1 @click.native="$refs.Card.open()">
              <div class="custom-cell-card">
                <p>{{$t('base.TravelCard')}}</p>
              </div>
            </hs-cell-1>
          </hs-section>

          <hs-section v-if="OrderBusinessType === 0 && currentDetails.index >= 0">
            <hs-cell-1 :showLogo="false" @click.native="_delete">
              <div class="custom-cell-delete">
                <p>移除该常旅客</p>
              </div>
            </hs-cell-1>
          </hs-section>

          <!-- 通知 -->
          <hs-section v-if="OrderBusinessType !== 0">
            <hs-cell-3 :title="$t('base.Notice')" :showLogo="false" titleColor="#333333" @click.native="showDetailsNotice = !showDetailsNotice">
              <div class="notice-header">
                <div class="logo" :style="{transform: showDetailsNotice ? 'rotate(90deg)' : 'rotate(-90deg)'}" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
              </div>
            </hs-cell-3>
            <template v-if="showDetailsNotice">
              <template v-if="OrderBusinessType === 1 || OrderBusinessType === 6">
                <hs-cell-1 :showLogo="false">
                  <div class="notice-cell">
                    <p>{{$t('base.BookingSMS')}}</p>
                    <hs-switch v-model="currentDetails.IsSendBookSms"></hs-switch>
                  </div>
                </hs-cell-1>
                <hs-cell-1 :showLogo="false">
                  <div class="notice-cell">
                    <p>{{$t('base.TicketingSMS')}}</p>
                    <hs-switch v-model="currentDetails.IsSendIssuedSms"></hs-switch>
                  </div>
                </hs-cell-1>
                <hs-cell-1 :showLogo="false">
                  <div class="notice-cell">
                    <p>{{$t('base.BookingMail')}}</p>
                    <hs-switch v-model="currentDetails.IsSendBookEmail"></hs-switch>
                  </div>
                </hs-cell-1>
                <hs-cell-1 :showLogo="false">
                  <div class="notice-cell">
                    <p>{{$t('base.TicketingMail')}}</p>
                    <hs-switch v-model="currentDetails.IsSendIssuedEmail"></hs-switch>
                  </div>
                </hs-cell-1>
              </template>
              <template v-else-if="OrderBusinessType === 2 || OrderBusinessType === 11" >
                <hs-cell-1 :showLogo="false">
                  <div class="notice-cell">
                    <p>{{$t('base.ConfirmSMS')}}</p>
                    <hs-switch v-model="currentDetails.IsSendConfirmSms"></hs-switch>
                  </div>
                </hs-cell-1>
                <hs-cell-1 :showLogo="false">
                  <div class="notice-cell">
                    <p>{{$t('base.ConfirmMail')}}</p>
                    <hs-switch v-model="currentDetails.IsSendConfirmEmail"></hs-switch>
                  </div>
                </hs-cell-1>
              </template>
              <template v-else-if="OrderBusinessType === 10">
                <hs-cell-1 :showLogo="false">
                  <div class="notice-cell">
                    <p>{{$t('base.TicketingSMS')}}</p>
                    <hs-switch v-model="currentDetails.IsSendIssuedSms"></hs-switch>
                  </div>
                </hs-cell-1>
                <hs-cell-1 :showLogo="false">
                  <div class="notice-cell">
                    <p>{{$t('base.TicketingMail')}}</p>
                    <hs-switch v-model="currentDetails.IsSendIssuedEmail"></hs-switch>
                  </div>
                </hs-cell-1>
              </template>
            </template>
          </hs-section>
        </ul>
      </hs-scroll>

      <!-- 国籍 -->
      <hs-nation v-model="showNation" @select="_selectNation"></hs-nation>

      <!-- 成本中心 -->
      <costCenters ref="CostCenters" :powerDetails="powerDetails" @select="_selectWithCostCenters"></costCenters>

      <!-- 部门 -->
      <departments ref="Departments" :powerDetails="powerDetails" @select="_selectWithDepartments"></departments>

      <!-- businessUnit -->
      <businessUnit ref="BusinessUnit" :powerDetails="powerDetails" @select="_selectWithBusinessUnit"></businessUnit>

      <!-- 常旅卡 -->
      <card ref="Card" v-if="currentDetails" :cardlist.sync="currentDetails.TravelCardList"></card>

      <!-- 姓名填写说明 -->
      <nameExplain ref="NameExplain"></nameExplain>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
import BusinessUnit from './businessUnit'
import Card from './card/card'
import CHeader from 'components/c-header/c-header'
import CostCenters from './costCenters'
import Departments from './departments'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsCell3 from 'base/hs-tableview/hs-cell-3/hs-cell-3'
import HsCellSmallTitle from 'base/hs-tableview/hs-cell-small-title/hs-cell-small-title'
import HsCheckbox from 'base/hs-checkbox/hs-checkbox'
import HsDialog from 'base/hs-dialog/hs-dialog'
import HsNation from 'base/hs-nation/hs-nation'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsSwitch from 'base/hs-switch/hs-switch'
import NameExplain from './nameExplain'
import {kpiFrequentTravelerDelete, kpiUserGetUserManageInfo} from 'api'
export default {
  props: {
    details: {
      type: Object,
      default: function () {
        return {}
      }
    },
    BookInit: {
      type: Object,
      default: function() {
        return {}
      }
    },
    OrderBusinessType: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      show: false,
      currentDetails: {},
      /** 证件 */
      // credential: {
      //   CredentialName: '',
      //   CredentialType: '',
      //   CredentialNo: '',
      //   EffectiveDate: ''
      // },
      credential: null,
      showNation: false,
      /** 通知 */
      showDetailsNotice: true,
      /** 各板块权限 */
      powerDetails: {
        title: '', // 标题
        showDefaultNameType: true, // 展示中英姓名切换

        showCredential: true, // 证件_展示
        requiredCredential: false, // 证件_必填

        showEffectiveDate: false, // 证件有效期_展示
        requiredEffectiveDate: false, // 证件有效期_必填

        showNation: false, // 国籍_展示
        requiredNation: false, // 国籍_必填

        showBirthday: false, // 出生日期_展示
        requiredBirthday: false, // 出生日期_必填

        showGender: false, // 性别_展示

        requiredMobile: true, // 手机号_必填

        showCard: false, // 常旅客卡_展示

        IsDisplayCostCenter: false, // 成本中心_展示
        IsAllowAddCostCenter: true, // 成本中心_添加
        IsRequiredCostCenter: false, // 成本中心_必填
        EnableEditCostCenter: true, // 成本中心_编辑

        IsDisplayDepartment: false, // 部门_展示
        IsRequiredDepartment: false, // 部门_必填
        EnableEditDepartment: true, // 部门_编辑
        // IsRequiedDepartment: true, // 当前接口的字段名，缺字母r

        IsDisplayBusinessUnit: false, // BU_展示
        IsRequiredBusinessUnit: false, // BU_必填
        EnableEditBusinessUnit: true, // BU_编辑
        BusinessUnitName: '' // BU名称
      },
      /** 常旅客管理权限 */
      userManageInfo: undefined
    }
  },
  watch: {
    show(val) {
      if (val) {
        this.currentDetails = JSON.parse(JSON.stringify(this.details))
        if (this.details.Credentials.filter(v => v.CredentialType === this.details.DefaultCredentialType)[0]) {
          this.credential = this.details.Credentials.filter(v => v.CredentialType === this.details.DefaultCredentialType)[0]
        }

        if (this.OrderBusinessType === 0) {
          this._getUserManageInfo()
        } else if (this.BookInit) {
          this.powerDetails.IsDisplayCostCenter = this.BookInit.IsDisplayCostCenter // 成本中心_展示
          this.powerDetails.IsAllowAddCostCenter = this.BookInit.IsAllowAddCostCenter // 成本中心_添加
          this.powerDetails.IsRequiredCostCenter = this.BookInit.IsRequiredCostCenter // 成本中心_必填
          this.powerDetails.EnableEditCostCenter = this.BookInit.EnableEditCostCenter // 成本中心_编辑
          this.powerDetails.IsDisplayDepartment = this.BookInit.IsDisplayDepartment // 部门_展示
          this.powerDetails.IsRequiredDepartment = this.BookInit.IsRequiredDepartment // 部门_必填
          this.powerDetails.EnableEditDepartment = this.BookInit.EnableEditDepartment // 部门_编辑
          this.powerDetails.IsDisplayBusinessUnit = this.BookInit.IsDisplayBusinessUnit // BU_展示
          this.powerDetails.IsRequiredBusinessUnit = this.BookInit.IsRequiredBusinessUnit // BU_必填
          this.powerDetails.EnableEditBusinessUnit = this.BookInit.EnableEditBusinessUnit // BU_编辑
          this.powerDetails.BusinessUnitName = this.BookInit.BusinessUnitName // BU名称
        }

        this._initPowerDetails()

        this._initPicker()
      }
    },
    'credential.CredentialNo': {
      handler(val) {
        // console.log(val)
        this._setBirthdayAndGender(val)
      },
      deep: true
    }
  },
  methods: {
    open() {
      this.show = true
    },
    close() {
      this.show = false
    },

    /** 获取成本中心，部门，BU权限 */
    _getUserManageInfo() {
      kpiUserGetUserManageInfo({}).then((res) => {
        console.log('获取成本中心，部门，BU权限: ', res)
        this.userManageInfo = res.ResultData
        this.powerDetails.IsDisplayCostCenter = this.userManageInfo.TravelerManageConfig.IsDisplayCostCenter
        this.powerDetails.IsDisplayDepartment = this.userManageInfo.TravelerManageConfig.IsDisplayDepartment
        this.powerDetails.IsDisplayBusinessUnit = this.userManageInfo.TravelerManageConfig.IsDisplayBusinessUnit
        this.powerDetails.BusinessUnitName = this.userManageInfo.TravelerManageConfig.BusinessUnitName
      })
    },

    /** 根据板块初始化页面权限 */
    _initPowerDetails() {
      // 常旅客
      if (this.OrderBusinessType === 0) {
        if (this.currentDetails.index >= 0) {
          this.powerDetails.title = '常旅客详情'
        } else if (this.currentDetails.TravelType === 0) {
          this.powerDetails.title = '常旅客添加(因公)'
        } else if (this.currentDetails.TravelType === 1) {
          this.powerDetails.title = '常旅客添加(因私)'
        }
        this.powerDetails.showEffectiveDate = true
        this.powerDetails.showDefaultNameType = false
        this.powerDetails.showNation = true
        this.powerDetails.showBirthday = true
        this.powerDetails.showGender = true
        this.powerDetails.showCard = true
      // 国内机票
      } else if (this.OrderBusinessType === 1) {
        this.powerDetails.title = this.$t('base.Passenger')
        this.powerDetails.showEffectiveDate = true
        this.powerDetails.requiredCredential = true
        this.powerDetails.showBirthday = true
        this.powerDetails.showGender = true
        this.powerDetails.requiredBirthday = true
        this.powerDetails.showCard = true
      // 国际机票
      } else if (this.OrderBusinessType === 6) {
        this.powerDetails.title = this.$t('base.Passenger')
        this.powerDetails.showDefaultNameType = false
        this.powerDetails.requiredCredential = true
        this.powerDetails.showEffectiveDate = true
        this.powerDetails.requiredEffectiveDate = true
        this.powerDetails.showNation = true
        this.powerDetails.requiredNation = true
        this.powerDetails.showBirthday = true
        this.powerDetails.showGender = true
        this.powerDetails.requiredBirthday = true
        this.powerDetails.showCard = true
      // 国内酒店
      } else if (this.OrderBusinessType === 2) {
        this.powerDetails.title = this.$t('base.Roomer')
        this.powerDetails.showCredential = false
      // 国际酒店
      } else if (this.OrderBusinessType === 11) {
        this.powerDetails.title = this.$t('base.Roomer')
        this.powerDetails.showDefaultNameType = false
        this.powerDetails.showCredential = false
      // 火车票
      } else if (this.OrderBusinessType === 10) {
        this.powerDetails.title = this.$t('base.Passenger')
        this.powerDetails.requiredCredential = true
      }
    },

    /** 初始化picker */
    _initPicker() {
      // 证件类型选择器
      this.credentialsPicker = this.$createPicker({
        title: this.$t('base.IDType'),
        data: [this.details.Credentials.map(v => {
          return {
            text: v.CredentialName,
            value: v.CredentialNo
          }
        })],
        cancelTxt: this.$t('base.Cancel'),
        confirmTxt: this.$t('base.Confirm'),
        onSelect: (val, index, text) => {
          this.credential = this.details.Credentials[index[0]]
        }
      })

      // 证件有效期选择器
      if (this.powerDetails.showEffectiveDate) {
        this.effectiveDatePicker = this.$createDatePicker({
          title: this.$t('base.EffectiveDate'),
          min: new Date(),
          max: new Date(this.getNewDate(this.beTime(new Date()), 100, 0, 0)),
          value: (this.credential.EffectiveDate && this.credential.EffectiveDate !== '1900-01-01') ? new Date(this.credential.EffectiveDate) : new Date(this.getNewDate(this.beTime(new Date()), 3, 0, 0)),
          cancelTxt: this.$t('base.Cancel'),
          confirmTxt: this.$t('base.Confirm'),
          onSelect: (date, selectedVal, selectedText) => {
            this.credential.EffectiveDate = this.beTime(Date.parse(date))
            this.$forceUpdate()
          }
        })
      }

      // 出生日期选择器
      this.birthdayPicker = this.$createDatePicker({
        title: this.$t('base.DateOfBirth'),
        min: new Date(this.getNewDate(this.beTime(new Date()), -100, 0, 0)),
        max: new Date(),
        value: (this.currentDetails.Birthday && this.currentDetails.Birthday !== '1900-01-01') ? new Date(this.currentDetails.Birthday) : new Date(this.getNewDate(this.beTime(new Date()), -30, 0, 0)),
        cancelTxt: this.$t('base.Cancel'),
        confirmTxt: this.$t('base.Confirm'),
        onSelect: (date, selectedVal, selectedText) => {
          this.currentDetails.Birthday = this.beTime(Date.parse(date))
          this.$forceUpdate()
        }
      })
    },

    /** 通过身份证设置生日和性别 */
    _setBirthdayAndGender(val) {
      if (this.credential.CredentialType === 1) {
        if (this.idCard(val).valid) {
          // 利用出生日期选择器的回调方法设置出生日期
          this.currentDetails.Birthday = this.beTime(Date.parse(this.calBirthDate(val)))
          this.$forceUpdate()
          // 设置性别
          this.currentDetails.Gender = this.calGender(val)
        }
      }
    },

    /** 设置员工类型 */
    _setUpType(val) {
      console.log('设置员工类型: ', val)
      this.currentDetails.UpType = val ? 1 : 2
    },

    /** 选择成本中心回调 */
    _selectWithCostCenters(item) {
      this.currentDetails.CostCenter = item
    },

    /** 选择国籍回调 */
    _selectNation(item) {
      this.currentDetails.Nation = {
        ID: item.Id,
        Name: item[`NationName_${this.language}`],
        Code: item.NationCode
      }
    },

    /** 选择部门回调 */
    _selectWithDepartments(item) {
      this.currentDetails.Department = item
    },

    /** 选择BusinessUnit回调 */
    _selectWithBusinessUnit(item) {
      this.currentDetails.BusinessUnit = item
    },

    /** 本地验证 */
    _localCheck() {
      // 1. 中英文姓名必填一个
      if (
        this.OrderBusinessType === 0 &&
        this.noValue(this.currentDetails.ChName) &&
        (this.noValue(this.currentDetails.FirstName) || this.noValue(this.currentDetails.LastName))
      ) {
        this.$toast('请完善您的姓名信息')
        return
      } else if (this.OrderBusinessType !== 0 && this.currentDetails.DefaultNameType === 0 && this.noValue(this.currentDetails.ChName)) {
        this.$toast('请完善您的姓名信息')
        return
      } else if (this.OrderBusinessType !== 0 && this.currentDetails.DefaultNameType === 1 && (this.noValue(this.currentDetails.FirstName) || this.noValue(this.currentDetails.LastName))) {
        this.$toast('请完善您的姓名信息')
        return

      // 2. 英文姓英文名需同时填写
      } else if (this.currentDetails.LastName && this.noValue(this.currentDetails.FirstName)) {
        this.$toast('请完善您的英文姓')
        return
      } else if (this.currentDetails.FirstName && this.noValue(this.currentDetails.LastName)) {
        this.$toast('请完善您的英文名')
        return

      // 3. 保存的证件号必填
      } else if (this.powerDetails.requiredCredential && this.noValue(this.credential.CredentialNo)) {
        this.$toast(this.$t('TM.PleaseFill', 'function', this.$t('base.IDNumber')))
        return

      // 4. 校验保存的证件号格式
      } else if (this.powerDetails.requiredCredential && !this.noValue(this.credential.CredentialNo) && this.credential.CredentialType === 1 && !this.idCard(this.credential.CredentialNo).valid) {
        this.$toast(this.idCard(this.credential.CredentialNo).msg)
        return
      // eslint-disable-next-line
      } else if (this.powerDetails.requiredCredential && !this.noValue(this.credential.CredentialNo) && this.credential.CredentialNo.match(/[^a-zA-Z-0-9-\/\ ]/g) !== null) {
        this.$toast(this.$t('TM.XFormatIsIncorrect', 'function', this.$t('base.IDNumber')))
        return

      // 5. 国际机票, 除身份证, 其余证件有效期必填
      } else if (this.powerDetails.requiredEffectiveDate && this.credential.CredentialType !== 1 && this.noValue(this.credential.EffectiveDate)) {
        this.$toast(this.$t('TM.PleaseFill', 'function', this.$t('base.EffectiveDate')))
        return

      // 6. 国际机票国籍必填
      } else if (this.powerDetails.requiredNation && (!this.currentDetails.Nation || !this.currentDetails.Nation.Name)) {
        this.$toast(this.$t('TM.PleaseSelect', 'function', this.$t('base.Nation')))
        return

      // 7. 生日必填
      } else if (this.powerDetails.requiredBirthday && this.noValue(this.currentDetails.Birthday)) {
        this.$toast(this.$t('TM.PleaseFill', 'function', this.$t('base.DateOfBirth')))
        return

      // 8. 业务板块手机号必填
      } else if (this.powerDetails.requiredMobile && this.noValue(this.currentDetails.Mobile)) {
        this.$toast(this.$t('TM.PleaseFill', 'function', this.$t('base.CellphoneNumber')))
        return

      // 9. 校验手机号格式
      } else if (!this.noValue(this.currentDetails.Mobile) && !this.isPhoneNumber(this.currentDetails.Mobile)) {
        this.$toast(this.$t('TM.XFormatIsIncorrect', 'function', this.$t('base.CellphoneNumber')))
        return

      // 10. 校验邮箱必填
      } else if (this.noValue(this.currentDetails.Email) && (this.currentDetails.IsSendBookEmail || this.currentDetails.IsSendIssuedEmail || this.currentDetails.IsSendConfirmEmail)) {
        this.$toast(this.$t('TM.PleaseFill', 'function', this.$t('base.Email')))
        return

      // 11. 校验邮箱格式
      } else if (!this.noValue(this.currentDetails.Email) && !this.isEmail(this.currentDetails.Email)) {
        this.$toast(this.$t('TM.XFormatIsIncorrect', 'function', this.$t('base.Email')))
        return

      // 12. 成本中心
      } else if (this.powerDetails.IsRequiredCostCenter && (!this.currentDetails.CostCenter || !this.currentDetails.CostCenter.Name)) {
        this.$toast(this.$t('TM.PleaseSelect', 'function', this.$t('base.CostCenter')))
        return

      // 13. 检查部门
      } else if (this.powerDetails.IsRequiedDepartment && this.currentDetails.UpType === 1 && (!this.currentDetails.Department || !this.currentDetails.Department.Name)) {
        this.$toast(this.$t('TM.PleaseSelect', 'function', this.$t('base.Department')))
        return

      // 14. 检查BU
      } else if (this.powerDetails.IsRequiredBusinessUnit && this.currentDetails.UpType === 1 && (!this.currentDetails.BusinessUnit || !this.currentDetails.BusinessUnit.Name)) {
        this.$toast(this.$t('TM.PleaseSelect', 'function', this.powerDetails.BusinessUnitName))
        return
      }

      this._confirm()
    },

    /** 确定 */
    _confirm() {
      // 组装证件信息
      if (this.credential && this.credential.CredentialType) {
        let index = this.currentDetails.Credentials.map(v => v.CredentialType).indexOf(this.credential.CredentialType)
        this.currentDetails.Credentials.splice(index, 1, this.credential)
        this.currentDetails.DefaultCredentialType = this.credential.CredentialType
      }
      this.$emit('delegate', this.currentDetails)
      console.log('乘客: ', this.currentDetails)
      this.close()
    },

    /** 移除 */
    _delete() {
      this.$alert({
        content: '确定将其从常旅客列表中移除?',
        cancelTxt: this.$t('base.Cancel'),
        confirmFn: () => {
          let params = {
            TravelerID: this.currentDetails.TravelerID
          }
          kpiFrequentTravelerDelete(params).then((res) => {
            console.log('移除: ', res)
            if (res.ResultData) {
              this.$emit('delegateWithDelete', this.currentDetails.index)
              this.close()
            } else {
              this.$toast(this.$t('TM.RemovalFailedPleaseTryAgainLater'))
            }
          })
        }
      })
    },

    /** 姓名填写说明 */
    _openNameExplain() {
      this.$refs.NameExplain.open()
    }
  },
  components: {
    BusinessUnit,
    Card,
    CHeader,
    CostCenters,
    Departments,
    HsCell1,
    HsCell3,
    HsCellSmallTitle,
    HsCheckbox,
    HsDialog,
    HsNation,
    HsScroll,
    HsSection,
    HsSwitch,
    NameExplain
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container-child
  @extend .flex-column
  @extend .flex-start
  position absolute
  width 100%
  height 100%
  left 0
  top 0
  bottom 0
  background-color $color_gray_background_0
  z-index 200
  .c-header-right
    font-size 0.8rem

  .table-view
    width 100%
    height calc(100% - 45px)
    overflow hidden
    background-color $color_gray_background_0

    .nameSwitch
      @extend .flex-row
      @extend .flex-start
      width 50px
      height 30px
      border 1px solid $color_theme
      background-color $color_theme
      border-radius 15px
      position absolute
      top 7px
      right 15px
      >div
        @extend .flex-center
        width 28px
        height 28px
        border-radius 15px
        background-color $color_white
        transition all 400ms
        >p
          font-size 0.8rem
          color $color_theme

    // 员工 常旅卡
    .custom-cell-staff
    .custom-cell-card
      @extend .flex-row
      @extend .flex-between
      width 100%
      >p
        color $color_black_text_1

    // 性别
    .custom-cell-gender
      @extend .flex-row
      @extend .flex-start
      height 33px

    // 移除
    .custom-cell-delete
      @extend .flex-row
      @extend .flex-between
      width 100%
      >p
        color $color_theme

    .notice-header
      @extend .flex-row
      @extend .flex-end
      width 100%
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
        transition all 300ms
    .notice-cell
      @extend .flex-row
      @extend .flex-between
      width 100%
      font-size 1rem
      color $color_gray_text
</style>



