<template>
  <transition name="datepicker">
    <div class="hs-datepicker" v-if="show" @click.stop>
      <div class="header">
        <p @click="$emit('update:show', false)">{{$t('base.Cancel')}}</p>
        <p>{{$t('base.SelectDate')}}</p>
        <p></p>
      </div>
      <hs-scroll ref="tableView" class="tableView">
        <ul>
          <li class="cell" v-for="n in 12" :key="n" :id="`cell${_getMonthBarsMonth(n)}`">
            <div class="moutn-bar">{{$t(`${_getMonthBarsYear(n)}-${_getMonthBarsMonth(n)}`, 'date', ['yyyy年MM月', 'MM yyyy'])}}</div>
            <div class="week-bar">
              <div class="item" v-for="(item, index) in week" :key="index">{{item}}</div>
            </div>
            <div class="day-bar">
              <div class="disabledItem" style="visibility: hidden" v-for="number in _getPreMonthDay(_getMonthBarsYear(n), _getMonthBarsMonth(n))" :key="`pre${number}`">{{number}}</div>
              <div
                :class="[{weekendItem: _isWeekend(_getMonthBarsYear(n), _getMonthBarsMonth(n), number)},
                _isOverdue(_getMonthBarsYear(n), _getMonthBarsMonth(n), number) || (noValue(toDate) && _addZero(_getMonthBarsYear(n), _getMonthBarsMonth(n), number) > maxToData) ? 'disabledItem' : 'item',
                {thisItem: _isFromDate(_getMonthBarsYear(n), _getMonthBarsMonth(n), number) || _isToDate(_getMonthBarsYear(n), _getMonthBarsMonth(n), number)},
                {rangeItem: _isRangeDate(_getMonthBarsYear(n), _getMonthBarsMonth(n), number)}]"
                v-for="number in _getDayNumber(_getMonthBarsYear(n), _getMonthBarsMonth(n))" :key="`this${number}`"
                @click="_select(_getMonthBarsYear(n), _getMonthBarsMonth(n), number)">
                <!-- 节日 -->
                <span class="holiday-span no-wrap">{{_setHoliday(_getMonthBarsYear(n), _getMonthBarsMonth(n), number)}}</span>
                <div v-if="birthday === _addZero(_getMonthBarsYear(n), _getMonthBarsMonth(n), number)"
                  class="birthday" style="background-image: url('static/image/base/date-picker/birthday.png')"></div>
                <p v-else>{{_isToday(_getMonthBarsYear(n), _getMonthBarsMonth(n), number) ? $t('base.Today') : number}}</p>
                <span class="mark" v-if="_isFromDate(_getMonthBarsYear(n), _getMonthBarsMonth(n), number)">{{$t('base.CI')}}</span>
                <span class="mark" v-else-if="_isToDate(_getMonthBarsYear(n), _getMonthBarsMonth(n), number)">{{$t('base.CO')}}</span>
                <span class="mark" v-else>&nbsp;</span>
                <div v-if="_setRestDay(_getMonthBarsYear(n), _getMonthBarsMonth(n), number)"
                  class="rest">休</div>
                <div v-if="_setWorkDay(_getMonthBarsYear(n), _getMonthBarsMonth(n), number)"
                  class="work">班</div>
              </div>
              <div class="disabledItem" style="visibility: hidden" v-for="number in _getNextMonthDay(_getMonthBarsYear(n), _getMonthBarsMonth(n))" :key="`next${number}`">{{number}}</div>
            </div>
          </li>
        </ul>
      </hs-scroll>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
import Vue from 'vue'
import HsScroll from 'base/hs-scroll/hs-scroll'
// import setHoliday from './holiday.js'
export default {
  props: {
    show: {
      type: Boolean,
      default: false
    },
    fromDate: {
      type: String,
      default: Vue.prototype.beTime(new Date())
    },
    toDate: {
      type: String,
      default: Vue.prototype.beTime(new Date().setDate(new Date().getDate() + 1))
    },
    overdueDate: {
      type: String,
      default: Vue.prototype.beTime(new Date())
    }
  },
  computed: {
    currentFromDate: {
      get: function() {
        return this.fromDate
      },
      set: function(val) {
        return this.$emit('update:fromDate', val)
      }
    },
    currentToDate: {
      get: function() {
        return this.toDate
      },
      set: function(val) {
        return this.$emit('update:toDate', val)
      }
    },
    maxToData() {
      let res = this.beTime(new Date(this.fromDate).setDate(new Date(this.fromDate).getDate() + 28)) // 最多选择28晚
      return res
    }
  },
  watch: {
    show(val) {
      if (val) {
        /** 初始化读取节假日数据 */
        if (window.localStorage.CALENDAR) {
          let list = JSON.parse(window.localStorage.CALENDAR)

          // 节假日
          this.calendar = new Map(list.map((value, key) => [this.beTime(value.Date), value]))
          this.calendarRest = new Map(list.filter(v => v.DayType === 1).map((value, key) => [this.beTime(value.Date), value]))
          this.calendarWork = new Map(list.filter(v => v.DayType === 2).map((value, key) => [this.beTime(value.Date), value]))
          console.log('this.calendar: ', this.calendar)

          // 生日
          let birthday = list.filter(v => v.DayType === 3)
          this.birthday = (birthday && birthday[0]) ? `${this.beTime(new Date(), 'yyyy')}-${this.beTime(birthday[0].Date, 'MM-dd')}` : ''
          console.log('this.birthday: ', this.birthday)
        }

        this.historyFromDate = this.fromDate
        this.historyToDate = this.toDate
        setTimeout(() => {
          this.$refs.tableView && this.$refs.tableView.refresh()
          let res = new Date(this.fromDate).getMonth() + 1
          this.$refs.tableView.scrollToElement(`#cell${res}`, 300)
        }, 20)
      } else {
        if (this.noValue(this.currentToDate)) {
          this.currentFromDate = this.historyFromDate
          this.currentToDate = this.historyToDate
        }
      }
    }
  },
  data() {
    return {
      week: [this.$t('base.Week.Sun'), this.$t('base.Week.Mon'), this.$t('base.Week.Tues'), this.$t('base.Week.Wed'), this.$t('base.Week.Thur'), this.$t('base.Week.Fri'), this.$t('base.Week.Sat')],
      monthDay: [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31],
      thatYear: new Date().getFullYear(),
      thatMouth: new Date().getMonth(),
      nextYear: new Date().getFullYear() + 1,
      historyFromDate: '',
      historyToDate: '',
      calendar: new Map(), // 节假日数据
      calendarRest: new Map(), // 非正常休息日数据
      calendarWork: new Map(), // 非正常工作日数据
      birthday: ''
    }
  },
  methods: {
    /** 设置节日 */
    _setHoliday(year, month, day) {
      let key = this._addZero(year, month, day)
      let res = this.calendar.get(key)
      return res ? res.Title : ''
    },

    /** 设置工作日 */
    _setWorkDay(year, month, day) {
      let key = this._addZero(year, month, day)
      let res = this.calendarWork.get(key)
      return res
    },

    /** 设置休息日 */
    _setRestDay(year, month, day) {
      let key = this._addZero(year, month, day)
      let res = this.calendarRest.get(key)
      return res
    },

    // 获取某年某月一号的星期数值
    _getFirstDayWeek(year, month) {
      return new Date(year, month - 1, 1).getDay()
    },
    // 获取某年某月最后一天的星期数值
    _getLastDayWeek(year, month) {
      return new Date(year, month - 1, this._getDayNumber(year, month)).getDay()
    },
    // 是否为周末
    _isWeekend(year, month, day) {
      let res = new Date(year, month - 1, day).getDay()
      return res === 0 || res === 6
    },
    // 获取这个月bar中上个月的尾部天数数组
    _getPreMonthDay(year, month) {
      let preYear = year
      let preMonth = month
      if (month === 1) {
        preYear--
        preMonth = 12
      } else {
        preMonth--
      }
      let number = this._getDayNumber(preYear, preMonth)
      let length = this._getFirstDayWeek(year, month)
      let list = []
      for (let i = 0; i < length; i++) {
        list.push(number - (length - i) + 1)
      }
      return list
    },
    // 获取这个月bar中下个月的尾部天数数组
    _getNextMonthDay(year, month) {
      let length = 6 - this._getLastDayWeek(year, month)
      let list = []
      for (let i = 0; i < length; i++) {
        list.push(i + 1)
      }
      return list
    },
    // 根据年月，知道这个月有几天
    _getDayNumber(year, month) {
      return this._isLeapYeat(year) && month === 2 ? 29 : this.monthDay[month - 1]
    },
    // 获取年月栏的年数值
    _getMonthBarsYear(n) {
      return (this.thatMouth + n) < 13 ? this.thatYear : this.nextYear
    },
    // 获取年月栏的月数值
    _getMonthBarsMonth(n) {
      return (this.thatMouth + n) < 13 ? (this.thatMouth + n) : (this.thatMouth + n - 12)
    },
    // 是否为闰年
    _isLeapYeat(year) {
      return (year % 100 == 0 ? (year % 400 == 0 ? true : false) : (year % 4 == 0 ? year : false)) // eslint-disable-line
    },
    // 日期是否已经过期
    _isOverdue(year, month, day) {
      let that = this._addZero(year, month, day)
      return that < this.overdueDate
    },
    // 是否为选择的入住日期,用于高亮css
    _isFromDate(year, month, day) {
      let res = this._addZero(year, month, day)
      return res === this.currentFromDate
    },
    // 是否为选择的离店日期,用于高亮css
    _isToDate(year, month, day) {
      let res = this._addZero(year, month, day)
      return res === this.currentToDate
    },
    _isToday(year, month, day) {
      let res = this._addZero(year, month, day)
      return res === this.beTime(new Date())
    },
    _isRangeDate(year, month, day) {
      let res = this._addZero(year, month, day)
      return res > this.currentFromDate && res < this.currentToDate
    },
    // 选择
    _select(year, month, day) {
      let res = this._addZero(year, month, day)
      if (!this.noValue(this.currentFromDate) && (!this.noValue(this.currentToDate) || res < this.currentFromDate)) {
        this.currentFromDate = ''
        this.currentToDate = ''
        this.currentFromDate = res
      } else if (res > this.currentFromDate) {
        this.currentToDate = res
        this.$emit('update:show', false)
      }
    },
    _addZero(year, month, day) {
      return `${year}-${month < 10 ? '0' : ''}${month}-${day < 10 ? '0' : ''}${day}`
    }
  },
  components: {
    HsScroll
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-datepicker
  width 100%
  height 100%
  position absolute
  top 0
  left 0
  background-color $color_white
  z-index 99
  >.header
    @extend .flex-row
    @extend .flex-between
    width 100%
    height 50px
    padding 0 10px
    >p
      @extend .flex-center
      height 100%
      &:nth-child(1)
        font-size 1rem
        color $color_gray_text
      &:nth-child(2)
        font-size 1.1rem
        font-weight bolder
      &:nth-child(3)
        width 2rem
  >.tableView
    width 100%
    height calc(100% - 50px)
    overflow hidden
    .cell
      width 100%
      border-top 10px solid $color_gray_layer
      padding 10px 0
      >.moutn-bar
        @extend .flex-row
        @extend .flex-start
        width 100%
        height 30px
        font-weight bold
        padding 0 10px
      >.week-bar
        @extend .flex-row
        @extend .flex-between
        width 100%
        border-bottom 1px solid $color_gray_layer
        margin-bottom 2px
        >.item
          @extend .flex-center
          width: 14.28571%
          height 2.5rem
          font-size 0.8rem
      >.day-bar
        @extend .flex-row
        @extend .flex-between
        flex-wrap wrap
        >.disabledItem
        >.item
          @extend .flex-column
          @extend .flex-center
          width: 14.28571%
          height 3rem
          font-size 0.8rem
          position relative
          >p
            font-weight bold
          >.holiday-span
            font-size 0.7rem
            position absolute
            top calc(50% - 1.6em)
            width 100%
            text-align center
          >.mark
            font-size 0.7rem
            position absolute
            top calc(50% + 0.7em)
            width 100%
            text-align center
          >.birthday
            width 25px
            height 25px
            background-size 100% 100%
          >.rest
          >.work
            @extend .flex-center
            position absolute
            bottom 0
            left 0
            width 16px
            height 16px
            font-size 12px
            background-color $color_theme
            color $color_white
            font-weight bold
          >.work
            background-color $color_gray_text
        >.disabledItem
          color $color_gray_text !important
          pointer-events none !important
        >.weekendItem
          color $color_theme
        >.thisItem
          background-color $color_theme
          color $color_white
          border-radius 2px
        >.rangeItem
          background-color $color_red_background_0 !important
          color $color_black_text_0 !important
.datepicker-enter-active
.datepicker-leave-active
  transition: all 300ms
.datepicker-enter
.datepicker-leave-to
  transform translateY(100%)
</style>



