<template>
  <transition name="slide">
    <div class="hs-container-child" v-if="show">
      <c-header :title="$t('base.AirlineList')" leftIcon1="return.png" :leftFn1="close"></c-header>
      <hs-scroll
        class="table-view"
        :data="datalist"
        v-if="!noValue(JSON.stringify(datalist))"
        :options="tableViewOptions"
        @pulling-down="_getDatalist">
        <ul>
          <hs-section :title="$t('base.Hot')">
            <hs-cell-1 :showLogo="false" v-for="(item, index) in datalist[0].flightAirLines" :key="index" @click.native="_select(item)">
              <div class="custom-cell-name">
                <img class="logo" v-lazy="item.AirLineLogoUrl" :key="item.AirLineLogoUrl"/>
                <p>{{item.AirLineName}}<span>{{item.AirLineName_EN}}</span></p>
              </div>
            </hs-cell-1>
          </hs-section>
          <hs-section :title="$t('base.List')">
            <hs-cell-1 :showLogo="false" v-for="(item, index) in datalist" :key="index" v-if="index > 0">
              <div class="letter-block">
                <div class="header" @click="datalist[index].open = !datalist[index].open">
                  <p>{{item.Label}}</p>
                  <div class="logo" :class="{'logo-open': item.open}" style="background-image: url('static/image/base/cell/right_arrow_red.png')"></div>
                </div>
                <div class="details" v-if="item.open">
                  <div class="city" v-for="(itemChild, indexChild) in item.flightAirLines" :key="indexChild" @click="_select(itemChild)">
                    <img class="logo" v-lazy="itemChild.AirLineLogoUrl"/>
                    <p>{{itemChild.AirLineName}}<span>{{itemChild.AirLineName_EN}}</span></p>
                  </div>
                </div>
              </div>
            </hs-cell-1>
          </hs-section>
        </ul>
      </hs-scroll>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
import CHeader from 'components/c-header/c-header'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import {kpiFlightGetAllAirLines} from 'api'
export default {
  data() {
    return {
      tableViewOptions: {
        pullDownRefresh: {
          threshold: 50
        }
      },
      show: false,
      datalist: [],
      editingIndex: undefined
    }
  },
  methods: {
    open() {
      this.show = true
      this._getDatalist()
    },
    close() {
      this.show = false
      this.editingIndex = undefined
    },
    _getDatalist() {
      kpiFlightGetAllAirLines().then((res) => {
        console.log('航司列表: ', res)
        this.datalist = res.ResultData
        this.datalist && this.datalist.forEach((element, index) => {
          this.$set(this.datalist[index], 'open', false)
        })
      })
    },
    _select(item) {
      this.$emit('select', item, this.editingIndex)
      this.close()
    }
  },
  components: {
    CHeader,
    HsScroll,
    HsSection,
    HsCell1
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container-child
  @extend .flex-column
  @extend .flex-start
  position absolute
  width 100%
  height 100%
  left 0
  top 0
  bottom 0
  background-color $color_white
  z-index 200
  .table-view
    width 100%
    height calc(100% - 45px)
    overflow hidden
    .custom-cell-name
      @extend .flex-row
      @extend .flex-start
      width 100%
      >.logo
        width 20px
        height 20px
        margin-right 5px
      >p
        width calc(100% - 30px)
        >span
          color $color_gray_text
          margin-left 5px
    .letter-block
      width 100%
      >.header
        @extend .flex-row
        @extend .flex-between
        width 100%
        height 44px
        >p
          font-size 0.9rem
        >.logo
          width 20px
          height 20px
          background-size 100% 100%
          transition all 0.3s
        >.logo-open
          transform rotate(90deg)
      >.details
        width 100%
        .city
          @extend .flex-row
          @extend .flex-start
          width 100%
          height 44px
          border-top 1px solid $color_gray_layer
          >.logo
            width 20px
            height 20px
            margin-right 5px
          >p
            width calc(100% - 30px)
            >span
              color $color_gray_text
              margin-left 5px
</style>



