<template>
  <transition name="slide">
    <div class="hs-container-child" v-if="show">
      <c-header :title="`${currentValue && currentValue.index >= 0 ? $t('base.EditContact') : $t('base.AddContact')}`" leftIcon1="return.png" :leftFn1="_close">
        <div slot="right" class="header-right" @click="_save">
          <p>{{$t('base.Save')}}</p>
        </div>
      </c-header>
      <hs-section>
        <hs-cell-1 :showLogo="false" v-if="currentValue && currentValue.index >= 0">
          <div class="custom-cell-name">
            <p>{{$t('base.Name')}}</p>
            <p>{{currentValue.Name}}</p>
          </div>
        </hs-cell-1>
        <hs-cell-2 v-else :title="$t('base.Name')" :placeholder="`(${$t('base.RequiredFill')}) ${$t('base.PleaseFill')}`" :edit="true" v-model="currentValue.Name"></hs-cell-2>
        <hs-cell-2 :title="$t('base.CellphoneNumber')" :placeholder="`(${$t('base.RequiredFill')}) ${$t('base.PleaseFill')}`" type="tel" :edit="true" v-model="currentValue.Mobile"></hs-cell-2>
        <hs-cell-2 :title="$t('base.Email')" :placeholder="`(${(currentValue.IsSendBookEmail || currentValue.IsSendIssuedEmail || currentValue.IsSendConfirmEmail) ? $t('base.RequiredFill') : $t('base.NotNecessaryFill')}) ${$t('base.PleaseFill')}`" :edit="true" v-model="currentValue.Email"></hs-cell-2>
      </hs-section>
      <hs-section>
        <hs-cell-3 :title="$t('base.Notice')" :showLogo="false" titleColor="#333333" @click.native="showDetailsNotice = !showDetailsNotice">
          <div class="notice-header">
            <div class="logo" :style="{transform: showDetailsNotice ? 'rotate(90deg)' : 'rotate(-90deg)'}" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
          </div>
        </hs-cell-3>
        <template v-if="showDetailsNotice">
          <template v-if="OrderBusinessType === 1 || OrderBusinessType === 6">
            <hs-cell-1 :showLogo="false">
              <div class="notice-cell">
                <p>{{$t('base.BookingSMS')}}</p>
                <hs-switch v-model="currentValue.IsSendBookSms"></hs-switch>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false">
              <div class="notice-cell">
                <p>{{$t('base.TicketingSMS')}}</p>
                <hs-switch v-model="currentValue.IsSendIssuedSms"></hs-switch>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false">
              <div class="notice-cell">
                <p>{{$t('base.BookingMail')}}</p>
                <hs-switch v-model="currentValue.IsSendBookEmail"></hs-switch>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false">
              <div class="notice-cell">
                <p>{{$t('base.TicketingMail')}}</p>
                <hs-switch v-model="currentValue.IsSendIssuedEmail"></hs-switch>
              </div>
            </hs-cell-1>
          </template>
          <template v-else-if="OrderBusinessType === 2">
            <hs-cell-1 :showLogo="false">
              <div class="notice-cell">
                <p>{{$t('base.ConfirmSMS')}}</p>
                <hs-switch v-model="currentValue.IsSendConfirmSms"></hs-switch>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false">
              <div class="notice-cell">
                <p>{{$t('base.ConfirmMail')}}</p>
                <hs-switch v-model="currentValue.IsSendConfirmEmail"></hs-switch>
              </div>
            </hs-cell-1>
          </template>
          <template v-else-if="OrderBusinessType === 10">
            <hs-cell-1 :showLogo="false">
              <div class="notice-cell">
                <p>{{$t('base.TicketingSMS')}}</p>
                <hs-switch v-model="currentValue.IsSendIssuedSms"></hs-switch>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false">
              <div class="notice-cell">
                <p>{{$t('base.TicketingMail')}}</p>
                <hs-switch v-model="currentValue.IsSendIssuedEmail"></hs-switch>
              </div>
            </hs-cell-1>
          </template>
          <template v-else-if="OrderBusinessType === 101">
            <hs-cell-1 :showLogo="false">
              <div class="notice-cell">
                <p>{{$t('base.WhetherToSendARefundSuccessfulSMS')}}</p>
                <hs-switch v-model="currentValue.IsSendRefundSms"></hs-switch>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false">
              <div class="notice-cell">
                <p>{{$t('base.WhetherToSendARefundEmail')}}</p>
                <hs-switch v-model="currentValue.IsSendRefundEmail"></hs-switch>
              </div>
            </hs-cell-1>
          </template>
          <!-- <hs-cell-1 :showLogo="false">
            <div class="notice-cell">
              <p>预订微信通知</p>
              <hs-switch v-model="currentValue.IsSendBookWechat"></hs-switch>
            </div>
          </hs-cell-1>
          <hs-cell-1 :showLogo="false">
            <div class="notice-cell">
              <p>出票微信通知</p>
              <hs-switch v-model="currentValue.IsSendIssuedWechat"></hs-switch>
            </div>
          </hs-cell-1> -->
        </template>
      </hs-section>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
// 填写联系人页面
import CHeader from 'components/c-header/c-header'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsCell2 from 'base/hs-tableview/hs-cell-2/hs-cell-2'
import HsCell3 from 'base/hs-tableview/hs-cell-3/hs-cell-3'
import HsSwitch from 'base/hs-switch/hs-switch'
import {kpiUserSetDefaultContact} from 'api'
export default {
  props: {
    show: {
      type: Boolean,
      default: false
    },
    value: {
      type: Object,
      default: function() {
        return {}
      }
    },
    datalist: {
      type: Array,
      default: function() {
        return []
      }
    },
    // 订单业务类型(国内机票:1 酒店:2 旅游:3 保险:5 国际机票:6 火车票:10 退票:N+100)
    OrderBusinessType: {
      type: Number,
      default: 1
    }
  },
  computed: {
    currentValue: {
      get: function() {
        return this.value
      },
      set: function(val) {
        return this.$emit('input', val)
      }
    }
  },
  data() {
    return {
      showDetailsNotice: true
    }
  },
  methods: {
    _close() {
      this.$emit('update:show', false)
    },
    _save() {
      if (this.noValue(this.currentValue.Name)) {
        this.$toast(this.$t('TM.PleaseFill', 'function', this.$t('base.ContactName')))
        return
      } else if (this.noValue(this.currentValue.Mobile)) {
        this.$toast(this.$t('TM.PleaseFill', 'function', this.$t('base.ContactPhoneNumber')))
        return
      } else if (!this.isPhoneNumber(this.currentValue.Mobile)) {
        this.$toast(this.$t('TM.XFormatIsIncorrect', 'function', this.$t('base.ContactPhoneNumber')))
        return
      } else if (this.noValue(this.currentValue.Email) && (this.currentValue.IsSendBookEmail || this.currentValue.IsSendIssuedEmail || this.currentValue.IsSendConfirmEmail)) {
        this.$toast(this.$t('TM.TheEmailNotificationHasBeenTurnedOn'))
        return
      } else if (!this.noValue(this.currentValue.Email) && !this.isEmail(this.currentValue.Email)) {
        this.$toast(this.$t('TM.XFormatIsIncorrect', 'function', this.$t('base.Email')))
        return
      }
      let index = this.datalist.map(v => v.Mobile).indexOf(this.currentValue.Mobile)
      if (index >= 0 && this.currentValue.index < 0) {
        this.$toast(this.$t('TM.ThereMustBeNoContactWithTheSamePhoneNumber'))
      } else {
        let params = {
          ID: this.currentValue.ID,
          IsDefault: this.currentValue.IsDefault,
          Name: this.currentValue.Name,
          Mobile: this.currentValue.Mobile,
          Email: this.currentValue.Email
        }
        kpiUserSetDefaultContact(params).then((res) => {
          console.log('修改常用联系人: ', res)
          this.currentValue.ID = res.ResultData.ID
          this.$emit('delegate', this.currentValue)
          this._close()
        })
        // if (this.currentValue && this.currentValue.index >= 0) {
        //   res.Contacts.splice(this.currentValue.index, 1, Object.assign(this.currentValue, {openDelete: false}))
        // } else {
        //   res.Contacts.push(Object.assign(this.currentValue, {openDelete: false}))
        // }
      }
    }
  },
  components: {
    CHeader,
    HsSection,
    HsCell1,
    HsCell2,
    HsCell3,
    HsSwitch
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container-child
  position absolute
  width 100%
  height 100%
  left 0
  top 0
  bottom 0
  background-color $color_gray_background_0
  z-index 200
  .header-left
    @extend .flex-row
    @extend .flex-start
    width 60px
    height 100%
    >p
      font-size 0.9rem
  .header-right
    @extend .flex-row
    @extend .flex-end
    width 60px
    height 100%
    >p
      font-size 0.9rem
  .custom-cell-name
    @extend .flex-row
    @extend .flex-between
    width 100%
    >p
      &:nth-child(2)
        font-weight bold
  .fill-in-cell
    @extend .flex-row
    @extend .flex-between
    width 100%
    font-size 0.8rem
    color $color_gray_text
    .picker
    >input
      width 90%
      height 100%
      color $color_black_text_0
  .notice-header
    @extend .flex-row
    @extend .flex-end
    width 100%
    >.logo
      width 20px
      height 20px
      background-size 100% 100%
      transition all 300ms
  .notice-cell
    @extend .flex-row
    @extend .flex-between
    width 100%
    font-size 1rem
    color $color_gray_text
</style>



