<template>
  <div class="hs-cell-2">
    <div class="details">
      <p class="title">{{title}}</p>
      <p class="input" v-if="!edit">{{currentValue}}</p>
      <input maxlength='50' class="input" :type="type" v-model="currentValue" :placeholder="placeholder" v-else/>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
/**
 * 适用于文字居右的填写/展示型cell
 *  ————————————————————————————
 * | 标题    XXXXXXXXXXXXXXXXXX |
 *  ————————————————————————————
 */
export default {
  props: {
    value: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    },
    edit: {
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    }
  },
  computed: {
    currentValue: {
      get: function() {
        return this.value
      },
      set: function(val) {
        return this.$emit('input', val)
      }
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-cell-2
  width 100%
  padding 0 15px
  >.details
    @extend .flex-row
    @extend .flex-between
    width 100%
    height 44px
    border-bottom 1px solid $color_gray_layer
    >.title
      @extend .flex-row
      @extend .flex-start
      font-size 1rem
      height 100%
      width 90px
    >.input
      @extend .flex-row
      @extend .flex-end
      width calc(100% - 100px)
      height 100%
      color $color_gray_text
      text-align right
      padding 0
  &:last-child
    >.details
      border-bottom 0px
</style>



