<template>
  <hs-dialog ref="Dialog" v-model="currentShow">
    <div class="hs-filter" @click.stop>
      <div class="header">
        <p @click="_close">{{$t('base.Cancel')}}</p>
        <p :class="[canReset ? 'canReset' : 'cannotReset']" @click="_reset">{{$t('base.ResetFilter')}}</p>
        <p @click="_confirm">{{$t('base.Confirm')}}</p>
      </div>
      <div class="details" v-if="!noValue(JSON.stringify(currentDetails)) && selectedKey">
        <div class="left" :type="language">
          <div v-if="currentDetails.CommericalInfoList && currentDetails.CommericalInfoList.length > 0" @click="selectedKey = 'CommericalInfoList'"
            class="item"
            :class="{'item-active': selectedKey === 'CommericalInfoList'}">
            <div class="mark" :class="{'mark-hidden': currentDetails.CommericalInfoList.filter(v => v.selected === true).length === 0}"></div>
            <p>{{$t('base.BusinessDistrict')}}</p>
          </div>
          <div v-if="currentDetails.DistrictInfoList && currentDetails.DistrictInfoList.length > 0" @click="selectedKey = 'DistrictInfoList'"
            class="item"
            :class="{'item-active': selectedKey === 'DistrictInfoList'}">
            <div class="mark" :class="{'mark-hidden': currentDetails.DistrictInfoList.filter(v => v.selected === true).length === 0}"></div>
            <p>{{$t('base.AdministrativeDistrict')}}</p>
          </div>
          <div v-if="currentDetails.RailwayAirInfoList && currentDetails.RailwayAirInfoList.length > 0" @click="selectedKey = 'RailwayAirInfoList'"
            class="item"
            :class="{'item-active': selectedKey === 'RailwayAirInfoList'}">
            <div class="mark" :class="{'mark-hidden': currentDetails.RailwayAirInfoList.filter(v => v.selected === true).length === 0}"></div>
            <p>{{$t('base.AirportStation')}}</p>
          </div>
          <div v-if="currentDetails.SubwayInfoList && currentDetails.SubwayInfoList.length > 0" @click="selectedKey = 'SubwayInfoList'"
            class="item"
            :class="{'item-active': selectedKey === 'SubwayInfoList'}">
            <div class="mark" :class="{'mark-hidden': currentDetails.SubwayInfoList.filter(v => v.selected === true).length === 0}"></div>
            <p>{{$t('base.SubwayStation')}}</p>
          </div>
          <div v-if="currentDetails.FacilitieInfoList && currentDetails.FacilitieInfoList.length > 0" @click="selectedKey = 'FacilitieInfoList'"
            class="item"
            :class="{'item-active': selectedKey === 'FacilitieInfoList'}">
            <div class="mark" :class="{'mark-hidden': currentDetails.FacilitieInfoList.filter(v => v.selected === true).length === 0}"></div>
            <p>{{$t('base.Infrastructure')}}</p>
          </div>
          <div v-if="currentDetails.BrandInfoList && currentDetails.BrandInfoList.length > 0" @click="selectedKey = 'BrandInfoList'"
            class="item"
            :class="{'item-active': selectedKey === 'BrandInfoList'}">
            <div class="mark" :class="{'mark-hidden': currentDetails.BrandInfoList.filter(v => v.selected === true).length === 0}"></div>
            <p>{{$t('base.Brand')}}</p>
          </div>
          <div v-if="currentDetails.ThemeList && currentDetails.ThemeList.length > 0" @click="selectedKey = 'ThemeList'"
            class="item"
            :class="{'item-active': selectedKey === 'ThemeList'}">
            <div class="mark" :class="{'mark-hidden': currentDetails.ThemeList.filter(v => v.selected === true).length === 0}"></div>
            <p>{{$t('base.Theme')}}</p>
          </div>
        </div>
        <div class="right">
          <hs-scroll
            class="table-view"
            ref="tableView"
            :data="currentDetails[selectedKey]">
            <ul>
              <hs-cell-1 @click.native="currentDetails[selectedKey].map(v => {v.selected = false})" :showLogo="false">
                <div class="custom-cell">
                  <p>{{$t('base.All')}}</p>
                  <div v-if="currentDetails[selectedKey].filter(v => v.selected === true).length === 0"
                    class="logo"
                    style="background-image: url('static/image/base/filter/checkbox-selected.png')"></div>
                  <div v-else
                    class="logo"
                    style="background-image: url('static/image/base/filter/checkbox-default.png')"></div>
                </div>
              </hs-cell-1>
              <hs-cell-1 @click.native="_select(index)" :showLogo="false"
                v-for="(item, index) in currentDetails[selectedKey]" :key="index">
                <div class="custom-cell">
                <p>{{item.Name ? item.Name : item.NameChn}}</p>
                  <div v-if="currentDetails[selectedKey][index].selected"
                    class="logo"
                    style="background-image: url('static/image/base/filter/checkbox-selected.png')"></div>
                  <div v-else
                    class="logo"
                    style="background-image: url('static/image/base/filter/checkbox-default.png')"></div>
                </div>
              </hs-cell-1>
            </ul>
          </hs-scroll>
        </div>
      </div>
    </div>
  </hs-dialog>
</template>
<script type="text/ecmascript-6">
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsDialog from 'base/hs-dialog/hs-dialog'
export default {
  props: {
    show: {
      type: Boolean,
      default: false
    },
    details: {
      type: Object,
      default: function() {
        return {}
      }
    },
    initSelectedKey: {
      type: String,
      default: 'CommericalInfoList'
    }
  },
  computed: {
    currentShow: {
      get: function() {
        return this.show
      },
      set: function(val) {
        this.$emit('update:show', val)
      }
    }
  },
  watch: {
    currentShow(val) {
      if (val) {
        /** setTimeoue解决第一次打开, 无法滚动的问题 */
        setTimeout(() => {
          this.currentDetails = JSON.parse(JSON.stringify(this.details))
          console.log('this.details: ', this.details)
          console.log('this.currentDetails: ', this.currentDetails)
          this._setCanReset()
          this.selectedKey = this.initSelectedKey
        }, 200)
      }
    },
    selectedKey() {
      this.$refs.tableView && this.$refs.tableView.scrollTo(0, 0)
    }
  },
  data() {
    return {
      selectedKey: '',
      currentDetails: {},
      canReset: false
    }
  },
  methods: {
    /** 关闭 */
    _close() {
      this.$refs.Dialog.close()
    },
    /** 选择 */
    _select(index) {
      if (this.selectedKey === 'CommericalInfoList' || this.selectedKey === 'DistrictInfoList') {
        this.currentDetails[this.selectedKey].map(v => { v.selected = false })
      }
      this.currentDetails[this.selectedKey][index].selected = !this.currentDetails[this.selectedKey][index].selected
      this._setCanReset()
    },
    /** 重置 */
    _reset() {
      for (let key of Object.keys(this.currentDetails)) {
        this.currentDetails[key].forEach((element, index) => {
          this.$set(this.currentDetails[key][index], 'selected', false)
        })
      }
      this._setCanReset()
    },
    /** 确认 */
    _confirm() {
      this.$emit('delegate', this.currentDetails)
      this._close()
    },
    /** 是否可清除筛选 */
    _setCanReset() {
      let res = false
      for (let key of Object.keys(this.currentDetails)) {
        this.currentDetails[key].forEach((element, index) => {
          if (this.currentDetails[key][index].selected === true) {
            res = true
          }
        })
      }
      this.canReset = res
    }
  },
  components: {
    HsScroll,
    HsCell1,
    HsDialog
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-filter
  position absolute
  width 100%
  left 0
  bottom 0
  background-color $color_white
  border-radius 10px 10px 0 0
  max-height 70%
  overflow hidden
  >.header
    @extend .flex-row
    @extend .flex-between
    width 100%
    height 45px
    border-bottom 1px solid $color_gray_layer
    padding 0 15px
    >p
      font-size 0.8rem
    >.cannotReset
      color $color_gray_layer
      user-select none
    >.canReset
      padding 5px
      background-color $color_gray_background_0
  >.details
    @extend .flex-row
    @extend .flex-start
    @extend .flex-top
    width 100%
    height 308px
    font-size 0.8rem
    // position relative
    >.left
      width 145px
      height 100%
      background-color $color_gray_background_0
      >.item
        @extend .flex-row
        @extend .flex-start
        width 100%
        height 44px
        padding 0 10px
        >.mark
          width 6px
          height 6px
          border-radius 3px
          background-color $color_theme
          margin-right 4px
        >.mark-hidden
          visibility hidden
        >p
          width calc(100% - 30px)
      >.item-active
        background-color $color_white
    >.left[type="EN"]
      font-size 0.7rem
    >.right
      width calc(100% - 145px)
      height 100%
      .table-view
        width 100%
        height 100%
        overflow hidden
        .custom-cell
          @extend .flex-row
          @extend .flex-between
          width 100%
          padding 10px 0
          >p
            width calc(100% - 25px)
          >.logo
            width 20px
            height 20px
            background-size 100%
</style>



