<template>
  <div></div>
</template>
<script>
import Vue from 'vue'
import eventBus from 'common/js/eventBus.js'
import {kpiPayPayStart} from 'api'
import { mapGetters } from 'vuex'
// 微信浏览器
const WechatBrowser = 0
// 手机浏览器
const Browser = 3
export default {
  data() {
    return {
      details: {}
    }
  },
  computed: {
    ...mapGetters([
      'isMiniprogram'
    ])
  },
  methods: {
    /**
     * @param {*details.OrderBusinessType} 业务类型 1: 国内机票 2: 国内酒店 6: 国际机票 10: 火车票 11: 国际酒店
     * @param {*details.OrderID} 订单ID
     * @param {*details.PayWay} 支付方式 3: 微信
     * @param {*details.TravelType} 因公因私
     * @param {*details.CallbackPathName} 发起支付的路由地址名称
     */
    gotoPay(details) {
      this.details = details
      if (this.isMiniprogram) {
        // 小程序内支付
        this._gotoPayWithMiniprogram()
      } else {
        let params = {
          OrderBusinessType: this.details.OrderBusinessType,
          OrderID: this.details.OrderID,
          PayWay: 3
        }
        kpiPayPayStart(params).then((res) => {
          console.log('支付： ', res)
          if (res.ResultData.IsCanPay === true) {
            if (res.ResultData.PayType === Browser) {
              // 手机浏览器内支付
              this._gotoPayWithBrowser(res)
            } else if (res.ResultData.PayType === WechatBrowser) {
              // 微信浏览器内支付
              this._gotoPayWithWechatBrowser(res)
            }
          } else {
            this.$toast(res.ResultData.Message)
          }
        })
      }
    },
    /**
     * 小程序内支付
     * @param {*订单id} OrderID
     * @param {*业务类型 1:机票} OrderBusinessType
     */
    _gotoPayWithMiniprogram() {
      // eslint-disable-next-line
      wx.miniProgram.navigateTo({
        url: `/pages/pay/pay?OrderID=${this.details.OrderID}&OrderBusinessType=${this.details.OrderBusinessType}`
      })
    },
    /**
     * 手机浏览器内支付
     */
    _gotoPayWithBrowser(res) {
      console.log('在手机浏览器', res)
      this.$alert({
        content: this.$t('TM.PleaseConfirmIfWeChatPaymentHasBeenCompleted'),
        confirmTxt: this.$t('TM.Completed'),
        cancelTxt: this.$t('TM.HasProblem'),
        confirmFn: () => {
          setTimeout(() => { eventBus.$emit(`refresh`) }, 0)
          this.$router.replace({name: this.details.CallbackPathName, params: {TravelType: Number(this.details.TravelType)}})
        }
      })
      setTimeout(() => {
        window.location.href = res.ResultData.WebUrl
      }, 1000)
    },
    /**
     * 微信浏览器内支付
     */
    _gotoPayWithWechatBrowser(res) {
      let _this = this
      let gotoPay = function() {
        // eslint-disable-next-line
        WeixinJSBridge.invoke('getBrandWCPayRequest', {
          appId: res.ResultData.AppId, // 公众号名称，由商户传入
          timeStamp: res.ResultData.Timestamp, // 时间戳
          nonceStr: res.ResultData.NonceStr, // 随机串
          package: res.ResultData.Package, // 扩展包
          signType: 'MD5', // 微信签名方式:MD5
          paySign: res.ResultData.PaySign // 微信签名
        }, function (res) {
          if (res.err_msg === 'get_brand_wcpay_request:ok') {
            Vue.prototype.$toast(Vue.prototype.$t('TM.PaymentSuccessful'), {
              complete: () => {
                setTimeout(() => { eventBus.$emit(`refresh`) }, 0)
                _this.$router.replace({name: this.details.CallbackPathName, params: {TravelType: Number(_this.details.TravelType)}})
              }
            })
          } else {
            Vue.prototype.$toast(Vue.prototype.$t('TM.PaymentFailed'))
          }
        })
      }
      // WeixinJSBridge
      if (typeof WeixinJSBridge === 'undefined') {
        if (document.addEventListener) {
          // eslint-disable-next-line
          document.addEventListener('WeixinJSBridgeReady', gotoPay(), false)
        } else if (document.attachEvent) {
          // eslint-disable-next-line
          document.attachEvent('WeixinJSBridgeReady', gotoPay())
          // eslint-disable-next-line
          document.attachEvent('onWeixinJSBridgeReady', gotoPay())
        }
      } else {
        gotoPay()
      }
    }
  }
}
</script>



