<template>
  <transition name="hs-alert-fade">
    <div class="hs-alert" v-show="show">
      <div class="mask" @click.self.stop="_maskClose">
        <div class="hs-alert-main">
          <i class="cubeic-alert"></i>
          <div class="content">
            <div class="title">
              <slot name="title">
                <p>{{title}}</p>
              </slot>
            </div>
            <div class="content">
              <slot name="content">
                <p>{{content}}</p>
              </slot>
            </div>
          </div>
          <slot name="footer">
            <div class="footer">
              <div class="cancel-btn" v-if="!noValue(cancelBtn)" @click="_cancel">{{cancelBtn}}</div>
              <div class="confirm-btn" @click="_confirm">{{confirmBtn}}</div>
            </div>
          </slot>
        </div>
      </div>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
import Vue from 'vue'
export default {
  props: {
    show: {
      type: Boolean,
      default: false
    },
    cancelBtn: {
      type: String,
      default: Vue.prototype.$t('base.Cancel')
    },
    confirmBtn: {
      type: String,
      default: Vue.prototype.$t('base.Confirm')
    },
    title: {
      type: String,
      default: Vue.prototype.$t('base.Tips')
    },
    content: {
      type: String,
      default: ''
    },
    maskClose: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    _close() {
      this.$emit('update:show', false)
    },
    _maskClose() {
      this.maskClose && this._close()
    },
    _cancel() {
      this.$emit('cancel')
      this._close()
    },
    _confirm() {
      this.$emit('confirm')
      this._close()
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-alert
  position absolute
  top 0
  left 0
  bottom 0
  right 0
  >.mask
    @extend .flex-center
    width 100%
    height 100%
    background-color $color-black_5
    >.hs-alert-main
      @extend .flex-column
      @extend .flex-start
      width 270px
      border-radius 2px
      background-color $color_white
      >i
        @extend .flex-center
        width 50px
        height 50px
        border-radius 25px
        background-color $color_gray_background_0
        margin-top 20px
        font-size 30px
      >.content
        width 100%
        padding 10px 0
        >.title
          @extend .flex-row
          @extend .flex-center
          width 100%
          padding 10px 0
          color $color_black_text_0
          p
            font-weight bold
        >.content
          @extend .flex-row
          @extend .flex-center
          width 100%
          padding 10px 0
          color $color_black_text_1
          p,
          input
            font-size 0.9rem
      >.footer
        @extend .flex-row
        width 100%
        height 50px
        border-top 1px solid $color_gray_layer
        >div
          @extend .flex-center
          flex 1
          &:nth-child(2)
            border-left 1px solid $color_gray_layer
        >.cancel-btn
          color $color_gray_text
        >.confirm-btn
          color $color_theme
.hs-alert-fade-enter-active
  animation: dialog-fadein .4s
  .hs-alert-main
    animation: dialog-zoom .4s

@keyframes dialog-fadein
  0%
    opacity: 0
  100%
    opacity: 1
@keyframes dialog-zoom
  0%
    transform: scale(0)
  50%
    transform: scale(1.1)
  100%
    transform: scale(1)
</style>



