<template>
  <transition name="slide">
    <div class="hs-container-child" v-if="currentValue">
      <div id="container"></div>
      <div class="return" @click="_backToBook">
        <div style="background-image: url('static/image/header/return.png')"></div>
      </div>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
import AMap from 'AMap'
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    details: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  computed: {
    currentValue: {
      get: function() {
        return this.value
      },
      set: function(val) {
        return this.$emit('input', val)
      }
    }
  },
  watch: {
    currentValue(val) {
      if (val) {
        setTimeout(() => {
          this._initMap()
        }, 500)
      } else {
        console.log('销毁地图')
        this.map && this.map.destroy()
      }
    }
  },
  data() {
    return {
      map: undefined
    }
  },
  methods: {
    _initMap() {
      console.log('地图')
      let inforWidowContent = `
        <div style="display: block; width: 200px; padding: 10px; background-color: white; border-radius: 6px; box-shadow: 0px 0px 25px #999999;position: relative;">
          <p style="width: 100%; font-size: 0.8rem; line-height: 1rem; color: #404040;">${this.details.HotelName}</p>
          <div style="transform: rotate(45deg); width: 10px; height: 10px; background-color: white; position: absolute; left: 95px; bottom: -5px;"></div>
        </div>
      `
      let _this = this
      function createInfoWindow() {
        console.log('添加点击事件')
        let info = document.createElement('div')
        info.onclick = _this._backToBook
        info.innerHTML = inforWidowContent
        return info
      }
      // eslint-disable-next-line
      this.map = new AMap.Map('container', {
        zoom: 17, // 级别 就是看地图的远-近（3-20）
        center: [this.details.Longitude, this.details.Latitude], // 中心点坐标
        viewMode: '3D' // 使用3D视图
      })
      this.map.on('complete', () => {
        console.log('地图加载完成！')
        let infoWindow = new AMap.InfoWindow({ // 创建信息窗体
          resizeEnable: true,
          isCustom: true, // 使用自定义窗体
          content: createInfoWindow(), // 信息窗体的内容可以是任意html片段
          offset: new AMap.Pixel(0, -40),
          closeWhenClickMap: true
        })
        let onMarkerClick = (e) => {
          infoWindow.open(this.map, e.target.getPosition()) // 打开信息窗体
          // e.target就是被点击的Marker
        }
        infoWindow.on('open', () => {
          console.log('窗体打开')
          _this.$forceUpdate()
        })
        let marker = new AMap.Marker({
          position: [this.details.Longitude, this.details.Latitude] // 位置
        })
        marker.on('click', onMarkerClick) // 绑定click事件
        this.map.add(marker)

        // 地图加载完后打开信息窗体, 打开信息窗体. 参数为拼凑的 e.target.getPosition()
        infoWindow.open(this.map, {
          O: this.details.Latitude,
          P: this.details.Longitude,
          lat: this.details.Latitude,
          lng: this.details.Longitude
        })
      })
    },
    _backToBook() {
      console.log('返回上一页预定')
      this.currentValue = false
    }
  },
  beforeDestroy() {
    console.log('销毁地图')
    this.map && this.map.destroy()
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container-child
  position absolute
  width 100%
  height 100%
  top 0
  left 0
  background-color $color_white
  z-index 10
  #container
    width 100%
    height 100%
  >.return
    @extend .flex-center
    width 30px
    height 30px
    background-color $color_white
    border-radius 15px
    box-shadow 0 0 20px $color_gray_text
    position absolute
    top 20px
    left 20px
    >div
      width 20px
      height 20px
      background-size 100% 100%
</style>



