<template>
  <transition name="slide">
    <div class="hs-container-child" v-if="show">
      <c-header class="header" :title="orderInit.CustomItemName" leftIcon1="return.png" :leftFn1="_close"></c-header>
      <div class="search-bar">
        <div class="bar">
          <input maxlength='50' v-model.trim="KeyWord" :placeholder="$t('base.PleaseEnterAKeywordToSearch')"/>
          <div class="logo" style="background-image: url('static/image/plane-ticket/book/search.png')"></div>
        </div>
      </div>
      <hs-scroll
        class="table-view"
        :data="orderInit.CustomItems">
        <ul>
          <hs-section v-if="orderInit.IsAllowAddCustomItem">
            <hs-cell-1 :showLogo="false">
              <div class="custom-cell">
                <hs-button width="100%" @click="showDialog = true">{{$t('base.New')}}</hs-button>
              </div>
            </hs-cell-1>
          </hs-section>
          <hs-section>
            <hs-cell-1
              v-if="item.Name && (item.Name.includes(KeyWord) || noValue(KeyWord))"
              v-for="(item, index) in orderInit.CustomItems" :key="index"
              :value="item.Name"
              :showLogo="false"
              @click.native="_select(item)"></hs-cell-1>
          </hs-section>
        </ul>
      </hs-scroll>
      <hs-dialog v-model="showDialog">
        <div class="dialog">
          <div class="header">{{$t('base.New')}} {{orderInit.CustomItemName}}</div>
          <div class="details" @click.stop>
            <input maxlength='50' v-model="newName" :placeholder="$t('TM.PleaseFill', 'function', orderInit.CustomItemName)"/>
            <button @click="_addCustomItems">{{$t('base.Confirm')}}</button>
          </div>
        </div>
      </hs-dialog>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
// 自定义项
import CHeader from 'components/c-header/c-header'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsButton from 'base/hs-button/hs-button'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsDialog from 'base/hs-dialog/hs-dialog'
import {kpiUserAddCustomItem} from 'api'
export default {
  props: {
    show: {
      type: Boolean,
      default: false
    },
    // 订单填写初始化的一些条件
    orderInit: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  components: {
    CHeader,
    HsScroll,
    HsButton,
    HsSection,
    HsCell1,
    HsDialog
  },
  data() {
    return {
      KeyWord: '',
      newName: '',
      showDialog: false
    }
  },
  methods: {
    _close() {
      this.$emit('update:show', false)
    },
    _select(item) {
      this.$emit('delegate', item)
      this._close()
    },
    _addCustomItems() {
      let params = {
        Name: this.newName
      }
      kpiUserAddCustomItem(params).then((res) => {
        console.log('添加成本中心: ', res)
        let orderInit = JSON.parse(JSON.stringify(this.orderInit))
        orderInit.CustomItems.splice(0, 0, res.ResultData)
        this.$emit('update:orderInit', orderInit)
        this.showDialog = false
        this.$toast(this.$t('base.AddSucceed'))
      })
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container-child
  position absolute
  width 100%
  height 100%
  left 0
  top 0
  bottom 0
  background-color $color_white
  z-index 200
  >.header
    >.header-left
      @extend .flex-row
      @extend .flex-start
      width 60px
      height 100%
      >p
        font-size 0.9rem
  >.search-bar
    @extend .flex-center
    width 100%
    padding 5px 15px
    background-color $color_white
    >.bar
      @extend .flex-row
      @extend .flex-between
      width 100%
      border-radius 4px
      border 1px solid $color_gray_layer
      padding 10px
      background-color $color_white
      font-size 0.9rem
      >input
        width calc(100% - 30px)
        height 100%
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
  >.table-view
    width 100%
    height calc(100% - 45px - 52px)
    overflow hidden
    background-color $color_white
    .custom-cell
      @extend .flex-row
      @extend .flex-start
      width 100%
      padding 12px 2px
  .dialog
    @extend .flex-column
    @extend .flex-center
    width 100%
    height 100%
    padding 0 15px
    >.header
      @extend .flex-row
      @extend .flex-start
      width 100%
      height 40px
      font-size 1rem
      color $color_white
    >.details
      @extend .flex-column
      width 100%
      padding 15px 10px
      background-color $color_white
      border-radius 2px
      >input
        width 100%
        height 44px
        border-bottom 1px solid $color_gray_layer
        margin-bottom 10px
        font-size 0.9rem
      >button
        width 100%
        background-color $color_theme
        color $color_white
        font-size 0.9rem
        border-radius 3px
        padding 10px 0
</style>



