<template>
  <transition name="slide">
    <div class="hs-container-child" v-if="show">
      <c-header leftIcon1="return.png" :leftFn1="close" :title="$t('base.ChooseCostCenter')"></c-header>
      <div class="search-bar">
        <div class="bar">
          <input maxlength='50' v-model.trim="KeyWord" :placeholder="$t('base.PleaseEnterAKeywordToSearch')"/>
          <div class="logo" style="background-image: url('static/image/plane-ticket/book/search.png')" @click="_getDatalist"></div>
        </div>
      </div>
      <hs-scroll
        class="table-view"
        ref="tableView"
        :data="datalist"
        :options="tableViewOptions"
        @pulling-down="_getDatalist"
        @pulling-up="_getDatalistMore">
        <ul>
          <hs-section v-if="powerDetails.IsAllowAddCostCenter">
            <hs-cell-1 :showLogo="false">
              <div class="custom-cell">
                <hs-button width="100%" @click="showDialog = true">{{$t('base.New')}}</hs-button>
              </div>
            </hs-cell-1>
          </hs-section>

          <hs-section>
            <hs-cell-1 :showLogo="false" :value="item.Name" v-for="(item, index) in datalist" :key="index" @click.native="_select(item)"></hs-cell-1>
          </hs-section>
        </ul>
      </hs-scroll>

      <!-- 添加弹窗 -->
      <hs-dialog v-model="showDialog">
        <div class="dialog">
          <div class="header">{{$t('base.AddCostCenter')}}</div>
          <div class="details" @click.stop>
            <hs-input v-model="newName"
              class="input"
              height="44px"
              :placeholder="$t('base.PleaseEnterACostCenterName')"></hs-input>
            <hs-button @click="_addCostCenter">{{$t('base.Confirm')}}</hs-button>
          </div>
        </div>
      </hs-dialog>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
import CHeader from 'components/c-header/c-header'
import HsButton from 'base/hs-button/hs-button'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsDialog from 'base/hs-dialog/hs-dialog'
import HsInput from 'base/hs-input/hs-input'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import {kpiUserGetCostCenters, kpiUserAddCostCenter} from 'api'
export default {
  props: {
    powerDetails: {
      type: Object,
      default: function() {
        return {}
      }
    }
  },
  data() {
    return {
      show: false,
      tableViewOptions: {
        pullDownRefresh: {
          threshold: 50
        }
      },
      currentPageIndex: 1,
      currentPageSize: 30,
      datalist: [],
      KeyWord: '',
      travelType: 0,
      /** 添加弹窗 */
      newName: '',
      showDialog: false
    }
  },
  watch: {
    show(val) {
      if (val) {
        this._getDatalist()
      }
    },
    KeyWord(val) {
      this.timer && clearTimeout(this.timer) // eslint-disable-line
      this.timer = setTimeout(() => {
        this._getDatalist()
      }, 1000)
    }
  },
  methods: {
    open() {
      this.show = true
    },
    close() {
      this.show = false
    },
    _getDatalist() {
      this.currentPageIndex = 1
      this.tableViewOptions.pullUpLoad = false
      let params = {
        PageIndex: this.currentPageIndex,
        PageSize: this.currentPageSize,
        KeyWord: this.KeyWord
      }
      kpiUserGetCostCenters(params).then((res) => {
        console.log('成本中心: ', res)
        this.datalist = res.ResultData
        if (res.ResultData.length >= this.currentPageSize) {
          this.tableViewOptions.pullUpLoad = {threshold: 50}
        }
        this.$refs.tableView && this.$refs.tableView.forceUpdate()
      })
    },
    _getDatalistMore() {
      this.currentPageIndex++
      let params = {
        PageIndex: this.currentPageIndex,
        PageSize: this.currentPageSize
      }
      kpiUserGetCostCenters(params).then((res) => {
        console.log('成本中心: ', res)
        this.datalist = this.datalist.concat(res.ResultData)
        if (res.ResultData.length < this.currentPageSize) {
          this.tableViewOptions.pullUpLoad = false
        } else {
          this.tableViewOptions.pullUpLoad = {threshold: 50}
        }
        this.$refs.tableView && this.$refs.tableView.forceUpdate()
      })
    },
    _select(item) {
      this.$emit('select', item)
      this.close()
    },

    /** 添加 */
    _addCostCenter() {
      let params = {
        Name: this.newName
      }
      kpiUserAddCostCenter(params).then((res) => {
        console.log('添加成本中心: ', res)
        this.datalist.unshift(res.ResultData)
        this.showDialog = false
        this.$toast(this.$t('base.AddSucceed'))
      })
    }
  },
  components: {
    CHeader,
    HsButton,
    HsCell1,
    HsDialog,
    HsInput,
    HsScroll,
    HsSection
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container-child
  @extend .flex-column
  @extend .flex-start
  position absolute
  width 100%
  height 100%
  left 0
  top 0
  bottom 0
  background-color $color_white
  z-index 200
  >.search-bar
    @extend .flex-center
    width 100%
    padding 5px 15px
    background-color $color_white
    >.bar
      @extend .flex-row
      @extend .flex-between
      width 100%
      border-radius 4px
      border 1px solid $color_gray_layer
      padding 10px
      background-color $color_white
      font-size 0.9rem
      >input
        width calc(100% - 30px)
        height 100%
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
  .table-view
    width 100%
    height calc(100% - 45px - 52px)
    overflow hidden
    background-color $color_white
    .custom-cell
      @extend .flex-row
      @extend .flex-start
      width 100%
      padding 12px 2px

  .dialog
    @extend .flex-column
    @extend .flex-center
    width 100%
    height 100%
    padding 0 15px
    >.header
      @extend .flex-row
      @extend .flex-start
      width 100%
      height 40px
      font-size 1rem
      color $color_white
    >.details
      @extend .flex-column
      width 100%
      padding 15px 10px
      background-color $color_white
      border-radius 2px
      >.input
        border-bottom 1px solid $color_gray_layer
        margin-bottom 10px
</style>



