<template>
  <transition name="slide">
    <div class="hs-container-child" v-if="currentValue">
      <c-header :title="$t('base.ApprovalInformation')" leftIcon1="return.png" :leftFn1="() => currentValue = false"></c-header>
      <hs-scroll class="table-view">
        <ul>
          <hs-section>
            <hs-cell-1 :showLogo="false">
              <div class="custom-cell">
                <div class="header">
                  <p>{{businessName}}:</p>
                  <p v-for="(item, index) in passengers" :key="index">{{item.Name}}</p>
                </div>
                <div class="details">
                  <div class="item" v-for="(item, index) in  datalist" :key="index">
                    <div class="left">
                      <div class="point" :class="{'point-active': activedIndex === index, 'pointer-no-arrival': activedIndex < index}"></div>
                      <div class="layer" :class="{'layer-no-arrival': activedIndex <= index}"></div>
                    </div>
                    <div class="right">
                      <p v-show="item.TemplateNodeProcessTime > 0">{{beTime(item.TemplateNodeProcessTime, 'yyyy-MM-dd hh:mm')}}</p>
                      <p v-if="(item.Main && item.Main.ProcessName) || (item.Reserve && item.Reserve.ProcessName)">
                        {{$t('base.Operator')}}: <template v-if="item.Main && item.Main.ProcessName">{{item.Main.ProcessName}}</template>&nbsp;<template v-if="item.Reserve && item.Reserve.ProcessName">{{item.Reserve.ProcessName}}</template>
                      </p>
                      <p v-if="item.CommunicationType !== 0">{{$t('base.ApprovalMethod')}}: {{item.CommunicationType | filterVettingHandleCommunicationTypeOptions}}</p>
                      <p>{{item.TemplateNodeName}}</p>
                      <p :class="{actived: activedIndex >= index}">{{item.TemplateNodeProcessStatusDesc}}</p>
                    </div>
                  </div>
                </div>
              </div>
            </hs-cell-1>
          </hs-section>
        </ul>
      </hs-scroll>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
// 机票订单详情审批信息页面
import Vue from 'vue'
import CHeader from 'components/c-header/c-header';
import HsScroll from 'base/hs-scroll/hs-scroll';
import HsSection from 'base/hs-tableview/hs-section/hs-section';
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1';
export default {
  components: {
    CHeader,
    HsScroll,
    HsSection,
    HsCell1
  },
  props: {
    businessName: {
      type: String,
      default: ''
    },
    value: {
      type: Boolean,
      default: false
    },
    datalist: {
      type: Array,
      default: function() {
        return [];
      }
    },
    passengers: {
      type: Array,
      default: function() {
        return [];
      }
    }
  },
  computed: {
    currentValue: {
      get: function() {
        return this.value;
      },
      set: function(val) {
        return this.$emit('input', val);
      }
    },
    activedIndex() {
      let res = this.datalist.length - 1;
      let index = this.datalist.map(v => v.TemplateNodeProcessStatus).indexOf(0);
      if (index >= 0) {
        res = index - 1
      }
      // if (this.datalist[0].TemplateNodeProcessStatusDesc !== '已发起') {
      //   res = 0;
      // } else if (this.datalist[res].TemplateNodeProcessStatusDesc === '已结束') {
      // } else {
      //   let index = this.datalist.map(v => v.TemplateNodeProcessStatusDesc).indexOf('进行中');
      //   res = index;
      // }
      return res;
    }
  },
  filters: {
    filterVettingHandleCommunicationTypeOptions(val) {
      switch (val) {
        case 0:
          return Vue.prototype.$t('base.Other')
        case 1:
          return Vue.prototype.$t('base.SMSApproval')
        case 2:
          return Vue.prototype.$t('base.EmailApproval')
        case 3:
          return Vue.prototype.$t('base.SystemApproval')
        case 4:
          return Vue.prototype.$t('base.ManualApproval')
        case 5:
          return Vue.prototype.$t('base.AppApproval')
        case 6:
          return Vue.prototype.$t('base.WechatApproval')
        default:
          return Vue.prototype.$t('base.Unknown')
      }
    }
  },
  data() {
    return {
    }
  }
};
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container-child
  position absolute
  width 100%
  height 100%
  left 0
  top 0
  bottom 0
  background-color $color_white
  z-index 200
  >.table-view
    width 100%
    height calc(100% - 45px)
    overflow hidden
    .custom-cell
      width 100%
      padding 15px 0
      >.header
        @extend .flex-row
        @extend .flex-start
        @extend .flex-top
        flex-wrap wrap
        margin-bottom 8px
        >p
          font-size 1rem
          line-height 1.5em
          margin-right 15px
          font-weight bold
      >.details
        width 100%
        >.item
          @extend .flex-row
          @extend .flex-start
          @extend .flex-top
          width 100%
          position relative
          >.left
            @extend .flex-column
            @extend .flex-start
            width 40px
            >.point
              width 12px
              height 12px
              background-color $color_theme
              border-radius 6px
              z-index 99
            >.point-active
              box-shadow 0px 0px 8px 2px $color_theme
            >.pointer-no-arrival
              background-color $color_gray_layer
            >.layer
              position absolute
              top 10px
              bottom 0
              width 1px
              left 20px
              background-color $color_theme
            >.layer-no-arrival
              background-color $color_gray_layer
          >.right
            @extend .flex-column
            @extend .flex-start
            @extend .flex-top
            width calc(100% - 40px)
            padding-bottom 20px
            >p
              width 100%
              font-size 0.8rem
              line-height 1.5em
              color $color_gray_text
              &:nth-child(2)
                color $color_black_text_0
            >.actived
              color $color_green_text
          &:last-child
            >.left
              >.layer
                display none
</style>



