<template>
  <li class="hs-section">
    <div :class="[!noValue(title) ? 'header' : 'header-short']">
      <slot name="title">{{title}}</slot>
    </div>
    <div class="details" :style="{backgroundColor: backgroundColor}">
      <slot></slot>
    </div>
  </li>
</template>
<script type="text/ecmascript-6">
export default {
  props: {
    title: {
      type: String,
      default: ''
    },
    backgroundColor: {
      type: String,
      default: '#FFFFFF'
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-section
  width 100%
  >.header
    @extend .flex-row
    @extend .flex-start
    width 100%
    height 44px
    background-color $color_gray_background_0
    border-top 1px solid $color_gray_layer
    border-bottom 1px solid $color_gray_layer
    padding 0 15px
  >.header-short
    @extend .flex-row
    @extend .flex-start
    width 100%
    height 10px
    background-color $color_gray_background_0
    padding 0 10px
  >.details
    width 100%
    position relative
  &:last-child
    border-bottom 10px solid rgba(0, 0, 0, 0) !important
</style>



