<template>
  <div class="hs-button" :style="{width: width, height: height, fontSize: fontSize}" @click.stop="$emit('click')">
    <slot></slot>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  props: {
    width: {
      type: String,
      default: ''
    },
    height: {
      type: String,
      default: '34px'
    },
    fontSize: {
      type: String,
      default: '1rem'
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-button
  @extend .flex-center
  background-color $color_theme
  color $color_white
  padding 0 15px
  border-radius 3px
.hs-button[type="common"]
  @extend .flex-center
  background-color $color_white
  color $color_theme
  border 1px solid $color_theme
  padding 0 15px
  border-radius 3px
.hs-button[type="gradient"]
  background linear-gradient(to right, #fe5d39, #f22e30, #d61538)
  padding 0 5px
</style>



