<template>
  <transition name="address-picker" v-if="cityData.hot">
    <div class="hs-address-picker" v-show="currentValue" @click.stop>
      <c-header :title="title">
        <p class="c-header-left" slot="left" @click="currentValue = false">{{$t('base.Cancel')}}</p>
      </c-header>
      <div class="search-bar">
        <div class="bar">
          <input maxlength='50' v-model.trim="searchTxt" :placeholder="$t('base.PleaseEnterAKeywordToSearch')"/>
          <div class="logo" style="background-image: url('static/image/plane-ticket/book/search.png')"></div>
        </div>
      </div>

      <hs-tab v-if="showSwitchBar" :options="switchList" @select="_selectSwitchItem"></hs-tab>

      <hs-scroll ref="tableView" :data="cityData.list" class="tableView" :style="{height: showSwitchBar ? 'calc(100% - 45px - 52px - 40px)' : 'calc(100% - 45px - 52px)'}">
        <ul>

          <!-- 查询结果 -->
          <hs-section :title="$t('base.Result')" v-if="!noValue(searchTxt)">
            <hs-cell-1 :value="$t('base.NoData')" :showLogo="false" v-if="noValue(JSON.stringify(vaguelist))"></hs-cell-1>

            <template v-else-if="blank === PLANE_BLANK || blank === INTEL_PLANE_BLANK">
              <hs-cell-1 :showLogo="false" :showSeparator="!(item.Children && item.Children.length > 0)" v-for="(item, index) in vaguelist" :key="index">
                <ul class="search-list">
                  <li class="item-city" :type="language" @click="_select(item)">
                    <p>{{`${item.GeoType === 1 ? $t('base.Airport') : $t('base.City')}`}}</p>
                    <p>{{item[`CityName_${language}`]}}</p>
                    <p>
                      {{item.CityCode}}
                      <template v-if="blank === INTEL_PLANE_BLANK">({{item[`CountryName_${language}`]}})</template>
                    </p>
                  </li>
                  <li class="item-airport" v-if="item.Children && item.Children.length > 0" v-for="(itemChild, indexChild) in item.Children" :key="indexChild" @click="_select(itemChild)"
                    :style="{'border-bottom-width': (index + 1 === vaguelist.length && indexChild + 1 === item.Children.length) ? '0px' : '1px'}">
                    <div class="item-airport-details" :type="language">
                      <div class="logo" style="background-image: url('static/image/base/address-picker/enter.png')"></div>
                      <div class="details">
                        <p>{{`${itemChild.GeoType === 1 ? $t('base.Airport') : $t('base.City')}`}}</p>
                        <p>{{itemChild[`AirPortName_${language}`]}}</p>
                        <p>{{itemChild.AirPortCode}}</p>
                      </div>
                    </div>
                    <!-- <div class="logo" style="background-image: url('static/image/base/address-picker/plane.png')"></div> -->
                  </li>
                </ul>
              </hs-cell-1>
            </template>

            <template v-else-if="blank === HOTEL_BLANK">
              <hs-cell-1 :showLogo="false" v-for="(item, index) in vaguelist" :key="index">
                <ul class="search-list">
                  <li class="item-city" @click="_select(item)">
                    <p v-show="false">{{$t('base.City')}}</p>
                    <p>{{item[`CityName_${language}`]}}</p>
                  </li>
                </ul>
              </hs-cell-1>
            </template>

            <template v-else-if="blank === INTEL_HOTEL_BLANK">
              <hs-cell-1 :showLogo="false" v-for="(item, index) in vaguelist" :key="index">
                <ul class="search-list">
                  <li class="item-city" @click="_select(item)">
                    <p v-show="false">{{$t('base.City')}}</p>
                    <p>{{item[`CityName_${language}`]}}</p>
                    <p>({{item[`CountryName_${language}`]}})</p>
                  </li>
                </ul>
              </hs-cell-1>
            </template>

            <template v-else-if="blank === TRAIN_BLANK">
              <hs-cell-1 :showLogo="false" v-for="(item, index) in vaguelist" :key="index">
                <ul class="search-list">
                  <li class="item-city" @click="_select(item)">
                    <p v-show="false">{{$t('base.City')}}</p>
                    <p>{{item[`CityName_${language}`]}}</p>
                    <p>{{item.CityCode}}</p>
                  </li>
                </ul>
              </hs-cell-1>
            </template>

          </hs-section>

          <!-- 历史搜索 -->
          <hs-section :title="$t('base.RecentSearches')" v-if="!noValue(JSON.stringify(historyAddress))">
            <!-- 中文样式 -->
            <hs-cell-1 :showLogo="false" v-if="language === 'CN'">
              <div class="city-blocks">
                <p @click="_select(item)"
                  v-for="(item, index) in historyAddress" :key="index"
                  class="block">
                  {{item[`AirPortName_${language}`] ? item[`AirPortName_${language}`] : item[`CityName_${language}`]}}
                </p>
              </div>
            </hs-cell-1>
            <!-------------------------------------------------------------------------------------------------------------------------------------------------------------->
            <!-- 英文样式 -->
            <template v-else>
              <hs-cell-1 @click.native="_select(item)"
                v-for="(item, index) in historyAddress" :key="index"
                :value="item[`AirPortName_${language}`] ? item[`AirPortName_${language}`] : item[`CityName_${language}`]"
                :showLogo="false"></hs-cell-1>
            </template>
            <!-------------------------------------------------------------------------------------------------------------------------------------------------------------->
          </hs-section>

          <!-- 热门 -->
          <hs-section :title="$t('base.PopularDestinations')">
            <!-- 中文样式 -->
            <hs-cell-1 :showLogo="false" v-if="language === 'CN'">
              <div class="city-blocks">
                <p class="block" v-for="(item, index) in cityData.hot.CityList" :key="index" @click="_select(item)">{{item[`AirPortName_${language}`] ? item[`AirPortName_${language}`] : item[`CityName_${language}`]}}</p>
              </div>
            </hs-cell-1>
            <!-------------------------------------------------------------------------------------------------------------------------------------------------------------->
            <!-- 英文样式 -->
            <template v-else>
              <hs-cell-1 v-for="(item, index) in cityData.hot.CityList" :key="index" :value="item[`AirPortName_${language}`] ? item[`AirPortName_${language}`] : item[`CityName_${language}`]" :showLogo="false" @click.native="_select(item)"></hs-cell-1>
            </template>
            <!-------------------------------------------------------------------------------------------------------------------------------------------------------------->
          </hs-section>

          <!-- A - Z -->
          <div class="letterPicker">
            <div class="letter-block" v-for="(item, index) in cityData.list" :key="index">
              <div class="header" @click="cityData.list[index].open = !cityData.list[index].open">
                <p>{{item.Label}}</p>
                <div class="logo" :class="{'logo-open': item.open}"></div>
              </div>
              <div class="details" v-if="item.open">
                <div class="city" v-for="(itemChild, indexChild) in item.CityList" :key="indexChild" @click="_select(itemChild)">
                  <p>{{itemChild[`AirPortName_${language}`] ? itemChild[`AirPortName_${language}`] : itemChild[`CityName_${language}`]}}</p>
                  <p v-if="itemChild.AirPortCode">{{itemChild.AirPortCode}}</p>
                  <p v-if="itemChild[`CountryName_${language}`] && itemChild.CountryName_CN !== '中国'">({{itemChild[`CountryName_${language}`]}})</p>
                </div>
              </div>
            </div>
          </div>
        </ul>
      </hs-scroll>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
import CHeader from 'components/c-header/c-header'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsTab from 'base/hs-tab/hs-tab'
import {kpiFlightSearchAirportCity, kpiTrainSearchTrainStations, kpiHotelGetHotelCityByKeyword, kpiIntlHotelGetIntlHotelCityByKeyword} from 'api'
import AMap from 'AMap'
// import WEBAPP_CONFIG from 'common/config'
// import Vue from 'vue'
const isEN = window.localStorage.language === 'EN'
const PLANE_BLANK = isEN ? 'China' : '国内'
const INTEL_PLANE_BLANK = isEN ? 'International' : '国际·港澳台'
const HOTEL_BLANK = isEN ? ' China ' : ' 国内 ' // 酒店BLANK左右空一格和机票的做区分
const INTEL_HOTEL_BLANK = isEN ? ' International ' : ' 国际·港澳台 ' // 酒店BLANK左右空一格和机票的做区分
const TRAIN_BLANK = isEN ? 'City' : '城市'
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    switchList: {
      type: Array,
      default: function() {
        return []
      }
    },
    activeIndex: {
      type: Number,
      default: 0
    },
    showSwitchBar: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    currentValue: {
      get: function(val) {
        return this.value
      },
      set: function(val) {
        return this.$emit('input', val)
      }
    }
  },
  watch: {
    searchTxt(val) {
      this.timer && clearTimeout(this.timer) // eslint-disable-line
      this.timer = setTimeout(() => {
        this._vague(val)
      }, 100)
    },
    currentValue(val) {
      if (val) {
        this._selectSwitchItem(this.activeIndex)
      }
    }
  },
  data() {
    return {
      searchTxt: '',
      cityData: {},
      vaguelist: [],
      historyAddress: [],
      blank: '',
      localStorageHistoryKey: '',
      PLANE_BLANK: PLANE_BLANK,
      INTEL_PLANE_BLANK: INTEL_PLANE_BLANK,
      HOTEL_BLANK: HOTEL_BLANK,
      INTEL_HOTEL_BLANK: INTEL_HOTEL_BLANK,
      TRAIN_BLANK: TRAIN_BLANK
    }
  },
  methods: {
    // 根据activeIndex && 显示的名称,比如:国内, 来判断加载不同的城市json
    _selectSwitchItem(index) {
      this.searchTxt = '' // 重置

      this.$emit('update:activeIndex', index)

      // 改变this.blank后, 为this.localStorageHistoryKey赋值
      this.blank = this.switchList[index]

      let localStorageCityDataKey = ''

      if (this.blank === PLANE_BLANK) {
        this.localStorageHistoryKey = 'HISTORY_DOMESTIC_PLANE_CITY'
        localStorageCityDataKey = 'DOMESTIC_PLANE_CITY'
      } else if (this.blank === INTEL_PLANE_BLANK) {
        this.localStorageHistoryKey = 'HISTORY_INTL_PLANE_CITY'
        localStorageCityDataKey = 'INTL_PLANE_CITY'
      } else if (this.blank === HOTEL_BLANK) {
        this.localStorageHistoryKey = 'HISTORY_DOMESTIC_HOTEL_CITY'
        localStorageCityDataKey = 'DOMESTIC_HOTEL_CITY'
      } else if (this.blank === INTEL_HOTEL_BLANK) {
        this.localStorageHistoryKey = 'HISTORY_INTER_HOTEL_CITY'
        localStorageCityDataKey = 'INTER_HOTEL_CITY'
      } else if (this.blank === TRAIN_BLANK) {
        this.localStorageHistoryKey = 'HISTORY_DOMESTIC_TRAIN_CITY'
        localStorageCityDataKey = 'DOMESTIC_TRAIN_CITY'
      }
      // ==============================================================
      this.historyAddress = window.localStorage[this.localStorageHistoryKey] ? JSON.parse(window.localStorage[this.localStorageHistoryKey]) : []
      this.cityData = JSON.parse(window.localStorage[localStorageCityDataKey])
      // if (this.blank === PLANE_BLANK) {
      //   this._getJson('DOMESTIC_PLANE_CITY', '国内机票城市')
      // } else if (this.blank === INTEL_PLANE_BLANK) {
      //   this._getJson('INTL_PLANE_CITY', '国际机票城市')
      // } else if (this.blank === HOTEL_BLANK) {
      //   this._getJson('DOMESTIC_HOTEL_CITY', '国内酒店城市')
      // } else if (this.blank === TRAIN_BLANK) {
      //   this._getJson('DOMESTIC_TRAIN_CITY', '火车票城市')
      // } else if (this.blank === INTEL_HOTEL_BLANK) {
      //   this._getJson('INTER_HOTEL_CITY', '国际酒店城市')
      // }
    },
    _select(item) {
      // 储存历史选取的城市
      let localStorage = window.localStorage

      // 初始化赋值
      let historyAddress = localStorage[this.localStorageHistoryKey] ? JSON.parse(localStorage[this.localStorageHistoryKey]) : []

      //  数组头部插入
      historyAddress.unshift(item)

      // 去重
      let maplist = new Map()
      historyAddress = historyAddress.filter(a => !maplist.has(`${a[`CityName_${this.language}`]}_${a[`AirPortName_${this.language}`]}`) && maplist.set(`${a[`CityName_${this.language}`]}_${a[`AirPortName_${this.language}`]}`, 1))

      // 截取
      historyAddress = historyAddress.slice(0, 3)

      localStorage[this.localStorageHistoryKey] = JSON.stringify(historyAddress)
      this.historyAddress = localStorage[this.localStorageHistoryKey] ? JSON.parse(localStorage[this.localStorageHistoryKey]) : []
      // =====================================================================================
      this.$emit('select', item)
      this.currentValue = false
    },
    // 模糊筛选
    _vague(val) {
      if (this.noValue(val)) {
        this.vaguelist = []
        return
      }
      if (this.blank === PLANE_BLANK) {
        let params = {
          CountryType: 1,
          KeyWord: this.searchTxt
        }
        kpiFlightSearchAirportCity(params).then((res) => {
          console.log('模糊搜索机场和城市: ', res)
          this.vaguelist = res.ResultData
        })
      } else if (this.blank === INTEL_PLANE_BLANK) {
        let params = {
          CountryType: 2,
          KeyWord: this.searchTxt
        }
        kpiFlightSearchAirportCity(params).then((res) => {
          console.log('模糊搜索国际机场和城市: ', res)
          this.vaguelist = res.ResultData
        })
      } else if (this.blank === HOTEL_BLANK) {
        let params = {
          KeyWord: this.searchTxt
        }
        kpiHotelGetHotelCityByKeyword(params).then((res) => {
          console.log('模糊搜索国内酒店: ', res)
          this.vaguelist = res.ResultData
        })
      } else if (this.blank === INTEL_HOTEL_BLANK) {
        let params = {
          KeyWord: this.searchTxt
        }
        kpiIntlHotelGetIntlHotelCityByKeyword(params).then((res) => {
          console.log('模糊搜索国际酒店: ', res)
          this.vaguelist = res.ResultData
        })
      } else if (this.blank === TRAIN_BLANK) {
        let params = {
          KeyWork: this.searchTxt
        }
        kpiTrainSearchTrainStations(params).then((res) => {
          console.log('模糊搜索火车票: ', res)
          this.vaguelist = res.ResultData
        })
      }
    },
    // 获取当前位置
    getLocation() {
      this._selectSwitchItem(this.activeIndex)
      let _this = this
      AMap && AMap.plugin('AMap.CitySearch', function () {
        var citySearch = new AMap.CitySearch()
        citySearch.getLocalCity(function (status, result) {
          console.log(result)
          if (status === 'complete' && result.info === 'OK') {
            // 查询成功，result即为当前所在城市信息
            _this.cityData.list && _this.cityData.list.forEach(element => {
              element.CityList.forEach(elementChild => {
                let cityName = result.city
                cityName.endsWith('市') && (cityName = cityName.substring(0, cityName.length - 1))
                if (cityName === elementChild.Name_CN) {
                  _this._select(elementChild)
                }
              })
            })
          } else {
            _this.$toast(_this.$t('TM.UnableToLocateCurrentCity'))
          }
        })
      })
    }
  },
  components: {
    CHeader,
    HsScroll,
    HsSection,
    HsCell1,
    HsTab
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-address-picker
  width 100%
  height 100%
  position absolute
  top 0
  left 0
  background-color $color_white
  z-index 9
  >.header
    @extend .flex-row
    @extend .flex-between
    width 100%
    height 50px
    padding 0 10px
    background-color $color_white
    // color $color_white
    >p
      @extend .flex-center
      height 100%
      // color $color_white
      &:nth-child(1)
        font-size 1rem
      &:nth-child(2)
        font-size 1.1rem
        font-weight bolder
      &:nth-child(3)
        opacity 0
  >.search-bar
    @extend .flex-center
    width 100%
    padding 5px 15px
    background-color $color_white
    >.bar
      @extend .flex-row
      @extend .flex-between
      width 100%
      border-radius 4px
      border 1px solid $color_gray_layer
      padding 10px
      background-color $color_white
      font-size 0.9rem
      >input
        width calc(100% - 30px)
        height 100%
      >.logo
        width 20px
        height 20px
        background-size 100% 100%

  >.tableView
    width 100%
    height calc(100% - 52px - 45px)
    overflow hidden
    .search-list
      width 100%
      background-color $color_white
      .item-city
        @extend .flex-row
        @extend .flex-start
        width 100%
        min-height 50px
        border-bottom 1px solid $color_gray_layer
        padding 10px 0
        &:last-child
          border-bottom-width 0px
        >p
          font-size 1rem
          &:nth-child(1)
            font-size 0.7rem
            color $color_theme
            padding 2px
            border 1px solid $color_theme
            border-radius 2px
            margin-right 5px
          &:nth-child(2)
            color $color_black_text_0
            font-weight bold
            margin-right 5px
          &:nth-child(3)
            color $color_gray_text
      .item-city[type="EN"]
        >p
          line-height 1.4em
          font-size 0.7rem !important
          &:nth-child(1)
            border 0
            padding 0
            font-weight bold
      .item-airport
        @extend .flex-row
        @extend .flex-between
        width calc(100% - 35px)
        min-height 50px
        margin-left 35px
        border-bottom 1px solid $color_gray_layer
        // &:last-child
        //   border-bottom-width 0px
        >.item-airport-details
          @extend .flex-row
          @extend .flex-start
          width calc(100% - 25px)
          padding 10px 0
          >.logo
            width 20px
            height 20px
            background-size 100% 100%
            transform scaleX(-1)
            filter fliph
            margin-right 5px
          >p
            font-size 1rem
          >.details
            >p
              line-height 1.4em
              &:nth-child(1)
                @extend .no-wrap
                font-size 0.7rem
                color $color_theme
                padding 2px
                border 1px solid $color_theme
                border-radius 2px
                margin-right 5px
              &:nth-child(2)
                color $color_black_text_0
                margin-right 5px
              &:nth-child(3)
                color $color_gray_text
        >.item-airport-details[type="CN"]
          >.details
            @extend .flex-row
            @extend .flex-start
        >.item-airport-details[type="EN"]
          >.details
            @extend .flex-column
            @extend .flex-top
            font-size 0.7rem
            >p
              &:nth-child(1)
                border 0
                padding 0
                font-weight bold
        >.logo
          width 20px
          height 20px
          background-size 100% 100%
      // .list-item
      //   @extend .flex-row
      //   @extend .flex-start
      //   width 100%
      //   height 50px
      //   padding 0 10px
      //   border-top 1px solid $color_gray_layer
      //   &:first-child
      //     border-top-width 0px
    .city-blocks
      @extend .flex
      @extend .flex-row
      @extend .flex-start
      @extend .flex-top
      flex-wrap wrap
      width 100%
      padding 5px 0
      >.block
        @extend .flex-center
        width calc(33.3333% - 10px)
        margin 5px
        padding 6px 0
        border 1px solid $color_gray_layer
        border-radius 3px
        font-size 1rem
    .letterPicker
      width 100%
      .letter-block
        width 100%
        >.header
          @extend .flex-row
          @extend .flex-between
          width 100%
          height 44px
          background-color $color_gray_background_0
          padding 0 10px
          >p
            font-size 0.9rem
          >.logo
            width 20px
            height 20px
            background-size 100% 100%
            background-image url('../../../static/image/base/cell/right_arrow_red.png')
            transition all 0.3s
          >.logo-open
            transform rotate(90deg)
        >.details
          width 100%
          .city
            @extend .flex-row
            @extend .flex-start
            width 100%
            height 44px
            padding 0 10px
            border-top 1px solid $color_gray_layer
            >p
              margin-right 5px
              &:nth-child(2)
              &:nth-child(3)
                color $color_gray_text
              &:last-child
                margin-right 0
.address-picker-enter-active
.address-picker-leave-active
  transition: all 300ms
  -webkit-transition: all 300ms
.address-picker-enter
.address-picker-leave-to
  transform translateY(100%)
</style>



