<template>
  <div class="hs-checkbox">
    <div class="checkbox-outer">
      <div class="logo-back" :class="{beSmall: !value}">
        <div class="logo"></div>
      </div>
    </div>
    <p>{{title}}</p>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  props: {
    value: {
      type: Boolean,
      default: true
    },
    title: {
      type: String,
      default: ''
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-checkbox
  @extend .flex-row
  @extend .flex-start
  height 20px
  >.checkbox-outer
    // @extend .flex-center
    width 16px
    height 16px
    border 1px solid $color_theme
    border-radius 8px
    position relative
    overflow hidden
    margin-right 8px
    >.logo-back
      position absolute
      top -1px
      left -1px
      width 16px
      height 16px
      border-radius 8px
      background-color $color_theme
      transition all 200ms
      transform scale(1)
      overflow hidden
      >.logo
        position absolute
        top 2px
        left 5px
        width 6px
        height 9px
        border-right 1px solid $color_white
        border-bottom 1px solid $color_white
        transform rotate(45deg)
    >.beSmall
      transform scale(0)
  >p
    font-size 0.9rem
</style>



