<template>
  <div class="hs-cell-3">
    <div class="details">
      <div class="title" :style="{color: titleColor}">
        <slot name="title">
          {{title}}
        </slot>
      </div>
      <div class="right">
        <div class="value" :style="{width: showLogo ? 'calc(100% - 30px)' : '100%'}">
          <slot>
            <p>{{value}}</p>
          </slot>
        </div>
        <div v-if="showLogo" class="logo" :style="{'background-image': `url('static/image/base/cell/${logoName}')`}"></div>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
/**
 * 适用于文字居左的展示型cell/自定义
 *  ————————————————————————————
 * | 标题  XXXXXXXXX            |
 *  ————————————————————————————
 */
export default {
  props: {
    title: {
      type: String,
      default: ''
    },
    titleColor: {
      type: String,
      default: ''
    },
    value: {
      type: String,
      default: ''
    },
    logoName: {
      type: String,
      default: 'right_arrow_gray.png'
    },
    showLogo: {
      type: Boolean,
      default: true
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-cell-3
  width 100%
  padding 0 15px
  >.details
    @extend .flex-row
    @extend .flex-between
    width 100%
    min-height 44px
    border-bottom 1px solid $color_gray_layer
    >.title
      font-size 1rem
      color $color_black_text_1
      width 90px
      margin-right 10px
    >.right
      @extend .flex-row
      @extend .flex-between
      width calc(100% - 100px)
      >.value
        @extend .flex-row
        @extend .flex-start
        font-size 1rem
        width calc(100% - 30px)
        span
          color color_gray_placeholder
        input
          padding 0
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
  &:last-child
    >.details
      border-bottom 0px
</style>



