<template>
  <div class="hs-gradient-button" :style="{width: width, height: height, fontSize: fontSize}">
    <div :class="type" @click.stop="$emit('click')" v-html="currentTitle"></div>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  props: {
    title: {
      type: String,
      default: ''
    },
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '50px'
    },
    fontSize: {
      type: String,
      default: '1rem'
    },
    type: {
      type: String,
      default: 'default'
    }
  },
  created() {
    this._initTitle()
  },
  data() {
    return {
      currentTitle: ''
    }
  },
  methods: {
    _initTitle() {
      if (this.language === 'CN') {
        this.currentTitle = Array.from(this.title).join('&nbsp;')
      } else {
        this.currentTitle = this.title
      }
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-gradient-button
  @extend .flex-center
  >div
    @extend .flex-center
    width 100%
    height 100%
    border-radius 50vmin
    background linear-gradient(to right, #fe5d39, #f22e30, #d61538)
    color $color_white
    font-size 1.1em
  >.default
    width calc(100% - 80px)
  >.common
    width auto
    height 38px
    padding 0 15px
  >.full
    width 100%
</style>



