<template>
  <div class="hs-swiper">
    <div
      class="details" ref="SwiperDetails"
      v-if="images.length > 0"
      :style="{width: `${images.length}00%`, transform: `translateX(-${(activedIndex % images.length) * (100 / images.length)}%)`}"
      @touchstart="_touchstart"
      @touchmove="_touchmove"
      @touchend="_touchend">
      <div class="image-view" v-for="(item, index) in images" :key="index" :style="{width: `${100 / images.length}%`}">
        <img
          :class="{widthMax: maxType === 'Width', heightMax: maxType === 'Height'}"
          :src="item"/>
      </div>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  props: {
    images: {
      type: Array,
      default() {
        return []
      }
    },
    maxType: {
      type: String,
      default: 'Height'
    },
    initialIndex: {
      type: Number,
      default: 0
    }
  },
  created() {
    this.activedIndex = this.initialIndex
  },
  beforeDestroy() {
    clearInterval(this.timeInterval)
  },
  data() {
    return {
      activedIndex: 0,
      startPageX: 0,
      movePageX: 0,
      endPageX: 0
    }
  },
  watch: {
    images(val) {
      this.timeInterval && clearInterval(this.timeInterval)
      if (this.$refs.SwiperDetails && val.length > 0) {
        this._openTransition()
        this._startInterval()
      }
    }
  },
  methods: {
    /** 开启滑动动画 */
    _openTransition() {
      this.$refs.SwiperDetails && (this.$refs.SwiperDetails.style.transition = 'all .3s')
    },
    /** 关闭滑动动画 */
    _closeTransition() {
      this.$refs.SwiperDetails && (this.$refs.SwiperDetails.style.transition = 'none')
    },
    /** 定时器 */
    _startInterval() {
      this.timeInterval = setInterval(() => {
        this.activedIndex = (this.activedIndex + 1) % this.images.length
      }, 8000)
    },
    /** 滑动开始 */
    _touchstart(event) {
      this.timeInterval && clearInterval(this.timeInterval)
      this._closeTransition()
      this.startPageX = event.changedTouches[0].pageX
    },
    /** 滑动过程中 */
    _touchmove(event) {
      this.movePageX = event.changedTouches[0].pageX
      const ReboundDistance = 150
      let difference = this.movePageX - this.startPageX
      if (
        // 第一页
        (this.activedIndex === 0 && difference > ReboundDistance) ||
        // 最后一页
        (this.activedIndex === (this.images.length - 1) && difference < -ReboundDistance)
      ) {
        this._openTransition()
        difference = 0
      } else {
        this._closeTransition()
      }
      this.$refs.SwiperDetails.style.transform = `translateX(calc(-${(this.activedIndex % this.images.length) * (100 / this.images.length)}% + ${difference}px))`
    },
    /** 滑动结束 */
    _touchend(event) {
      this.endPageX = event.changedTouches[0].pageX
      this._openTransition()
      this._startInterval()
      let pathWidth = this.$refs.SwiperDetails.getBoundingClientRect().width / (this.images.length * 3)
      let difference = this.endPageX - this.startPageX
      if (Math.abs(difference) > pathWidth) {
        if (difference < 0 && this.activedIndex !== (this.images.length - 1)) {
          // 下一页
          this.activedIndex = (this.activedIndex + 1) % this.images.length
        } else if (difference > 0 && this.activedIndex !== 0) {
          // 上一页
          this.activedIndex = (this.activedIndex - 1) % this.images.length
        }
      }
      this.$refs.SwiperDetails.style.transform = `translateX(-${(this.activedIndex % this.images.length) * (100 / this.images.length)}%)`
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-swiper
  width 100%
  height 100%
  overflow hidden
  >.details
    @extend .flex-row
    @extend .flex-start
    height 100%
    transition none
    >.image-view
      @extend .flex-center
      height 100%
      overflow hidden
      .widthMax
        width 100%
      .heightMax
        height 100%
</style>



