<template>
  <transition name="slide">
    <div class="hs-container-child" v-if="show">
      <c-header :title="$t('base.TravelCard')" leftIcon1="return.png" :leftFn1="_save"></c-header>
      <hs-scroll class="table-view" :data="currentCardlist">
        <ul>
          <hs-section>
            <div class="section-header">{{$t('base.YourTravelCard')}}</div>
            <hs-cell-1 :showLogo="false" v-for="(item, index) in currentCardlist" :key="index">
              <div class="custom-cell-card" :style="{transform: `translateX(${!currentCardlist[index].openDelete ? '0px' : '-60px'})`}">
                <div class="left" @click="_setOpenDelete(true, index)">
                  <div class="logo" style="background-image: url('static/image/plane-ticket/book/delete.png')"></div>
                </div>
                <div class="right" @click="_setOpenDelete(false, index)">
                  <div class="item">
                    <div @click="_gotoSelectAirline(index)">
                      <hs-input v-if="noValue(item.AirLineCode)" :placeholder="$t('base.PleaseChooseTheAirClub')" width="calc(100% - 40px)" class="disabled" style="margin-left: 10px"></hs-input>
                      <template v-else>
                        <img class="logo" v-lazy="item.AirLineLogoUrl" :key="item.AirLineLogoUrl"/>
                        <p>{{item.AirLineName}}</p>
                      </template>
                      <div class="logo" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
                    </div>
                    <div>
                      <hs-input v-model="currentCardlist[index].CardNo" :placeholder="$t('base.PleaseEnterTheCardNumberOrUsername')" width="calc(100% - 40px)" style="margin-left: 10px"></hs-input>
                    </div>
                  </div>
                  <div class="delete" @click.stop="_delete(index)">{{$t('base.Delete')}}</div>
                </div>
              </div>
            </hs-cell-1>
            <hs-cell-1 :showLogo="false" @click.native="_addCard">
              <div class="custom-cell-add">
                <div class="logo" style="background-image: url('static/image/personal-center/passengers/add.png')"></div>
                <p>{{$t('base.AddTravelCard')}}</p>
              </div>
            </hs-cell-1>
          </hs-section>
          <hs-null :title="$t('base.NoTravelCard')" v-if="!currentCardlist || currentCardlist.length < 1"></hs-null>
        </ul>
      </hs-scroll>
      <airline-list ref="AirlineList" @select="_delegateWithSelectAirline"></airline-list>
    </div>
  </transition>
</template>
<script type="text/ecmascript-6">
import CHeader from 'components/c-header/c-header'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsInput from 'base/hs-input/hs-input'
import AirlineList from './airline-list'
import HsNull from 'base/hs-null/hs-null'
export default {
  props: {
    cardlist: {
      type: Array,
      default: function() {
        return []
      }
    }
  },
  data() {
    return {
      show: false,
      currentCardlist: []
    }
  },
  methods: {
    open() {
      this.show = true
      this.currentCardlist = JSON.parse(JSON.stringify(this.cardlist.map(v => {
        v.openDelete = false
        return v
      })))
    },
    close() {
      this.show = false
    },
    _save() {
      // 去空
      this.currentCardlist = this.currentCardlist.filter(v => v.AirLineCode && v.CardNo)
      // 去重
      let maplist = new Map()
      this.currentCardlist = this.currentCardlist.filter(a => !maplist.has(`${a.AirLineCode}${a.CardNo}`) && maplist.set(`${a.AirLineCode}${a.CardNo}`, 1))
      this.$emit('update:cardlist', this.currentCardlist)
      this.close()
    },
    _setOpenDelete(boolean, index) {
      this.currentCardlist[index].openDelete = boolean
    },
    _delete(index) {
      this.currentCardlist.splice(index, 1)
    },
    _addCard() {
      this.currentCardlist.push({
        AirLineCode: '',
        AirLineName: '',
        CardNo: '',
        AirLineLogoUrl: '',
        openDelete: false
      })
    },
    // 去选择航司
    _gotoSelectAirline(index) {
      if (this.currentCardlist[index].openDelete) {
        this.currentCardlist[index].openDelete = false
        return
      }
      this.$refs.AirlineList.editingIndex = index
      this.$refs.AirlineList.open()
    },
    // 航司列表回调
    _delegateWithSelectAirline(item, index) {
      console.log('选择了航司: ', item, index)
      this.currentCardlist[index].AirLineCode = item.AirLineCode
      this.currentCardlist[index].AirLineName = item.AirLineName
      this.currentCardlist[index].AirLineLogoUrl = item.AirLineLogoUrl
      this.currentCardlist[index].CardNo = ''
    }
  },
  components: {
    CHeader,
    HsScroll,
    HsSection,
    HsCell1,
    HsInput,
    AirlineList,
    HsNull
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container-child
  @extend .flex-column
  @extend .flex-start
  position absolute
  width 100%
  height 100%
  left 0
  top 0
  bottom 0
  background-color $color_white
  z-index 200
  .table-view
    width 100%
    height calc(100% - 45px)
    overflow hidden
    .section-header
      @extend .flex-row
      @extend .flex-start
      width calc(100% - 30px)
      margin-left 15px
      height 50px
      border-bottom 1px solid $color_gray_layer
      font-weight bold
    .custom-cell-card
      @extend .flex-row
      @extend .flex-start
      @extend .no-wrap
      width 100%
      padding 0
      transition all 400ms
      font-size 0.9rem
      >.left
        @extend .flex-center
        width 60px
        >.logo
          width 20px
          height 20px
          background-size 100% 100%
      >.right
        width calc(100% - 60px)
        position relative
        >.item
          width 100%
          >div
            @extend .flex-row
            @extend .flex-start
            width 100%
            height 44px
            &:first-child
              border-bottom 1px solid $color_gray_layer
            >.logo
              width 20px
              height 20px
              background-size 100% 100%
              margin-right 5px
              margin-left 10px
            >p
              width calc(100% - 50px)
              line-height 1.2em
        // >.item
        //   @extend .flex-row
        //   @extend .flex-between
        //   @extend .flex-top
        //   width 100%
        //   margin 5px 0
        //   >.name
        //     color $color_black_text_0
        //     font-weight bold
        //     line-height 1.4em
        //     width: 100%
        //     margin-bottom 3px
        //   >.other-title
        //     line-height 1.1em
        //     color $color_gray_text
        //     font-size 1rem
        //     width 60px
        //   >.other-value
        //     line-height 1.1em
        //     color $color_gray_text
        //     font-size 1rem
        //     width calc(100% - 70px)
        //     word-break break-all
        //     white-space normal
        >.delete
          @extend .flex-center
          position absolute
          width 60px
          // height 100%
          background-color $color_theme
          color $color_white
          font-size 0.9rem
          top -11px
          right -80px
          bottom -11px
      &:last-child
        >.right
          >.delete
            bottom -10px
    .custom-cell-add
      @extend .flex-row
      @extend .flex-center
      width 100%
      height 44px
      >.logo
        width 20px
        height 20px
        background-size 100% 100%
        margin-right 5px
      >p
        font-size 0.9rem
        color $color_theme
</style>



